import React, {useState, useContext} from 'react'; 


import { GlobalContext } from '../../Context/GlobalProvider';

import ForgotPass from '../Components/ForgotPass';
import ConfirmForgotPass from '../Components/ConfirmForgotPass';
import {Button, TextField, Link} from '@material-ui/core';
// import Title from '../HtmlComponents/Title';



export default function ForgotPassPage() {
    const {changeOutsidePage, apiCall, globals, showToast} = useContext(GlobalContext);
    const [forgotPassInputs, setForgotPassInputs] = useState({
        
        userID: 0,
        reqCode: '',
        confirmingForgotPass: false
    });

    async function resendCode(e){
        e.preventDefault();
        console.log('confirm change pass');
        
        let data = {
            
            userID: forgotPassInputs.userID
            
            
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'user/resendCodeForgotPass', data);
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Code sent successfully.', 3, false);
            // userData.token = response.data.payload;

            // AsyncStorage.setItem('userData', JSON.stringify({token: userData.token}));
            // getUserData(response.payload);    
        }
        // else{
        //     showToast(response.message, 3, true);    
        // }
    }
    
    let div = <ForgotPass setForgotPassInputs={setForgotPassInputs}/>;
    let resendBtn = null;
    if(forgotPassInputs.confirmingForgotPass){
        div = <ConfirmForgotPass userID={forgotPassInputs.userID} reqCode={forgotPassInputs.reqCode}/>;
        // resendBtn = <Button onPress={resendCode} primary text="Resend Code" />;
        resendBtn = <Link className='mb-1 border-bottom' noIndent onClick={resendCode}>
            Resend Code
        </Link>
    }

    return (
        
        <div className='d-flex flex-column'>
            {/* <Title title='Forgot Password'/> */}
            {div}
            <div style={{justifyContent: 'space-between'}} className='d-flex flex-row'>
                
                <Link className='mb-1 border-bottom' noIndent onClick={(e) => {e.preventDefault(); changeOutsidePage('Login')}}>
                    Login
                </Link>
                {/* <Button onPress={()=>changeOutsidePage('Login')} primary text="Login" /> */}
                {resendBtn}
            </div>
             
        </div>

    );
}