import React, {useState, useContext} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';

import LoginPage from '../Views/LoginPage';
import VerificationPage from '../Views/VerificationPage';
import ForgotPassPage from '../Views/ForgotPassPage';
import RegisterPage from '../Views/RegisterPage';


export default function OutsidePages() {
	const {userData} = useContext(GlobalContext);
	let page = <LoginPage/>
    if(userData.currentPage.front == 'Verification'){
		page = <VerificationPage/>;
	}else if(userData.currentPage.front == 'ForgotPass'){
		page = <ForgotPassPage/>;
	}else if(userData.currentPage.front == 'Register'){
		page = <RegisterPage/>;
	}
    return (
        <div style={{flex: 1}} className='px-3 py-2 d-flex flex-column'>
            {page}
        </div>
    );
}

