import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';

// import { View, Modal } from 'react-native';
// import { Container, Header, Content, Text, Left, Right, Body, Input, Item, Icon, Card } from 'native-base';
import MsgList from '../List/MsgList';
// import OtherPage from '../HtmlComponents/OtherPage';
import PageNav from './PageNav';
import {Button, TextField} from '@material-ui/core';
// import { Button } from 'react-native-material-ui';

export default function Messages({onPressBack, msgHeader}) {
    console.log('other profile page');
    const {userData, globals, apiCall, openMsgModal} = useContext(GlobalContext);
    const [msgsData, setMsgsData] = useState({
        msgs: []
    })

    // const [msg, setMsg] = useState('');
    // const [startPt, setStartPt] = useState(0);

    useEffect(() => {
        // Update the document title using the browser API
        getMsgs(0);
    }, []);

    // function replyMsg(){
        
    //     var data = {
    //         msgHeaderID: msgHeader.id,
    //         post: msg
    //     }
    //     // console.log('options: ', options);
    //     apiCall(globals.varNames.blagsApiPath + 'message/replyMessage', data);
        
    // }
    
    
    async function getMsgs(startPt){
        // let data = {
        //     headerID: headerID,
        //     startPoint: startPoint
        // }

        // var response = await apiCall(state.globals.varNames.blagsApiPath + 'message/showMessages', data);
        var data = {
            headerID: msgHeader.id,
            startPoint: startPt
        }
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.blagsApiPath + 'message/showMessages', data);
        if(response.status == 1){

            let msgs = [];
            response.payload.forEach((msg) => {
                msgs.unshift(msg);
            })
            
            setMsgsData({...msgsData, msgs: msgs});

        }
    }

    return (
        // <View style={{flex: 1, paddingBottom: 10}}>
            
        //     <View style={{flexDirection: 'row', justifyContent: 'space-between'}}>
        //         <Button style={{container: {marginRight: 25}}} raised primary text='Back' onPress={onPressBack} />
        //         <PageNav startPt={startPt} setStartPt={setStartPt} isForward={false} getItems={getMsgs} />
        //     </View>
        //     <View style={{flex: 1}}>
        //         {msgsData.msgs.map((msg) => {
        //             return (
                       
        //                 <MsgList msgHeader={msgHeader} msg={msg} key={msg.id}/>
                            
        //             )
                    
        //         })}
        //     </View>
            
        //     <Button raised primary text='Reply' onPress={()=>openMsgModal('', '', 0, msgHeader.id)} />
           
        // </View> 
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            <div className='mb-2 d-flex' style={{justifyContent: 'space-between'}}>
                <Button variant="contained" onClick={onPressBack}>Back</Button>
                <PageNav isForward={false} onChangeStartPt={getMsgs} />   
            </div>
            <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column px-2'>
                {msgsData.msgs.map((msg) => {
                    return (
                        
                        <MsgList msgHeader={msgHeader} msg={msg} key={msg.id}/>
                            
                    )
                    
                })}   
            </div>
            <Button className='mb-2' style={{width: '70%', alignSelf: 'center'}} variant="contained" onClick={()=>openMsgModal('', '', 0, msgHeader.id)}>Reply</Button>
        </div>
    );
}