import React, {useState, useContext, useEffect} from 'react'; 


// import { View } from 'react-native';
import {GlobalContext} from '../../Context/GlobalProvider';
// import { Input, Item, Picker } from 'native-base';
import AppModal from '../HtmlComponents/AppModal';
import UserImg from '../Components/UserImg';
// import { Button } from 'react-native-material-ui';
// import {Button, TextField, IconButton, FormControl, InputLabel, Select, MenuItem} from '@material-ui/core';
// [post, tags, blogID, replyID, bidAmt]
export default function VerifiedInfoModal({closeModal}) {
    console.log('NewItemModal');
    const {userData, globals, convertEmptyStr, singleModalData, apiCall, showToast, getSiteID, convertTagsToStr, convertTagsStrToArrayStr, convertNumToStr, countries} = useContext(GlobalContext);
    console.log(globals.numOfItems)
    
    const [verifiedData, setVerifiedData] = useState({
        name: '',
        address: '',
        country: '',
        isMale: 1,
        imgURL: ''
    });
    
   
    
    

    // var startPoint = 0
    console.log('single modal data: ', singleModalData);
    async function getData(){
        console.log('SUBMIT NEW ITEM');
        

        let response = await apiCall(globals.varNames.blagsApiPath + "user/getVerifiedData", {userID: singleModalData.data}); 
        console.log('response: ', response);
        if(response.status == 1){
            setVerifiedData({
                name: response.payload.name,
                country: countries[response.payload.country],
                address: response.payload.address,
                isMale: response.payload.ismale,
                imgURL: response.payload.imgurl,
            })
            // showToast('Post submitted successfully', 3, false);
            // closeModal();
            
            
        }
        
        
        
        
    } 
    
    let imgPathBegin = globals.varNames.verifiedPhotoPath;
    let photoPassword = userData.sosyalData.photoPassword;
    let gender = 'Male';
    if(!verifiedData.isMale){
        gender = 'Female';
    }
    useEffect(() => {
        // Update the document title using the browser API
        getData();
        
    }, []);
    
    return (
        
        
        <AppModal buttons={['Done']} onButtonsPressed={closeModal} closeModal={closeModal}>
            <div className='d-flex flex-column'>
                <div className='' style={{alignSelf: 'center'}}>
                    <UserImg size={120} userID={userData.userID} imgURL={verifiedData.imgURL} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />
                </div>
                
                <span>{verifiedData.name}</span>
                <span>{verifiedData.country}</span>
                <span>{gender}</span>
                {/* <span>{verifiedData.address}</span> */}
            </div>
            
        </AppModal>
    );
}