import React, {useState, useContext, useRef} from 'react'; 

import {Button, TextField, Link} from '@material-ui/core';

import {GlobalContext} from '../../Context/GlobalProvider';

import AppModal from '../HtmlComponents/AppModal';



export default function InfoModal({closeModal}) {
    console.log('NewItemModal');
    const {userData, globals, getUserData, singleModalData, apiCall, showToast, changePage} = useContext(GlobalContext);
    console.log(globals.numOfItems)
    

    console.log('single modal data: ', singleModalData);
    let div = null;
    if(singleModalData.data.div == 'Data'){

        div = <div>
            {/* <Text>Not enough data. </Text><Text>Click here</Text><Text>to buy data.</Text>  */}
            <p style={{fontSize: 14}}>Not enough data. <Link noIndent onClick={(e) => {e.preventDefault(); changePage('Data'); closeModal()}}>Click here</Link>    to buy data.</p>
        </div>;
        
    }else if(singleModalData.data.div == 'Balance'){

        div = <div>
            <p style={{fontSize: 14}}>Not enough balance. <Link noIndent onClick={(e) => {e.preventDefault(); changePage('Wallet'); closeModal()}}>Click here</Link>    to load account.</p>
            {/* <Text>Not enough balance. <Text onPress={()=>{changePage('Wallet'); closeModal()}} style={{color: 'blue'}}>Click here</Text> to load account.</Text> */}
        </div>;

    }

    function onButtonsPressed(){

    }
    
    return (
        <AppModal buttons={['Submit']} onButtonsPressed={onButtonsPressed} closeModal={closeModal}>
            {div} 
            
               
            
            
        </AppModal>
    );
}