
import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
// import { View, Image } from 'react-native';

// import { Input, Item } from 'native-base';
import AppModal from '../HtmlComponents/AppModal';
import InsertComponent from '../HtmlComponents/InsertComponent';
import Note from '../HtmlComponents/Note';
import SearchHints from '../HtmlComponents/SearchHints';
import {Button, TextField} from '@material-ui/core';


export default function NewMsgModal() {
    console.log('NewMsgModal');
    const {userData, globals, msgModalData, forceUpdate, apiCall, getSiteID, consumptionNote, showToast, openHintsModal, convertEmptyStr} = useContext(GlobalContext);
    
    const [username, setUsername] = useState(msgModalData.username);
    // const [msgInputs, setMsgInputs] = useState({
    //     username: msgModalData.username,
    //     msg: msgModalData.msg
    // });

    const [isTyping, setIsTyping] = useState(false);
    const [msg, setMsg] = useState(msgModalData.msg);
    const [hints, setHints] = useState([]);
    

    async function sendMsg(){
        let usernameInit = convertEmptyStr(username);
        if((usernameInit == '' && msgModalData.headerID == 0) || msg == ''){
            let error = '';
            if(usernameInit == '' && msgModalData.headerID == 0){
            
                error = 'Username cannot be empty.';
                
                
            }else if(msg == ''){
                error = 'Message cannot be empty.';
                
            }

            showToast(error, 3, true);
            return;
        }
        

        let data = {
            username: username,
            message: msg,
            siteID: getSiteID()
        }

        let path = 'message/newMessage';

        if(msgModalData.msgID != 0){
            
            data = {
                
                msg: msg,
                msgID: msgModalData.msgID
            }

            path = 'message/editMessage';
        }else if(msgModalData.headerID != 0){

            data = {
                msgHeaderID: msgModalData.headerID,
                post: msg
            }
            // console.log('options: ', options);
            path = 'message/replyMessage';
            // apiCall(globals.varNames.blagsApiPath + 'message/replyMessage', data);    
        }

        let response = await apiCall(globals.varNames.blagsApiPath + path, data);
        console.log('result: ', response);
        if(response.status == 1){
            showToast('Message submitted successfully.', 3, false);
            closeModal();
        }
        
        
        
        
    }

    // async function getHints(){
    //     console.log('get hints');
    //     let tags = await getSearchTags(username, 0, true);
    //     setHints(tags);
        
    // }

    function onSearchInputPressed(){
        console.log('on search inputs pressed');
        // if(!isTyping){
        //     setIsTyping(true);         
        //     // getHints();
        // }

        openHintsModal(username, true, setUsername);
        
    }

    // function onChangeSearchInput(e){
    //     console.log('on search inputs pressed');
    //     setIsTyping(true);
    //     // setSearchInputs({s, searchValue: text})
    //     setUsername(e.target.value);
    // }
    
    function closeModal(){
        userData.openingMsgModal = false;
        forceUpdate();
    }

    // function onPressHint(hint){
    //     console.log('get hints');
    //     setUsername(hint);
        
    //     setIsTyping(false);
    //     // setOptions({...options, searchValue: hint});
    //     // setSearchInputs({...searchInputs, searchValue: hint});
        
        
    // }

    // let usernameInput = <Item>
    //     <Input value={username} onFocus={onSearchInputPressed} onChangeText={onChangeSearchInput} placeholder="Username" />
    // </Item>
    let usernameInput = <TextField autoComplete='off' onClick={onSearchInputPressed} value={username} id="standard-basic" label='Username' />
    if(msgModalData.msgID != 0 || msgModalData.headerID != 0){
        usernameInput = null;
    }

    // let div = <View>
        
    //     <Item>
    //         <Input value={msg} onChangeText={(text)=>setMsg(text)} placeholder="Message" />
    //     </Item>
    //     <InsertComponent post={msg} setPost={setMsg} />    
    // </View>;

    // if(isTyping){
    //     div = <SearchHints hints={hints} onPressHint={onPressHint} isUser={true} />;
    // }

    let btn = 'Send';
    if(msgModalData.msgID != 0){
        btn = 'Edit';
    }

    // useEffect(() => {
    //     // Update the document title using the browser API
    //     if(isTyping){
    //         getHints();
    //     }
        
    //     // setPageInitiated(true);
    // }, [username]);

    // let searchHints = null;
    // if(isTyping){
    //     searchHints = <SearchHints onPressClosed={()=>setIsTyping(false)} hintVal={username} onPressHint={onPressHint} isUser={true} />
    // }
    
    return (
        
        <AppModal consumptionShow={true} buttons={[btn]} onButtonsPressed={sendMsg} closeModal={closeModal}>
            
            <div style={{position: 'relative'}} className='d-flex flex-column'>
                {usernameInput}
                {/* {searchHints}   */}
            </div>
            
            {/* <Item>
                <Input value={msg} onChangeText={(text)=>setMsg(text)} placeholder="Message" />
            </Item> */}
            <TextField value={msg} onChange={(e)=>setMsg(e.target.value)} id="standard-basic" label='Message' />
            <InsertComponent post={msg} setPost={setMsg} />    
            {/* <Note note={consumptionNote()} /> */}
            {/* {div} */}
            {/* <div>
                
            </div> */}
            
        </AppModal>
    );
}




 