
import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';

import ErrorLog from '../HtmlComponents/ErrorLog';
// import Title from '../HtmlComponents/Title';
import {Button, TextField, Link} from '@material-ui/core';

export default function Register() {
    console.log('REGISTER');
    const {userData, globals, apiCall, forceUpdate, changeOutsidePage, checkUsernameInput, checkRepeatPassInput, checkPassInput, showToast} = useContext(GlobalContext);
    const [userInputs, setUserInputs] = useState({
        username: '',
        password: '',
        email: '',
        confirmingRegister: false,
        repeatPass: '',
        code: ''   
    })

    // const [error, setError] = useState('');
    const [errors, setErrors] = useState({
        username: '',
        password: '',
        repeatPass: ''
    });
    
    async function register(){
        console.log('register');
        // var response = null;
        console.log('user inputs: ', userInputs);
        if(errors.username != ''|| errors.password != '' || errors.repeatPass != '' || userInputs.username == '' || userInputs.password == '' || userInputs.email == '' || userInputs.repeatPass == ''){
            let toastObj = {
                text: '',
                buttonText: 'Okay',
                duration: 3000
            }

            if(errors.username != ''|| errors.password != '' || errors.repeatPass != ''){
                toastObj.text = 'Please check errors.';
                
            }else{
                if(userInputs.username == ''){
                    toastObj.text = 'Username cannot be blank.';
                     
                }else if(userInputs.password == ''){
                    toastObj.text = 'Password cannot be blank.'; 
                }else if(userInputs.repeatPass == ''){
                    toastObj.text = 'Repeat password cannot be blank.';    
                }else{
                    toastObj.text = 'Email cannot be blank.';   
                }
                  
            }
            showToast(toastObj.text, 3, true);
            // Toast.show(toastObj);
            return;
        }

        var options = {
            method: 'post',
            url: globals.varNames.blagsApiPath + 'user/register',
            data: {
                username: userInputs.username,
                password: userInputs.password,
                email: userInputs.email
            }
        }

        console.log('options: ', options);
        // if(userData.usingSample){
            
        //     response = Samples.register(options);
        // }else{
            
        // }
        let data = {
            username: userInputs.username,
            password: userInputs.password,
            email: userInputs.email    
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'user/register', data);
        console.log('response: ', response);
        if(response.status == 1){
            // userData.verification.userID = response.payload;
            userData.verification.email = userInputs.email;
            userData.currentPage.front = 'Verification';
            forceUpdate();
            // userInputs.code = response.data.payload.code;
            // userInputs.confirmingRegister = true;
            // setUserInputs({...userInputs});
        }
    }
    
    function onChangePassword(e){
        let text = e.target.value;
        setUserInputs({...userInputs, password: text});
        setErrors({...errors, password: checkPassInput(text)});
        // checkPassInput(text);
        // checkInputs(userInputs.username, text, userInputs.repeatPass);
        // setError(checkPassInput(text));
    }

    

    function onRepeatPasswordChange(e){
        let text = e.target.value;
        setUserInputs({...userInputs, repeatPass: text});
        setErrors({...errors, repeatPass: checkRepeatPassInput(userInputs.password, text)});
        // checkInputs(userInputs.username, userInputs.password, text);
        // setError(checkRepeatPassInput(userInputs.password, text));
    }

    function onChangeUsername(e){
        
        let text = e.target.value;

        setUserInputs({...userInputs, username: text});
        setErrors({...errors, username: checkUsernameInput(text)});
        
    }

    return (
        <div className='d-flex flex-column py-5'>
            {/* <Title title='Create Account'/> */}
            {/* <Item>
                <Input onChangeText={onChangeUsername} placeholder="Username" />
            </Item> */}
            <h4 style={{alignSelf: 'flex-end'}}>Create Account</h4>
            <TextField onChange={onChangeUsername} id="standard-basic" label="Username" />
            <ErrorLog error={errors.username} />
            {/* <Item>
                <Input onChangeText={onChangePassword} placeholder="Password" />
            </Item> */}
            <TextField type={userData.production? 'password' : 'text'} onChange={onChangePassword} id="standard-basic" label="Password" />
            <ErrorLog error={errors.password} />
            {/* <Item>
                <Input onChangeText={onRepeatPasswordChange} placeholder="Repeat Password" />
            </Item> */}
            <TextField type={userData.production? 'password' : 'text'} onChange={onRepeatPasswordChange} id="standard-basic" label="Repeat Password" />
            <ErrorLog error={errors.repeatPass} />
            {/* <Item>
                <Input onChangeText={(text)=>setUserInputs({...userInputs, email: text})} placeholder="Email" />
            </Item> */}
            <TextField onChange={(e)=>setUserInputs({...userInputs, email: e.target.value})} id="standard-basic" label="Email" />
            {/* <Button style={{container: {marginVertical: 10}}} raised onPress={register} primary text="Create" /> */}
            <Button className='mt-2' variant="contained" onClick={register}>Create</Button>
            <div className='d-flex flex-row mt-2' style={{justifyContent: 'space-between'}}>
                {/* <Text style={{fontSize: 30, alignSelf: 'center'}}>Sosyal Shaps</Text> */}
                <Link className='mb-1 border-bottom' noIndent onClick={(e) => {e.preventDefault(); changeOutsidePage('Login')}}>
                    Login
                </Link>
                
            </div>
            {/* <div style={{flexDirection: 'row'}}>
               
                <Button onPress={()=>changeOutsidePage('Login')} primary text="Login" />
            </div> */}
            
        </div>
    );
}
