import React, {useState, useContext, useRef} from 'react'; 


// import { View } from 'react-native';
import {GlobalContext} from '../../Context/GlobalProvider';
// import { Input, Item, Picker } from 'native-base';
import AppModal from '../HtmlComponents/AppModal';
import Note from '../HtmlComponents/Note';
// import { Button } from 'react-native-material-ui';
import {Button, TextField, IconButton, FormControl, InputLabel, Select, MenuItem} from '@material-ui/core';
// [post, tags, blogID, replyID, bidAmt]
export default function NewPostModal({closeModal}) {
    console.log('NewItemModal');
    const {userData, globals, convertEmptyStr, minBidNote, consumptionNote, singleModalData, apiCall, tagsNote, showToast, getSiteID, convertTagsToStr, convertTagsStrToArrayStr, convertNumToStr, convertStrToNum} = useContext(GlobalContext);
    console.log(globals.numOfItems)
    const [error, setError] = useState('');
    const [insertType, setInsertType] = useState('none');
    const [insertValue, setInsertValue] = useState('');

    // let tagsArr = JSON.parse(singleModalData.data.tags);
    // let tagsStr = '';
    // tagsArr.forEach((tag, ndx) => {
    //     tagsStr = tagsStr + tag + ' ';
    // })

    let tagsStr = convertTagsToStr(singleModalData.data.tags);
    console.log('tags str: ', tagsStr);
    
    const [postInputs, setPostInputs] = useState({
        post: singleModalData.data.post,
        tags: tagsStr,
        bidAmt: convertNumToStr(singleModalData.data.bidAmt / 1000)
    });
    
    

    // var startPoint = 0
    console.log('single modal data: ', singleModalData);
    async function newPost(){
        console.log('SUBMIT NEW ITEM');
        let postCheck = convertEmptyStr(postInputs.post);
        if(postCheck == ''){
            
            let msg = 'Post cannot be empty.';
            
            showToast(msg, 3, true);
            // Toast.show(toastObj);
            return;
        }

        // let tagsArray = postInputs.tags.split(" ");
        // let tagsStr = JSON.stringify(tagsArray);
        let arrayStr = convertTagsStrToArrayStr(postInputs.tags);

        let data = {
            post: postInputs.post,
            tags: arrayStr,
            replyID: singleModalData.data.replyID,
            siteID: getSiteID() 
        }

        let path = "blog/createBlog";
        if(singleModalData.data.blogID != 0){
            // data = {
            //     post: postInputs.post,
            //     tags: arrayStr,
            //     blogID: singleModalData.data.blogID,
            //     // siteID: getSiteID()    
            // }
            data.blogID = singleModalData.data.blogID;
            data.bidAmt = convertStrToNum(postInputs.bidAmt);
            data.siteID = singleModalData.data.siteID;
            path = "blog/editBlog";
        }

        let response = await apiCall(globals.varNames.blagsApiPath + path, data); 
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Post submitted successfully', 3, false);
            closeModal();
            
            
        }
        
        
        
        
    } 

    function onChangeInsertPicker(e){
        console.log('SUBMIT NEW ITEM');
        // if(value == 'link'){

        // }
        setInsertType(e.target.value);
    } 

    function onClickInsert(){
        console.log('SUBMIT NEW ITEM');
        
        if(insertType == 'link'){
            let newPost = postInputs.post + '[link]' + insertValue + '[/link]';
            setPostInputs({...postInputs, post: newPost});
        }
        
    } 

    let insertInput = null;
    let insertBtn = null;
    if(insertType != 'none'){
        if(insertType == 'link'){
            // insertInput = <Item>
            //     <Input onChangeText={(text)=>setInsertValue(text)} placeholder={insertType.charAt(0).toUpperCase() + insertType.substring(1)} />
            // </Item>;    
            
            insertInput = <TextField onChange={(e)=>setInsertValue(e.target.value)} id="standard-basic" placeholder={insertType.charAt(0).toUpperCase() + insertType.substring(1)} />
        }

        // insertBtn = <Button primary text='Insert' onPress={onClickInsert} raised />
        insertBtn = <Button variant="contained" onClick={onClickInsert}>Insert</Button>;
    }
    
    let btnLabel = 'Submit';
    let pickerOptionsFinal = ['none', 'link'];
    
    let bidAmtInput = null;
    let minNote = null;
    if(singleModalData.data.blogID != 0){
        // bidAmtInput = <Item>
        //     <Input value={postInputs.bidAmt} onChangeText={(text)=>setPostInputs({...postInputs, bidAmt: text})} placeholder="Bid Amount" />
        // </Item>;
        btnLabel = 'Edit';
        bidAmtInput = <TextField value={postInputs.bidAmt} onChange={(e)=>setPostInputs({...postInputs, bidAmt: e.target.value})} id="standard-basic" label="Bid Amount" />
        minNote = <Note note={minBidNote()} />;
    }

    return (
        // <AppModal error={error} buttons={['Submit']} onButtonsPressed={newPost} closeModal={closeModal}>
        //     <View>
        //         <Item>
        //             <Input value={postInputs.post} onChangeText={(text)=>setPostInputs({...postInputs, post: text})} placeholder="Post" />
        //         </Item>
        //         <Item>
        //             <Input value={postInputs.tags} onChangeText={(text)=>setPostInputs({...postInputs, tags: text})} placeholder="Tags" />
        //         </Item>
        //         {bidAmtInput}
        //         <Picker
        //             note
        //             mode="dropdown"
        //             // style={{ width: 120 }}
        //             selectedValue={insertType}
        //             onValueChange={onChangeInsertPicker}
        //         >
                    
        //             {pickerOptionsFinal.map((option, id) => {
        //                 return (
        //                     <Picker.Item key={id} label={option.charAt(0).toUpperCase() + option.substring(1)} value={option} />
        //                 )
                        
        //             })}
                    
        //         </Picker>
        //         {insertInput}
        //         {insertBtn}
        //     </View>
            
            
        // </AppModal>
        
        <AppModal consumptionShow={true} error={error} buttons={[btnLabel]} onButtonsPressed={newPost} closeModal={closeModal}>
            
            <TextField value={postInputs.post} onChange={(e)=>setPostInputs({...postInputs, post: e.target.value})} id="standard-basic" label='Post' />
            <TextField value={postInputs.tags} onChange={(e)=>setPostInputs({...postInputs, tags: e.target.value})} id="standard-basic" label='Tags' />
            <Note note={tagsNote()} />
            {bidAmtInput}
            {minNote}
            <FormControl>
                <InputLabel id="demo-simple-select-label">Insert</InputLabel>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={insertType}
                    onChange={onChangeInsertPicker}
                >
                    {pickerOptionsFinal.map((option, id) => {
                        return (
                            // <Picker.Item key={id} label={option.charAt(0).toUpperCase() + option.substring(1)} value={option} />
                            <MenuItem key={id} value={option}>{option.charAt(0).toUpperCase() + option.substring(1)}</MenuItem>
                        )
                        
                    })}
                
                {/* <MenuItem value={20}>Twenty</MenuItem>
                <MenuItem value={30}>Thirty</MenuItem> */}
                </Select>
            </FormControl>
            {insertInput}
            {insertBtn}
            {/* <Note note={consumptionNote()} /> */}
        </AppModal>
    );
}