
import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import GoogleMapReact from 'google-map-react';
import {Button, TextField} from '@material-ui/core';
import Marker from '../HtmlComponents/Marker';
// import { View } from 'react-native';

// import MapView, { Marker } from 'react-native-maps';

// import { Button } from 'react-native-material-ui';
// import { Container, Header, Content, Button, Text, Input, Item } from 'native-base';
// const AnyReactComponent = ({ text }) => <div>{text}</div>;

export default function SetLocation() {
    console.log('MAP SEARCH COMPONENT');
    const {userData, globals, getMsgs, messageData, showToast, apiCall} = useContext(GlobalContext);
    
    const [mapInputs, setMapInputs] = useState({
        latitude: userData.position.lat,
        longitude: userData.position.lng
    });
    
    async function setNewLocation(){
        let data = {
            lat: mapInputs.latitude,
            lng: mapInputs.longitude
        }
        let response = await apiCall(globals.varNames.apiPath + 'user/setLocation', data);  
        console.log('response; ', response);
        if(response.status == 1){
            userData.position.lat = mapInputs.latitude;
            userData.position.lng = mapInputs.longitude;
            showToast('Location changed successfully.', 3, false);      
        }
    }
    
    // let initialRegion = {
    //     latitude: mapInputs.latitude,
    //     longitude: mapInputs.longitude,
    //     latitudeDelta: 50,
    //     longitudeDelta: 0.9,
    // }

    function mapPressed(response){
        console.log('map pressed');
        console.log('response: ', response);
        
        // setMapInputs({
        //     latitude: response.nativeEvent.coordinate.latitude, 
        //     longitude: response.nativeEvent.coordinate.longitude
        // });
        setMapInputs({
            latitude: response.lat, 
            longitude: response.lng
        });
    }

    let defaultProps = {
        center: {
            lat: userData.position.lat,
            lng: userData.position.lng
        },
        zoom: 11
    };

    function onClickMarker(){

    }
    
    // console.log('user inputs: ', userInputs);
    return (
        // <View style = {{flex: 1}}>
        //     <MapView initialRegion={initialRegion} style={{flex: 1}} onPress={mapPressed}>
        //         <Marker coordinate={mapInputs} />
        //     </MapView> 

            
        //     <View style={{flexDirection: 'row-reverse', marginTop: 5}}>
        //         <Button raised accent text='Set Location' onPress={setNewLocation} />
        //     </View>
            
            
            
        // </View>
        <div style={{flex: 1}} className='d-flex flex-column mb-1'>
            <div style={{flex: 1}}>
                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyCpkBTMH6jnPgDEljN1vNW6HSofABjyzAk" }}
                    defaultCenter={defaultProps.center}
                    defaultZoom={defaultProps.zoom}
                    onClick={mapPressed}
                    >
                        <Marker
                            onClick={onClickMarker}
                            // key={groser.userid}
                            lat={mapInputs.latitude}
                            lng={mapInputs.longitude}
                            text={0}
                        />
                        
                
                </GoogleMapReact>
                
            </div>   
            <div className='px-3 d-flex flex-column mt-1'>
                <Button variant="contained" onClick={setNewLocation}>Set Location</Button>
            </div> 
            
        </div>
        
    );
}

