import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import HintList from '../List/HintList';
import PageNav from '../Components/PageNav';





export default function SearchHints({hintVal, onPressHint, isUser}) {
	console.log('LIST')
    const {userData, getSearchTags, openSingleModal} = useContext(GlobalContext);
    const [hints, setHints] = useState([]); 
    const [startPt, setStartPt] = useState(0); 
    const [pageInitiated, setPageInitiated] = useState(false); 
    async function getHints(){
        console.log('get hints');
        let tags = await getSearchTags(hintVal, startPt, isUser);
        setHints(tags);
        
    }

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            getHints();
        }
        
        // if(isTyping){
            
        // }
        
        // setPageInitiated(true);
    }, [hintVal]);

    useEffect(() => {
        // Update the document title using the browser API
        getHints();
        setPageInitiated(true);
        // if(isTyping){
            
        // }
        
        // setPageInitiated(true);
    }, []);

    useEffect(() => {
        // Update the document title using the browser API
        getHints();
        // setPageInitiated(true);
        // if(isTyping){
            
        // }
        
        // setPageInitiated(true);
    }, [startPt]);
	// let div = null;
    // if(hints.length > 0){
    //     div = <div style={{position: 'absolute', top: 50, right: 5, left:5, backgroundColor: 'white'}} className='shadow px-3'>
    //         {hints.map((hint, idx) => 
    //             <HintList onPressHint={onPressHint} key={hint.id} isUser={isUser} hint = {hint} />
    //         )}    
    //     </div>
    // }
    // let div = null;
    // if(hints.length > 0){
    //     div=
    // }
    return (
        <div style={{flex: 1, overflow: 'auto'}} className='shadow d-flex flex-column'>
            {/* <div className='border-bottom d-flex flex-row' style={{justifyContent: 'space-between'}}>
                <h6 className=''>Results</h6>
                <h6 className='' onClick={onPressClosed}>Close</h6>
            </div> */}
            <div className='px-3' style={{overflow: 'auto', flex: 1}}>
                {hints.map((hint) => 
                    <HintList onPressHint={onPressHint} key={hint.id} isUser={isUser} hint = {hint} />
                )}    
            </div>
            <div className='d-flex flex-column pr-3 py-2' style = {{alignItems: 'flex-end'}}>
                <PageNav isForward={true} onChangeStartPt={setStartPt} />
            </div>
            
                
        </div>           
    );
}