import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';


import CustomerList from '../List/CustomerList';
// import PageNav from '../common/Components/PageNav';

export default function CustomersDiv() {
    console.log('other profile page');
    const {userData, globals, apiCall, showPageNav, closePageNav} = useContext(GlobalContext);
    // const [startPt, setStartPt] = useState(0);
    const [customersData, setCustomersData] = useState({
        customers: []
    })

    

    useEffect(() => {
        // Update the document title using the browser API
        // getCustomers();
        showPageNav(getCustomers); 
        getCustomers(0); 
        return closePageNav
    }, []);
    

    async function getCustomers(startPt){
        // let data = {
        //     headerID: headerID,
        //     startPoint: startPoint
        // }

        // var response = await apiCall(state.globals.varNames.blagsApiPath + 'message/showMessages', data);
        var data = {
            
            startPoint: startPt,
            type: 'hirer'
        }
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.apiPath + 'map/getShappers', data);
        if(response.status == 1){
            setCustomersData({...customersData, customers: response.payload});
        }
    }

    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            <div style={{flex: 1, overflow: 'auto'}} className='px-2 py-1'>
                {customersData.customers.map((customer) => {
                    return (
                        <CustomerList customer={customer} key={customer.id}/>
                        // <ListCard buttons={['Message', 'Cancel', 'Items']} onButtonsPressed={onButtonsPressed} >
                        //     <Text>{JSON.stringify(customer)}</Text>
                        // </ListCard>
                        
                    )
                    
                })}
            </div>
            
            {/* <PageNav startPt={startPt} getItems={getCustomers} setStartPt={setStartPt} isForward={true}/> */}
        </div>    
    );
}