import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Link, Card, Paper} from '@material-ui/core';



import UserLink from '../HtmlComponents/UserLink';
import UserInfoDiv from '../HtmlComponents/UserInfoDiv';
import UserImg from '../Components/UserImg';
import Tab from '../HtmlComponents/Tab';
import MoreInfo from '../HtmlComponents/MoreInfo';
import UserMoreInfo from '../HtmlComponents/UserMoreInfo';

export default function JoinRequestList({ joinRequest }) {
    console.log('ITEM LIST')
    const { globals, userData, showToast, openMsgModal, createUserInfo, apiCall, openYesNoModal, getApiPath } = useContext(GlobalContext);
    
    
    async function acceptUser(){
        
        let response = await apiCall(getApiPath() + 'company/acceptUser', {reqID: joinRequest.id});
        console.log('response: ', response);
        if(response.status == 1){
            showToast('User accepted successfully.', 3, false);
        }
    }

    async function denyUser(){
        
        let response = await apiCall(getApiPath() + 'company/denyUser', {reqID: joinRequest.id});
        console.log('response: ', response);
        if(response.status == 1){
            showToast('User denied successfully.', 3, false);
        }
    }
    

    function onButtonsPressed(button){
        console.log('button: ', button);
        if(button == 'Accept'){
            // acceptUser();
            openYesNoModal('Accept user?', acceptUser)
        }else if(button == 'Deny'){
            openYesNoModal('Deny user?', denyUser)
            // denyUser();
        }
    }

    let photoPassword = userData.photoPassword;
    let imgPath = globals.varNames.imgPath;
    if(userData.isSosyal){
        photoPassword = userData.sosyalData.photoPassword;
        imgPath = globals.varNames.imgPathBlags;
    }

    let url = 'http';
    
    if(joinRequest.imgurl != ''){
        url = imgPath + joinRequest.imgurl + '/' + userData.userID + '/' + photoPassword
        // url = profileData.profileImgUrl;
    }

    let userInfo = createUserInfo(joinRequest, false);

    return (
        <Paper elevation={2} className='px-2'>
            <UserInfoDiv imgPathBegin={imgPath} userInfo={userInfo} photoPassword={photoPassword} />
            {/* <div className='d-flex'>
                <UserImg imgPathBegin={imgPath} imgURL={joinRequest.imgurl} userID={userData.userID} photoPassword={photoPassword} />
                
                <div className='ml-2'>
                    <UserLink verified={joinRequest.verifieduser} username={joinRequest.username} />
                    <h6>{joinRequest.name}</h6>
                    <MoreInfo>
                        <Rating size={17} isVerified={false} rating={joinRequest.rating} numOfRaters={joinRequest.numofraters} />
                        <Rating size={17} isVerified={true} rating={joinRequest.ratingverified} numOfRaters={joinRequest.numofratersverified} />   
                    </MoreInfo>
                    <UserMoreInfo isSosyal={userData.isSosyal} includeCompany={false} user={joinRequest} />
                   
                    <Text>{JSON.stringify(joinRequest)}</Text>
                </div>
                
            </div> */}
            <Tab headers={['Accept', 'Deny']} onChangeTab={onButtonsPressed} />
            
        </Paper>
    );
}