import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';

// import { View, ScrollView } from 'react-native';

// import { Container, Header, Content, Button, Text, Input, Item } from 'native-base';
// import SearchList from '../List/SearchList'


import InsidePageWrapper from '../HtmlComponents/InsidePageWrapper';
// import PageNav from '../Components/PageNav';
import SearchDiv from '../Components/SearchDiv';
import SerchItemsDiv from '../Components/SerchItemsDiv';

// import Title from '../HtmlComponents/Title';
// import Tab from '../HtmlComponents/Tab';

// import { IconToggle } from 'react-native-material-ui';



export default function SerchPage() {
	console.log('LIST')
	// const { userData, globals, getTimeOut, apiCall, openSingleModal, getApiPath } = useContext(GlobalContext);
    const [tab, setTab] = useState('Search');
    // const [startPoint, setStartPoint] = useState(0)
    function onChangeTab(page){
        setTab(page);
        
    }

    let page = <SearchDiv isSearch={false} />;
    if(tab == 'Items'){
        // reviews, startPt, setStartPt, getReviews
        page = <SerchItemsDiv />;
    }
    
    return (
        // <View style={{flex: 1}}>
        //     <Tab headers={['Search', 'Items']} moreTabs={[]} onChangeTab={(tab)=>setTab(tab)}></Tab>
            
            

            
        // </View>
        <InsidePageWrapper onChangeTab={onChangeTab} tabs={['Search', 'Items']}>
            <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column pt-1'>
                {page}
            </div>
             
        </InsidePageWrapper>
    );
}