import React, {useState, useContext, useRef} from 'react'; 



import {GlobalContext} from '../../Context/GlobalProvider';
import {TextField} from '@material-ui/core';
import AppModal from '../HtmlComponents/AppModal';

export default function BuyDataModal({closeModal}) {
    console.log('EDIT item modal');
    // console.log('item data: ', itemData);
    const {globals, apiCall, singleModalData, getApiPath, showToast} = useContext(GlobalContext);
    console.log(globals.numOfItems)

    const [dataInputs, setDataInputs] = useState({
        amount: 0,
        
    });
    const [error, setError] = useState('');
    // var startPoint = 0

    async function buyData(){
        console.log('SUBMIT NEW ITEM');
    
        let data = {
            amount: dataInputs.amount,
                
        }

        

        // let response = await apiCall(getApiPath() + 'pay/buyData', data); 
        apiCall(getApiPath() + 'pay/buyData', data).then((response) => {
            console.log('response: ', response);
            if(response.status == 1){
                showToast('Buy data successful.', 5);
            }
        });
        closeModal();
    }

   
    
    return (
        <AppModal error={error} onButtonsPressed={buyData} buttons={['Submit']} closeModal={closeModal}>
            <TextField onChange={(e)=>setDataInputs({...dataInputs, amount: e.target.value})} id="standard-basic" label="Amount" />
            {/* <View>
                <Item>
                    <Input onChangeText={(text)=>setDataInputs({...dataInputs, amount: text})} placeholder="Amount" />
                </Item>
                
                 
            </View> */}
            
        </AppModal>
    );
}