    // let timeOutID = 0;


    let userSearchOnly = false;
    if(userData.isShappers){
        userSearchOnly = true;
    }

    const [options, setOptions] = useState({
        
        
        
        searchValue: '',
        searchType: 'username',
        
        numOfRaters: 0,
        
        
        userSearch: true,
        companySearchEnabled: false,
        isVerifiedRatings: false,
        companySearch: {
            searchType: 'username',
            numOfRaters: 0,
            searchValue: '',
            isVerifiedRatings: false
        },
        otherLabel: 'Item',
        userSearchOnly: userSearchOnly,
        isSearch: true,
        // allSite: false,
        siteUsername: ''
    });

    function setSearchOptions(optionsModal){
        
        setOptions({
            ...options,
            userSearch: optionsModal.userSearch,
            searchType: optionsModal.searchType,
            numOfRaters: optionsModal.numOfRaters,
            isVerifiedRatings: optionsModal.isVerifiedRatings,
            companySearchEnabled: optionsModal.companyEnabled,
            companySearch: {
                searchType: optionsModal.company.searchType,
                numOfRaters: optionsModal.company.numOfRaters,
                searchValue: optionsModal.company.searchValue,
                isVerifiedRatings: optionsModal.company.isVerifiedRatings
            }
        })      
        
        
    }

    // function onSearchInputPressed(){
    //     console.log('on search inputs pressed');
    //     if(!isTyping){
    //         setIsTyping(true);         
    //         getHints()
    //     }
        
    // }

    // function onChangeSearchInput(e){
