
import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';


import AppModal from '../HtmlComponents/AppModal';
import Rating from '@material-ui/lab/Rating';
import {TextField} from '@material-ui/core';



export default function ReviewModal({closeModal}) {
    console.log('NewMsgModal');
    
    const {userData, globals, showToast, singleModalData, apiCall, getApiPath, convertEmptyStr} = useContext(GlobalContext);
    
    const [reviewInputs, setReviewInputs] = useState({
        comment: '',
        postedOn: 0,
        rate: 1
    });

    const [rate, setRate] = useState(1);

    // const [error, setError] = useState('');

    async function submitReview(){
        console.log('submit review');
        console.log('review inpputs: ', reviewInputs);
        let commentCheck = convertEmptyStr(reviewInputs.comment);
        if(commentCheck == ''){
            let text = '';
            
            text = 'Review cannot be empty.';
            
            showToast(text, 3, true);
            return;
            
        }
        
        let data = {
            comment: reviewInputs.comment,
            postedOn: singleModalData.data,
            rate: rate    
        }

        let response = await apiCall(getApiPath() + 'user/submitReview', data);
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Review submitted successfully.', 3, false); 
            closeModal();   
        }
    }

    function onButtonsPressed(button){
        
        if(button == 'Submit'){
            submitReview();
        }
    }

    function onRate(e, newValue){
        setRate(newValue);
    }

    // function ratingCompleted(rating){
    //     console.log('rating: ', rating);
    //     console.log('ratings: ', rating);
    //     console.log('reveew inputs: ', reviewInputs);
    //     setReviewInputs({...reviewInputs, rate: rating});
    //     console.log('revew inputs: ', reviewInputs);
    // }
    
    return (
        
        <AppModal buttons={['Submit']} onButtonsPressed={onButtonsPressed} closeModal={closeModal}>
            {/* <Item>
                <Input onChangeText={(text)=>{setReviewInputs({...reviewInputs, comment: text})}} placeholder="Review" />
            </Item>
            
            <Rating
                // showRating
                imageSize={30}
                onFinishRating={(rating)=>{setRate(rating)}}
                style={{ paddingVertical: 10 }}
                ratingCount={5}
                startingValue={1}
            /> */}
            <TextField onChange={(e)=>setReviewInputs({...reviewInputs, comment: e.target.value})} id="standard-basic" label="Comment" />
            <Rating
                className='mt-2'
                name="simple-controlled"
                value={rate}
                onChange={onRate}
            />
            
           
            
        </AppModal>   
    );
}
