import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';



// import PageNav from './PageNav';




import MemberList from '../List/MemberList';


export default function Members({coID, fromUser}) {
    console.log('JOIN REQUESTS');
    const {userData, globals, apiCall, getApiPath, showPageNav, closePageNav} = useContext(GlobalContext);
    // const [startPt, setStartPt] = useState(0);
    const [members, setMembers] = useState([]);
    
    
    // console.log('msg headers:', messageData.msgHeaders);

    async function getMembers(startPt){
        let data = {
            coID: coID,
            startPoint: startPt
        }
        let response = await apiCall(getApiPath() + 'company/getMembers', data);
        console.log('response: ', response);
        if(response.status == 1){
            setMembers(response.payload);
            // setMsgHeaders(response.payload);
            // messageData.msgHeaders = response.payload;
            // console.log('message headers: ', messageData.msgHeaders);
            // forceUpdate();
        }     
    }

    useEffect(() => {
        // Update the document title using the browser API
        showPageNav(getMembers);
        getMembers(0);

        return closePageNav
    }, []);
    
    
    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column mt-1 px-2'>
            
            
            <div style={{flex: 1, overflow: 'auto'}} className='px-1'>
                {members.map((member) => {
                    return <MemberList fromUser={fromUser} member={member} key={member.id}/>
                    
                    
                })}
            </div>
            
            
           
            {/* <div style={{alignItems: 'flex-end'}} className='d-flex flex-column px-3'>
                <PageNav startPt={startPt} getItems={getMembers} setStartPt={setStartPt} isForward={true}/>
            </div> */}
            
            
        </div>
    );
}