const GlobalProvider = {};

GlobalProvider.apiCall = async (url, data, state, forceUpdate) => {
    let response = await axios({
        method: 'post',
        url: url,
        data: data,
        headers: {
            'x-access-token': state.userData.token,
            'version': state.appData.version,
            'sosyal-version': state.appData.versionSosyal,
        }    
    })

    response.data.globalError = false;

    if(response.data.status == 0){
        if(!GlobalProvider.checkErrors(response.data.code, state, forceUpdate)){
            GlobalProvider.showToast(response.data.message, 3, true, state, forceUpdate);
        }
    }

    // GlobalProvider.showToast(response.data.message, 3, true, state, forceUpdate);
    // console.log('response: ', response);
    return response.data;

    
};

GlobalProvider.checkErrors = (code, state, forceUpdate) => {
    let errorExist = false;
    // let codeFinal
    console.log('state. globals: ', state.globals);
    console.log('code: ', code);
    state.globals.errorCodes.forEach((errorCode) => {
        if(code == errorCode){
            errorExist = true;
            if(errorCode == 500){
                GlobalProvider.openSingleModal('Info', {div: 'Data'}, state, forceUpdate);  
                // showToast('wsjf alsjfafsf ljsfajfjsf ;ajsfjjf ;asfjajs  f lakjjdflajfjajf ;adfjsjff asfa; asdfsldkf a;sdfasf jalsfjfa afjaf;af kajsjfal', 5000);
            }else if(errorCode == 501){
                GlobalProvider.openSingleModal('Info', {div: 'Balance'}, state, forceUpdate);
                // showToast('wsjf alsjfafsf ljsfajfjsf ;ajsfjjf ;asfjajs  f lakjjdflajfjajf ;adfjsjff asfa; asdfsldkf a;sdfasf jalsfjfa afjaf;af kajsjfal', 5000);
            }else if(errorCode == 56){
                GlobalProvider.showToast('Request limit exceeded. Try again after a few seconds.', 3, true, state, forceUpdate);
                
                
            }else if(errorCode == 5){
                GlobalProvider.showToast('Invalid token.', 3, true, state, forceUpdate);
                GlobalProvider.logout(state, forceUpdate);
                
            }
        }
    })  
    
    return errorExist;
};

