GlobalProvider.showToast = (message, duration, isError, state, forceUpdate) => {
    console.log('open modal start');
    state.userData.showingToast = true;
    state.toastData.message = message;
    state.toastData.duration = duration;
    state.toastData.isError = isError;
    forceUpdate();
};

GlobalProvider.getUserData = async (token, state, initiateUserData, forceUpdate) => {
    console.log('get user data');
    console.log('api path: ', state.globals.varNames.apiPath);
    // console.log('token: ', state.userData.token);
    state.userData.token = token;
    localStorage.setItem('userData', JSON.stringify({token: state.userData.token}));
    var response = await GlobalProvider.apiCall(state.globals.varNames.apiPath + 'user/getUserData', {}, state, forceUpdate);  
    
    console.log('response: ', response);
    if(response.status == 1){
        initiateUserData(response.payload.userData);
        // GlobalProvider.setVariables(response.payload, state);
        // GlobalProvider.initiateGlobals(response.payload.globals, state);
        var responseSosyal = await GlobalProvider.apiCall(state.globals.varNames.blagsApiPath + 'user/getUserDataOnly', {}, state, forceUpdate);
        console.log('response sosyal: ', responseSosyal);
        if(responseSosyal.status == 1){
            GlobalProvider.initiateSosyalData(responseSosyal.payload, state);
            // GlobalProvider.setLimits(responseSosyal.payload, state);
            state.userData.currentPage.back = 'Home';
            state.userData.loggedIn = true;
        }else{
            state.userData.loggedIn = false;
        }
        
        
        
        // connectToSocket();
        
        // dispatch({
        //     type: 'UPDATE_USER_DATA',
        //     payload: 'username'
        // });
    }else{
        // state.userData.gotUserData = true;
        state.userData.loggedIn = false;    
    }

    state.userData.gotUserData = true;
    forceUpdate();
};

