GlobalProvider.checkPassInput = (password, state) => {
    let minChar = state.globals.limits.min.password;
    let maxPassLength = 20;
    if(!state.userData.production){
        minChar = 0;
    }
    let numUpper = (password.match(/[A-Z]/g) || []).length;
    let numLower = (password.match(/[a-z]/g) || []).length;
    let numInt = (password.match(/[0-9]/g) || []).length;
    let status = false;
    let message = '';
    // var specialCharPass = (password.match(/[^a-z0-9]/ig) || []).length;
    if(numUpper >= minChar && numLower >= minChar && numInt >= minChar){
        message = '';
        // status = true;
        
    }else{
        if(numUpper < minChar){
            
            message = 'Password must contain at least ' + minChar + ' capital letters.';
        }else if(numLower < minChar){
            message = 'Password must contain at least ' + minChar + ' small letters.';     
        }else{
            message = 'Password must contain at least ' + minChar + ' numbers.';
            
        }
        
    }

    return message;
}

GlobalProvider.checkRepeatPassInput = (password, repeatPass) => {
    if(password != repeatPass){
        return 'Password and repeat password are not equal.';
    }else{
        return '';
    }
}

GlobalProvider.checkUsernameInput = (username, state) => {
    let minChar = state.globals.limits.min.username;
    let label = 'Username';
    let msg = '';
    // if(!isUsername){
    //     minChar = 5;
    //     label = 'Address';
    // }
    let maxChar = state.globals.limits.max.username;
    if(state.userData.production){
        minChar = 0;
    }

    let specialCharUsername = (username.match(/[^a-z0-9_]/ig) || []).length;
    if(specialCharUsername == 0 && username.length >= minChar && username.length <= maxChar){
        msg = '';
        
    }else{
        if(specialCharUsername > 0){
            msg = label +' can only contain numbers, letters and _.';
            // setError(label +' can only contain numbers, letters and _.');
        }else if(username.length < minChar){
            msg = label +' must be at least ' + minChar + ' characters.';
            // setError(label +' must be at least ' + minChar + ' characters.');
        }else{
            msg = label +' cannot exceed ' + maxChar + ' characters.';
            // setError(label +' cannot exceed ' + maxChar + ' characters.');    
        }
    }

    return msg;
}

