GlobalProvider.getLinks = (text, linkType) => {
    let words = text;
    let wordArray = [];
    if(linkType == 'userLink'){
        wordArray = words.split(/@([a-z0-9]+)/i);
    }else if(linkType == 'link'){
        wordArray = words.split(/\[link\]([a-z0-9.\/:]+)\[\/link\]/i);
    }
    
    let wordsObjArr = [];
    if(wordArray.length > 1){
        wordArray.forEach((word, ndx) => {
            if((ndx + 1) % 2 == 0){
                wordsObjArr.push({
                    text: word,
                    obj: linkType
                })
            }else{
                wordsObjArr.push({
                    text: word,
                    obj: 'text'
                })   
            }
        })
    }
    

    return wordsObjArr;

    
    
}

GlobalProvider.convertTextToObj = (text) => {
        
    let wordsObjArr = GlobalProvider.getLinks(text, 'userLink');
    // console.log('words obj arr: ', wordsObjArr);
    if(wordsObjArr.length == 0){
        wordsObjArr.push({
            text: text,
            obj: 'text'   
        })
    }
    
    let wordsObjArrLinks = [];
    for(let i = 0; i < wordsObjArr.length; i++){
        if(wordsObjArr[i].obj == 'text'){
            wordsObjArrLinks = GlobalProvider.getLinks(wordsObjArr[i].text, 'link'); 
            // console.log('words obj links: ', wordsObjArrLinks);
            if(wordsObjArrLinks.length > 0){
                
                wordsObjArrLinks.forEach((wordObjLinks, ndx) => {
                    wordsObjArr.splice(i + ndx + 1, 0, wordObjLinks);
                    // wordsObjArr.push(wordObjLinks);
                })
                wordsObjArr.splice(i, 1);
            }    
        }
    }

    
    
    

    return wordsObjArr;

    
    
}

