import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
// import {GlobalContext} from '../../Context/GlobalProvider';
import ProfileDiv from '../Components/ProfileDiv';
import {Button, Tabs, Tab, Paper} from '@material-ui/core';
import InsidePageWrapper from '../HtmlComponents/InsidePageWrapper';
import Reviews from '../Components/Reviews';
// import EditUser from '../Components/EditUser';
import PostsDiv from '../Components/PostsDiv';

// // import { Container, Header, Content, Button, Text, Input, Item, Tab, Tabs, TabHeading, Icon } from 'native-base';
// import Tab from '../HtmlComponents/Tab';
export default function ProfilePage() {
    const {userData, updateUserData, globals, apiCall, changePage, countries, openSingleModal} = useContext(GlobalContext);
    // const {updateUserData} = useContext(GlobalContext);
    
    const [gotUserData, setGotUserData] = useState(false);
    
    const [tab, setTab] = useState('Profile');
    
    let profile = {
        userID: userData.userID,
        imgURL: userData.imgURL,
        imgPath: globals.varNames.imgPath,
        profileImgUrl: globals.varNames.imgPath + userData.imgURL + '/' + userData.userID + '/' + userData.photoPassword,
        username: userData.username,
        name: userData.name,
        description: userData.description,
        country: countries[userData.country],
        rate: userData.rating,
        numOfRaters: userData.numOfRaters,
        rateVerified: userData.ratingVerified,
        numOfRatersVerified: userData.numOfRatersVerified,
        isSosyal: userData.isSosyal,
        photoPassword: userData.photoPassword,
        bidBal: userData.bidBal,
        bidAmt: userData.bidAmt,
        company: userData.company,
        verifiedUser: userData.verifiedUser
    }

    let bidName = 'User';
    
    if(userData.isSosyal){
        console.log('user data: ', userData);
        profile = {
            userID: userData.userID,
            imgURL: userData.sosyalData.imgURL,
            imgPath: globals.varNames.imgPathBlags,
            profileImgUrl: globals.varNames.imgPathBlags + userData.sosyalData.imgURL + '/' + userData.userID + '/' + userData.sosyalData.photoPassword,
            username: userData.username,
            name: userData.sosyalData.name,
            description: userData.sosyalData.description,
            country: countries[userData.sosyalData.country],
            rate: userData.sosyalData.rating,
            numOfRaters: userData.sosyalData.numOfRaters,
            rateVerified: userData.sosyalData.ratingVerified,
            numOfRatersVerified: userData.sosyalData.numOfRatersVerified,
            isSosyal: userData.isSosyal,
            photoPassword: userData.sosyalData.photoPassword,
            bidBal: userData.sosyalData.bidBal,
            bidAmt: userData.sosyalData.bidAmt,
            company: userData.sosyalData.company,
            verifiedUser: userData.sosyalData.verifiedUser
        }  
        
        bidName = 'User Sosyal';
    }
    
    // function getProfileDatas(){
    //     getUserData(userData.token);
    //     // setGotUserData(true);
    // }
    const [value, setValue] = useState(2);

    function onChangeTab(value){
        console.log('value: ', value);
        if(value != 'Edit' && value != 'Bid'){
            setTab(value);
        }else{
            if(value == 'Edit'){
                changePage('Edit Profile');
        //         <Button size='small' className='mr-1' variant="contained" onClick={()=>changePage('Edit Profile')}>Edit</Button>
        // <Button size='small' variant="contained" onClick={()=>openSingleModal('Bid', {bidAmt: profileData.bidAmt, itemName: bidName, id: profileData.userID})}>Bid</Button>
            }else{
                openSingleModal('Bid', {bidAmt: profile.bidAmt, itemName: bidName, id: profile.userID, bidBal: profile.bidBal})   
            }
        }
        
    }
    
    useEffect(() => {
        // Update the document title using the browser API
        updateUserData();
        
    }, []);

    let page = <ProfileDiv profileData={profile} isUser={true} />;
    if(tab == 'Reviews'){
        // reviews, startPt, setStartPt, getReviews
        page = <Reviews userData={profile} isUser={true} />;
    }else if(tab == 'Posts'){
        page = <PostsDiv isPostResult={false} postResult={null} isProfilePage={true} userViewedID={userData.userID} />
        
    }
    
    return (
        <InsidePageWrapper onChangeTab={onChangeTab} tabs={['Profile', 'Reviews', 'More']} moreTabs={['Posts', 'Edit', 'Bid']}>
            {page}
        </InsidePageWrapper>
        // <div style={{flex: 1}}>
            
            
        //     <Tab headers={['Profile', 'Reviews', 'More']} moreTabs={['Items', 'Posts']} onChangeTab={(tab)=>setTab(tab)}></Tab>
            
        //     {page}
        // </div>
    );
}

