import React, {useState, useContext, forwardRef, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import Axios from 'axios';
import {Button, TextField, Avatar, FormControl, Select, MenuItem} from '@material-ui/core';
import UserImg from '../Components/UserImg'
export default function EditVerifyAccount({setIsEditing}) {
    const {userData, appData, randomStr, showToast, globals, openSingleModal, datePickerStr, getApiPath, countries} = useContext(GlobalContext);
    
    
    // async function editAccount(){
    //     console.log('send money');
    //     var data = {
    //         name: verifyInputs.name,
    //         username: verifyInputs.address
    //     }
    //     // console.log('options: ', options);
    //     let response = await apiCall(globals.varNames.blagsApiPath + 'user/verifyAccount', data);
    //     console.log('response: ', response);
    //     if(response.status == 1){
            
    //     }
    // }

    async function uploadPhoto(image){
        let formData = new FormData();
        formData.append('file', image);
        // formData.append('userID', userData.userID);
        formData.append('userID', userData.userID);
        let options = {
            method: 'post',
            url: globals.varNames.blagsApiPath + 'user/uploadVerifiedPhoto',
            data: formData,
            headers: {
                'x-access-token': userData.token,
                'version': appData.version,
                'sosyal-version': appData.versionSosyal,
                'photo-token': randomStr(5),
                
            }
        }

        let response = await Axios(options);
        console.log('response: ', response.data);
        if(response.data.status == 1){
            showToast(response.data.message, 5, false);
            // closeModal();   
        }
    }
    
    let url = 'http';
    if(userData.verifiedData.imgURL != ''){
        url = globals.varNames.verifiedPhotoPath + userData.verifiedData.imgURL + '/' + userData.userID + '/' + userData.sosyalData.photoPassword;
        // url = profileData.profileImgUrl;
    }

    console.log('url: ', url);
    let gender = 'Male';
    if(userData.verifiedData.isMale == 0){
        gender = 'Female';
    }
    
    return (
        <div className='d-flex flex-column'>
            {/* <Image style={{width: 100, height: 100, backgroundColor: 'blue', alignSelf: 'center'}} source={{uri: url}}/>
            <Avatar alt="Remy Sharp" src={imgURI} /> */}
            <div className='d-flex flex-column mb-2 border-bottom py-2' style={{alignItems: 'center'}}>
                <UserImg size={120} imgPathBegin={globals.varNames.verifiedPhotoPath} imgURL={userData.verifiedData.imgURL} userID={userData.userID} photoPassword={userData.sosyalData.photoPassword} />
                {/* <Button raised primary text="Submit" onPress={()=>openSingleModal('Upload Verified Photo', '')} /> */}
                <Button className='mt-2' style={{width: '70%'}} variant="contained" onClick={()=>openSingleModal('Upload Verified Photo', '')}>Upload Photo</Button>
            </div>
            <div className='d-flex flex-column mb-2 border-bottom py-2'>
                <div><span style={styles.label}>Name: </span><span style={styles.value}>{userData.verifiedData.name}</span></div>
                <div><span style={styles.label}>Address: </span><span style={styles.value}>{userData.verifiedData.address}</span></div>
                <div><span style={styles.label}>Birthdate: </span><span style={styles.value}>{datePickerStr(userData.verifiedData.birthdate * 1000)}</span></div>
                <div><span style={styles.label}>Country: </span><span style={styles.value}>{countries[userData.verifiedData.country]}</span></div>
                <div><span style={styles.label}>Gender: </span><span style={styles.value}>{gender}</span></div>
                {/* <span>Address: {userData.verifiedData.address}</span>
                <span>Birthdate: {datePickerStr(userData.verifiedData.birthdate * 1000)}</span>
                <span>Country: {countries[userData.verifiedData.country]}</span>
                <span>Gender: {gender}</span> */}
                
                {/* <Button raised primary text="Edit" onPress={()=>setIsEditing(true)} /> */}
                <Button className='mt-3' style={{width: '70%', alignSelf: 'center'}} variant="contained" onClick={()=>setIsEditing(true)}>Edit</Button>
            </div>
            
            
        </div>
        
    );
}

const styles = {
    label: {
        fontSize: 15
    },
    value: {
        fontSize: 15,
        fontWeight: 700
    }
}