import React, {useState, useContext, useRef, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import {Button, TextField, Link} from '@material-ui/core';
import Tab from '../common/HtmlComponents/Tab';
import InsidePageWrapper from '../common/HtmlComponents/InsidePageWrapper';

// import DetailsDiv from '../Components/DetailsDiv';
import ItemList from '../List/ItemList';
import UserImg from '../common/Components/UserImg';
import UserInfoDiv from '../common/HtmlComponents/UserInfoDiv';
import DetailsDiv from '../common/Components/DetailsDiv';
// import UserLink from '../co/HtmlComponents/UserLink';

export default function GroserModal() {
    console.log('AppModal');
    const {userData, globals, apiCall, openMsgModal, openYesNoModal, createUserInfo, showToast, showOtherUserList, groserModalData, forceUpdate} = useContext(GlobalContext); 
    
    const [isMinimized, setIsMinimized] = useState(false);
    const [tab, setTab] = useState(false);
    
    const {groser, items} = groserModalData.data;
    let btns = ['Message', 'Items'];
    if(userData.isShappers){
        if(!userData.groserMode){
            btns = ['Message', 'Hire']
        }
    }

    console.log('groser: ', groser);
    async function hireUser() {
        console.log('hire user');
        let response = await apiCall(globals.varNames.apiPath + 'map/hire', {hireeID: groser.userid});
        console.log('response: ', response); 
        if(response.status == 1){
            showToast('User hired successfully.', 3, false);
        }
    }
    

    function onButtonsPressed(button){
        if(button == 'Message'){
            // username, msg, msgID, headerID
            openMsgModal(groser.username, '', 0, 0); 
        }else if(button == 'Items'){
            showOtherUserList(groser.userid);  
        }else if(button == 'Hire'){
            openYesNoModal('Hire user?', hireUser);  
        }
    }

    function onChangeTab(tab){
        setTab(tab);
    }

    function closeModal(){
        userData.openingGroserModal = false;
        forceUpdate();
    }
    

    useEffect(() => {
        // Update the document title using the browser API
        if(userData.showingOtherPage){
            setIsMinimized(true);
        }else{
            setIsMinimized(false);   
        }
        
    }, [userData.showingOtherPage]);

    // let width = '100%';
    // if(isMinimized){
    //     width = '0%';
    // }

    let display = ' d-flex';
    if(isMinimized){
        display = ' d-none';
    }

    let itemsDiv = null;
    let item = null;
    if(groser.itemsNdx != null){
        itemsDiv = <div className='px-1' style={{flex: 1, overflow: 'auto'}}>
            {groser.itemsNdx.map((itemNum) => {
                item = items[itemNum];
                return <ItemList isSearch={true} key={item.id} item={item} fromModal={true} isUser={false}/>
                
                
            })}
        </div>  
    }

    // let page = <div className='d-flex'>
    //     <UserImg imgPathBegin={globals.varNames.imgPath} imgURL={groser.imgurl} userID={userData.userID} photoPassword={userData.photoPassword} />    
    //     <div className='d-flex flex-column'>
    //         <span>{groser.username}</span>
    //         <span>{groser.name}</span>
    //         <span>{groser.description}</span>
    //     </div>
    // </div>
    let imgPathBegin = globals.varNames.imgPath;
    let photoPassword = userData.photoPassword;
    // let userInfo = createUserInfo(groser, false);
    // let companyInfo = createUserInfo(groser, true);

    let page = <DetailsDiv info={groser} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />
    // let page = <UserInfoDiv userInfo={groser} imgPathBegin={globals.varNames.imgPath} photoPassword={userData.photoPassword} includeCompany={true} />

    if(tab == 'Items'){
        page = <div className='d-flex'>
            {itemsDiv}
        </div>
    }

    let mainDiv = <InsidePageWrapper onChangeTab={onChangeTab} tabs={['Profile', 'Items']}>
        <div className='d-flex flex-column p-1'>
            {page}
        </div>
        
    </InsidePageWrapper>

    if(userData.isShappers || itemsDiv == null){
        mainDiv = <div style={{flex: 1}} className='d-flex flex-column'>
            {page}   
        </div>
    }

    return (
       
        // <ModalBase styleView={{paddingVertical: 15, paddingHorizontal: 5}} style={{position: 'absolute', top: 110, width: width, backgroundColor: 'white'}}>
        //     <View>
        //         <View style={{flexDirection: 'row', justifyContent: 'flex-end'}}>
        //             <Button primary text='Close' onPress={closeModal} transparent />
                        
        //         </View>
        //         <View>
        //             <DetailsDiv info={groser} imgPathBegin={globals.varNames.imgPath} photoPassword={userData.photoPassword} />
        //             {itemsDiv}
        //         </View>
                
            
        //         <View style={{flexDirection: 'row'}}>
        //             {btns.map((button, id) => {
        //                 return (
        //                     <Button style={{container: {marginRight: 3}}} raised key={id} primary text={button} onPress={()=>onButtonsPressed(button)} />
                            
        //                 )
                        
        //             })}   

        //         </View>
        //     </View>
            
        // </ModalBase>
        <div className={'shadow-lg flex-column p-3' + display} style={{position: 'absolute', backgroundColor: 'white', top: 70, right: 15, left: 15, bottom: 50, zIndex: 2}}>
            <div className='d-flex flex-row mb-3' style={{justifyContent: 'flex-end'}}>
                <Button onClick={closeModal}>Close</Button>
            </div>
            {/* <InsidePageWrapper onChangeTab={onChangeTab} tabs={['Profile', 'Items']}>
                {page}
            </InsidePageWrapper> */}
            {mainDiv}
            {/* <DetailsDiv info={groser} imgPathBegin={globals.varNames.imgPath} photoPassword={userData.photoPassword} />
            <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column mb-3 pb-4 px-3'>
            
                
                {itemsDiv}
            </div> */}
            <Tab moreTabs={['haha']} headers={btns} isBtn={true} onChangeTab={onButtonsPressed} />
            {/* <div style={{flex: 1, overflow: 'auto'}} className='p-3 d-flex flex-column'>
                
            </div> */}
        </div>
       
       
    );
}