import React, {useState, useContext, useRef} from 'react'; 



import {GlobalContext} from '../../Context/GlobalProvider';

import AppModal from '../HtmlComponents/AppModal';
import Note from '../HtmlComponents/Note';
import {Button, TextField, Link} from '@material-ui/core';

// [tags, description, name, id, siteUsername, bidAmt]
export default function NewSerchItemModal({closeModal}) {
    console.log('NewItemModal');

    const {userData, globals, apiCall, minBidNote, consumptionNote, convertTagsStrToArrayStr, tagsNote, convertTagsToStr, singleModalData, showToast, convertNumToStr, convertStrToNum, convertEmptyStr} = useContext(GlobalContext);
    console.log(globals.numOfItems)
    console.log('single modal data: ', singleModalData);

    let tagsStr = convertTagsToStr(singleModalData.data.tags);

    let siteUsername = '';
    if(singleModalData.data.siteUsername != null){
        siteUsername = singleModalData.data.siteUsername;
    }

    const [item, setItem] = useState({
        name: singleModalData.data.name,
        description: singleModalData.data.description,
        link: singleModalData.data.link,
        tags: tagsStr,
        itemID: singleModalData.data.id,
        siteUsername: siteUsername,
        bidAmt: convertNumToStr(singleModalData.data.bidAmt / 1000)
    });

    async function addNewItem(){
        console.log('SUBMIT NEW ITEM');
        let itemName = convertEmptyStr(item.name);
        let itemDescription = convertEmptyStr(item.description);
        let itemLink = convertEmptyStr(item.link);
        
        if(itemName == '' || itemDescription == '' || itemLink == ''){
            
            let msg = 'Name cannot be empty.';
            if(itemDescription == ''){
                msg = 'Description cannot be empty.';
            }else if(itemLink == ''){
                msg = 'Link cannot be empty.';
            }
            
            showToast(msg, 3, true);
            // Toast.show(toastObj);
            return;
        }

        let arrayStr = convertTagsStrToArrayStr(item.tags);

        let data = {
            tags: arrayStr,
            description: item.description, 
            link: item.link,
            name: item.name,
            siteUsername: item.siteUsername
        }

        let path = 'serch/newItem';
        if(singleModalData.data.id != 0){
            path = 'serch/editItem';
            data.itemID = item.itemID;
            data.bidAmt = convertStrToNum(item.bidAmt);
        }

        let response = await apiCall(globals.varNames.blagsApiPath + path, data); 
        console.log('response: ', response);
        if(response.status == 1){
            showToast(response.message, 3, false);
            closeModal();
        }
        
        
    } 

    let bidAmtInput = null;
    let minNote = null;
    if(singleModalData.data.id != 0){
        // bidAmtInput = <Item>
        //     <Input value={item.bidAmt} onChangeText={(text)=>setItem({...item, bidAmt: text})} placeholder="Bid Amount" />
        // </Item>;
        bidAmtInput = <TextField value={item.bidAmt} onChange={(e)=>setItem({...item, bidAmt: e.target.value})} id="standard-basic" label="Bid Amount" />
        minNote = <Note note={minBidNote()} />;
    }

    return (
        <AppModal consumptionShow={true} buttons={['Submit']} onButtonsPressed={addNewItem} closeModal={closeModal}>
            <TextField value={item.name} onChange={(e)=>setItem({...item, name: e.target.value})} id="standard-basic" label="Name" />
            <TextField value={item.description} onChange={(e)=>setItem({...item, description: e.target.value})} id="standard-basic" label="Description" />
            <TextField value={item.link} onChange={(e)=>setItem({...item, link: e.target.value})} id="standard-basic" label="Link" />
            <TextField value={item.tags} onChange={(e)=>setItem({...item, tags: e.target.value})} id="standard-basic" label="Tags" />
            <Note note={tagsNote()} />
            <TextField value={item.siteUsername} onChange={(e)=>setItem({...item, siteUsername: e.target.value})} id="standard-basic" label="Site Username" />
            {bidAmtInput}
            {minNote}
            {/* <Note note={consumptionNote()} /> */}
            {/* <View>
                <Item>
                    <Input value={item.name} onChangeText={(text)=>setItem({...item, name: text})} placeholder="Name" />
                </Item>
                
                <Item>
                    <Input value={item.description} onChangeText={(text)=>setItem({...item, description: text})} placeholder="Description" />
                </Item>
                <Item>
                    <Input value={item.link} onChangeText={(text)=>setItem({...item, link: text})} placeholder="Link" />
                </Item>
                <Item>
                    <Input value={item.tags} onChangeText={(text)=>setItem({...item, tags: text})} placeholder="Tags" />
                </Item>
                <Item>
                    <Input value={item.siteUsername} onChangeText={(text)=>setItem({...item, siteUsername: text})} placeholder="Site Username" />
                </Item>
                {bidAmtInput}
            </View> */}
            
            
        </AppModal>
    );
}