import React, {useState, useContext} from 'react'; 


import { GlobalContext } from '../../Context/GlobalProvider';



import ErrorLog from '../HtmlComponents/ErrorLog';
// import Title from '../HtmlComponents/Title';

import {Button, TextField} from '@material-ui/core';

export default function ChangePassword() {
    const {userData, globals, showToast, apiCall, checkPassInput, checkRepeatPassInput} = useContext(GlobalContext);
    const [passInputs, setPassInputs] = useState({
        newPass: '',
        repeatPass: '',
        currentPass: '',
        
    });

    const [errors, setErrors] = useState({
        
        password: '',
        repeatPass: ''
    });

    const [error, setError] = useState('');
    
    async function changePass(){
        console.log('change pass');
        // var response = null;
        if(errors.password != '' || errors.repeatPass != '' || passInputs.newPass == '' || passInputs.currentPass == '' || passInputs.repeatPass == ''){
            
            let msg = '';
            if(errors.password != '' || errors.repeatPass != '' ){
                msg = 'Please check errors.';
                
            }else{
                if(passInputs.currentPass == ''){
                    msg = 'Current password cannot be empty.';
                }else if(passInputs.repeatPass == ''){
                    msg = 'Repeat password cannot be empty.';   
                }else{
                    msg = 'New password cannot be empty.';    
                }
                  
            }

            showToast(msg, 3000);
            // Toast.show(toastObj);
            return;
        }

        let data = {
            newPass: passInputs.newPass,
            oldPass: passInputs.currentPass  
        }
        
        let response = await apiCall(globals.varNames.blagsApiPath + 'user/changePassword', data);
       
        
        console.log('response: ', response);
        if(response.status == 1){
            
             
        }
    }


    function onChangePass(e){
        let text = e.target.value;
        setPassInputs({...passInputs, newPass: text});
        // checkInputs(text, passInputs.repeatPass);
        // setError(checkPassInput(text));
        setErrors({...errors, password: checkPassInput(text)});
    }

    function onChangeRepeatPass(e){
        let text = e.target.value;
        setPassInputs({...passInputs, repeatPass: text});
        // checkInputs(passInputs.newPass, text);
        // setError(checkRepeatPassInput(passInputs.newPass, text));
        setErrors({...errors, repeatPass: checkRepeatPassInput(passInputs.newPass, text)});
    }

    return (
        <div className='d-flex flex-column border-bottom py-2'>
            <h5 style={{alignSelf: 'flex-end'}}>Change Password</h5>
            {/* <Title title='Change Password'/> */}
            {/* <Item>
                <Input onChangeText={onChangePass} placeholder="New Password" />
            </Item> */}
            <TextField type={userData.production? 'password' : 'text'} onChange={onChangePass} id="standard-basic" label="New Password" />
            <ErrorLog error={errors.password} />
            {/* <Item>
                <Input onChangeText={onChangeRepeatPass} placeholder="Repeat Password" />
            </Item> */}
            <TextField type={userData.production? 'password' : 'text'} onChange={onChangeRepeatPass} id="standard-basic" label="Repeat Password" />
            <ErrorLog error={errors.repeatPass} />
            {/* <Item>
                <Input onChangeText={(text)=>setPassInputs({...passInputs, currentPass: text})} placeholder="Current Password" />
            </Item> */}
            <TextField type={userData.production? 'password' : 'text'} onChange={(e)=>setPassInputs({...passInputs, currentPass: e.target.value})} id="standard-basic" label="Current Password" />
            <Button className='mt-3' variant="contained" onClick={changePass}>Change</Button>
            {/* <div style={{flexDirection: 'row', marginTop: 5}}>
                <Button raised primary text='Change' onPress={changePass} />
            </div> */}
            
            
           
        </div>

    );
}