import React, {useState, useContext} from 'react'; 

import { GlobalContext } from '../../Context/GlobalProvider';

// import Title from '../HtmlComponents/Title';


import {Button, TextField, Link} from '@material-ui/core';

export default function VerificationPage() {
    const {userData, globals, getUserData, appData, changeOutsidePage, apiCall, showToast} = useContext(GlobalContext);
    const [code, setCode] = useState('');

    async function verify(){
        console.log('register');
        
        let data = {
            emailCode: code,
            email: userData.verification.email,
            siteID: appData.siteID
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'user/confirmRegister', data);
        console.log('response: ', response);
        if(response.status == 1){
            // userData.token = response.data.payload;

            // AsyncStorage.setItem('userData', JSON.stringify({token: userData.token}));
            getUserData(response.payload);    
        }
    }

    async function resendCode(e){
        console.log('register');
        e.preventDefault();
        let data = {
            
            email: userData.verification.email,
            
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'user/resendCode', data);
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Code sent successfully.', 3, false);
            // userData.token = response.data.payload;

            // AsyncStorage.setItem('userData', JSON.stringify({token: userData.token}));
            // getUserData(response.payload);    
        }else{
            showToast(response.message, 3, true);    
        }
    }

    

    return (
       
        <div className='d-flex flex-column pt-5'>
            <h4 style={{alignSelf: 'flex-end'}}>Confirm Account</h4>
            {/* <Title title='Verify'/> */}
            {/* <Item>
                <Input onChangeText={(text)=>setCode(text)} placeholder="Code" />
            </Item> */}
            <p className='mt-5' style={{fontSize: 18}}>A confirmation code has been sent to your email. Enter the code below to confirm your account.</p>
            <TextField onChange={(e)=>setCode(e.target.value)} id="standard-basic" label="Code" />
           
            {/* <Button style = {{container: {marginVertical: 10}}} raised onPress={verify} primary text="Verify" /> */}
            <Button className='mt-4' variant="contained" onClick={verify}>Verify</Button>
            <div className='d-flex mt-3' style={{justifyContent: 'space-between'}}>
                
                {/* <Button onPress={()=>changeOutsidePage('Login')} primary text="Login" /> */}
                <Link className='mb-1 border-bottom' noIndent onClick={(e) => {e.preventDefault(); changeOutsidePage('Login')}}>
                    Login
                </Link>
                {/* <Button onPress={resendCode} primary text="Resend Code" /> */}
                <Link className='mb-1 border-bottom' noIndent onClick={resendCode}>
                    Resend Code
                </Link>
            </div>
            
        </div>
    );
}