import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Link, Button, Card, Paper} from '@material-ui/core';

import UserMoreInfo from '../HtmlComponents/UserMoreInfo';

// import UserRatings from '../Components/UserRatings';
// import UserImg from '../Components/UserImg';
import UserLink from '../HtmlComponents/UserLink';
// import Rating from '../HtmlComponents/Rating';
import Tab from '../HtmlComponents/Tab';
import SiteSpan from '../HtmlComponents/SiteSpan';
import MoreInfo from '../HtmlComponents/MoreInfo';
import DetailsDiv from '../Components/DetailsDiv';

export default function SerchItemList({ item, isUser }) {
    console.log('ITEM LIST')
    const { userData, globals, showToast, apiCall, openSingleModal, openYesNoModal, createUserInfo, convertTagsToStr } = useContext(GlobalContext);
    const [showMoreInfo, setShowMoreInfo] = useState(false);

    console.log('item: ', item);
    console.log('item img: ', globals.varNames.itemsImgPath + item.imgurl);

    async function deleteItem() {
        console.log('DELETE ITEM')
        let response = await apiCall(globals.varNames.blagsApiPath + 'serch/deleteItem', {itemID: item.id});
        console.log('response: ', response);
        if(response.status == 1){
            showToast(response.message, 5);
        }
        
    }

    function onButtonsPressed(button){
        if(button == 'Edit'){
            // [tags, description, name, id, siteUsername, bidAmt]
            openSingleModal('New Serch Item', {name: item.name, description: item.description, link: item.link, id: item.id, tags: item.tags, siteUsername: item.siteusername, bidAmt: item.bidamt});   
        // }else if(button == 'Delete'){
        //     openSingleModal('Upload Item Photo', item);   
        }else if(button == 'Delete'){
            openYesNoModal('Delete this item?', deleteItem);   
        }else if(button == 'Bid'){
            openSingleModal('Bid', {bidAmt: item.bidamt, itemName: 'Serch Item', id: item.id, bidBal: item.bidbal});      
        }
    }

    // let label = 'More Info';
    // if(showMoreInfo){
    //     label = 'Less Info';
    // }
    

    let itemName = item.name;
    let itemDescription = item.description;
    let buttons = ['Edit', 'Delete', 'Bid'];
    let moreInfoBtn = null;

    let tagsStr = convertTagsToStr(item.tags);
    let userText = null;
    // let moreInfoLink = null;
    // let tagsText = <span style={{color: 'blue'}}>{tagsStr}</span>;
    let bidDiv = <div className='d-flex flex-column'>
        <span>Bid Amount: {item.bidamt / 1000}</span>
        <span>Bid Balance: {item.bidbal / 1000}</span>
        <span>Search Tags: {convertTagsToStr(item.tags)}</span>   
    </div>;

    let ratingsDiv = null;
    let borderTop = 'border-top';

    
    if(!isUser){
        // username, rating, ratingVerified, numOfRaters, numOfRatersVerified, imgURL
        let photoPassword = userData.sosyalData.photoPassword;
        let imgPathBegin = globals.varNames.imgPathBlags;
        
        borderTop = '';
        itemName = item.itemname;
        itemDescription = item.itemdescription;
        // let userInfo = createUserInfo(item.username, item.rating, item.ratingverified, item.numofraters, item.numofratersverified, item.imgurl);
        // let companyInfo = createUserInfo(item.companyusername, item.companyrating, item.companyratingverified, item.companynumofraters, item.companynumofratersverified, item.companyimgurl);
        // <Button variant="contained" onClick={()=>openYesNoModal('Send PHP' + sendInputs.amount + ' to @' + sendInputs.username + '?', sendMoney)}>Send</Button>
        buttons = [];
        // moreInfoBtn = <Button onPress={()=>openSingleModal('Details', {info: item, imgPathBegin, photoPassword })} primary text="More Info" />;
        moreInfoBtn = <Button variant="contained" onClick={()=>openSingleModal('Details', {info: item, imgPathBegin, photoPassword })}>More Info</Button>
        userText = <UserLink verified={item.verifieduser} username={item.username} />;
        // tagsText = null;
        bidDiv = null;

        // ratingsDiv = <div>
        //     <Rating size={17} isVerified={false} rating={item.rating} numOfRaters={item.numofraters} />
        //     <Rating size={17} isVerified={true} rating={item.ratingverified} numOfRaters={item.numofratersverified} />    
        //     <span>@{item.companyusername}</span>
        //     <Rating size={17} isVerified={false} rating={item.companyrating} numOfRaters={item.companynumofraters} />
        //     <Rating size={17} isVerified={true} rating={item.companyratingverified} numOfRaters={item.companynumofratersverified} />        
        // </div>

        // ratingsDiv = <UserMoreInfo includeCompany={true} isSosyal={userData.isSosyal} user={item} />
    }


    let siteLabel = '';
    if(item.siteusername != null){
        siteLabel = '@' + item.siteusername;
    }

    let siteDiv = null;
    if(item.siteusername != null){
        siteDiv = <SiteSpan username={item.siteusername} />;
    }

    let mainDiv = <div className='d-flex flex-column'>
        <Link style={{fontSize: 17, fontWeight: 600}} target='_blank' href={item.link}>{itemName}</Link>
        <span style={styles.span}>{itemDescription}</span>
        <span style={styles.span}>{item.link}</span>   
        {siteDiv}
    </div>

    let imgPathBegin = globals.varNames.imgPathBlags;
    let photoPassword = userData.sosyalData.photoPassword;
    let details = <div className='d-flex flex-column'>
        {mainDiv}
        <DetailsDiv info={item} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />
    </div>
    let moreInfoLink = null;
    if(!isUser){
        moreInfoLink = <MoreInfo div={details} />
    }
    
    return (
        // <ListCard style={{padding: 5}} buttons={buttons} onButtonsPressed={onButtonsPressed}>
           
        //     <View>
        //         <Text onPress={()=>Linking.openURL(item.link)} style={{fontSize: 20, fontWeight: 'bold'}}>{item.name}</Text> 
        //         <Text>{item.description}</Text>
        //         <Text style={{color: 'blue'}}>{item.link}</Text>
                
        //         {tagsText}
        //         {userText}
        //         {bidDiv}
                
        //         <Text style={{color: 'blue'}}>{siteLabel}</Text>
                
        //         {moreInfoBtn}
               
        //     </View>
        // </ListCard>
        <Paper elevation={2} className='d-flex flex-column py-2 px-2 mb-2'>
            <div height={90}>
                <div className='d-flex flex-column'>
                    {/* <Link style={{fontSize: 17, fontWeight: 600}} target='_blank' href={item.link}>{itemName}</Link>
                    <span style={styles.span}>{itemDescription}</span>
                    <span style={styles.span}>{item.link}</span> */}
                    {mainDiv}
                    {userText}
                    {/* {siteDiv} */}
                    {bidDiv}
                    {moreInfoLink}
                    
                    
                    {/* {ratingsDiv} */}
                    {/* <div className='d-flex flex-column'>
                        {tagsText}
                        
                    </div> */}
                    
                </div>
            </div>
            <div className={borderTop}>
                <Tab headers={buttons} onChangeTab={onButtonsPressed} moreTabs={[]} />
            </div>
            
            {/* <p>{JSON.stringify(item)}</p>    */}
        </Paper>
    );
}

const styles = {
    span: {
        fontSize: 14
    }   
}