import React, {useState, useContext} from 'react'; 
// import {GlobalContext} from '../Context/GlobalProvider';
// import Tab from '../HtmlComponents/Tab';
import InsidePageWrapper from '../common/HtmlComponents/InsidePageWrapper';
import ShappersDiv from '../Components/ShappersDiv';
import CustomersDiv from '../Components/CustomersDiv';
// import OtherProfilePage from './OtherProfilePage';





export default function ShappersPage() {
    console.log('MESSAGES PAGE');
    // const {userData, showToast} = useContext(GlobalContext);
    const [viewingMsgs, setViewingMsgs] = useState(false);
    const [page, setPage] = useState('Shoppers');
    const [otherUser, setOtherUser] = useState({
        isOpen: false,
        username: ''
    })

    
    let div = <ShappersDiv goToUserPage={(username)=>setOtherUser({isOpen: true, username: username})} />;
    if(page == 'Customers'){
        div = <CustomersDiv/>
        // page = <ChatPage onPressBack={()=>setViewingMsgs(false)} headerID={chatData.id} />;
    }

    // let otherUserPage = null;
    // if(otherUser.isOpen){
    //     otherUserPage = <OtherProfilePage onPressBack={()=>setOtherUser({...otherUser, isOpen: false})} username={otherUser.username} />
    // }
    
    return (
        // <div style={{flex: 1}}>
        //     <Tab onChangeTab={(header)=>setPage(header)} headers={['Shappers', 'Customers']} />
        //     {otherUserPage}
        //     {div}
        // </div>
        <InsidePageWrapper onChangeTab={(tab)=>setPage(tab)} tabs={['Shoppers', 'Customers']} moreTabs={[]}>
            {div}
        </InsidePageWrapper>
    );
}
