
import React, { useState, useContext, useRef } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {List, ListItem, ListItemText, Link} from '@material-ui/core';

import UserImg from './UserImg';
import UserLink from '../HtmlComponents/UserLink';

 
export default function SideNav({closeDrawer}) {
    const {userData, appData, globals, changePage, logout, forceUpdate} = useContext(GlobalContext);
    
    // const exampleImageUri = Image.resolveAssetSource(require('../assets/soy.png')).uri;

    function navigate(page){
        console.log('Pressed')
        closeDrawer();
        changePage(page);
    }

    function changeSite(){
        console.log('Pressed')
        userData.isSosyal = !userData.isSosyal;
        userData.pageNum ++;
        forceUpdate();
    }

    let siteLabel = appData.sosyalName;
    let menus = ['Home', 'Profile', 'Messages', 'Company', 'Search', 'Web Search', 'Search Tags', 'Data', 'Wallet', 'Verify Account'];

    let addtlMenus = ['Shoppers', 'Map', 'List'];
    if(!userData.isShappers){
        addtlMenus.splice(0, 1);    
    }

    if(!userData.isSosyal){
        siteLabel = appData.name;
        // menus.splice(4, 1);
        addtlMenus.forEach((addtlMenu) => {
            // menus.push(addtlMenu);
            menus.splice(2, 0, addtlMenu);
        })
        
    }

    let imgPathBegin = globals.varNames.imgPathBlags;
    let photoPassword = userData.sosyalData.photoPassword;
    let imgURL = userData.sosyalData.imgURL;
    if(!userData.isSosyal){
        imgPathBegin = globals.varNames.imgPath;
        photoPassword = userData.photoPassword;  
        imgURL = userData.imgURL;
    }

    let menuFin = '';
    return (
        <div className='' style={{width:  250}}>
            <div className='d-flex border-bottom pt-2 pb-4 px-2 mt-2'>
                <UserImg imgPathBegin={imgPathBegin} imgURL={imgURL} userID={userData.userID} photoPassword={photoPassword} />
                <div className='d-flex flex-column ml-2'>
                    <UserLink verified={0} username={userData.username} />  
                    <span>{userData.name}</span> 
                    <span>{userData.description}</span> 
                </div>
            </div>
            <List className='px-4'>
                <div className='d-flex flex-column mb-2'>
                    <Link style={{fontWeight: 700, fontSize: 17}} href="#" onClick={(e)=>{e.preventDefault(); changeSite()}}>
                        {siteLabel}
                        
                    </Link>
                </div>
                
                {/* <ListItem className='border-bottom' button onClick={() => changeSite()}>
                    <UserLink username={'ha'} />
                    <ListItemText primary={siteLabel} /> 
                </ListItem> */}
                {menus.map((menu, ndx) => {
                    menuFin = menu;
                    if(menu == 'List' && !userData.isShappers){
                        menuFin = 'Items'
                    }
                    return <ListItem className='px-0' button key={ndx} onClick={() => navigate(menu)}>
                        <span style={styles.menuSpans}>{menuFin}</span>
                        {/* <ListItemText style={{fontSize: 12}} primary={menuFin} />    */}
                    </ListItem>;
                    
                })} 
                <ListItem className='px-0' button onClick={() => logout()}>
                    <span style={styles.menuSpans}>Logout</span>
                    {/* <ListItemText primary='Logout' />  */}
                </ListItem>   
            </List>
            <div className='p-2 d-flex flex-column'>
                <span style={{fontSize: 14, fontWeight: 300, alignSelf: 'center'}}>{appData.sosyalName} {appData.name}</span> 
            </div>
        </div>
        // <View style={{flex: 1, backgroundColor: 'white', paddingTop: 50}}>
        //     <ScrollView>
        //         <View style={{borderBottomWidth: 1, padding: 10, flexDirection: 'row'}}>
        //             <View style={{width: 70, height: 70, borderWidth: 1, marginRight: 10}}></View>    
        //             <Text>{userData.username}</Text>
                    
        //         </View>
        //         <Content>

        //             <Separator>
        //                 <Text>MENU</Text>
        //             </Separator>
        //             <List>
        //                 <ListItem noIndent onPress={() => changeSite()}>
        //                     <Text>{siteLabel}</Text>
        //                 </ListItem>
        //                 {menus.map((menu, ndx) => {
        //                     return <ListItem key={ndx} noIndent onPress={() => navigate(menu)}>
        //                         <Text>{menu}</Text>
        //                     </ListItem>;
                            
        //                 })}
                        
        //                 <ListItem noIndent onPress={() => logout()}>
        //                     <Text>Logout</Text>
        //                 </ListItem>
                           
        //             </List>
        //             <Separator>
        //                 <Text style={{alignSelf: 'center'}}>Shappers Co. 2020</Text>
        //             </Separator>
        //         </Content>
        //         <View>
                    
                
                    
        //         </View>
                
        //     </ScrollView>
        // </View>
        
        
        
    );
}

const styles = {
    menuSpans: {
        fontWeight: 500
    }
}
