import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import {Button, TextField} from '@material-ui/core';

import ItemList from '../List/ItemList';



import PageNav from '../common/Components/PageNav';




export default function ListPage() {
	console.log('LIST')
	const { userData, globals, getTimeOut, apiCall, openSingleModal, showPageNav, closePageNav } = useContext(GlobalContext);
    // const [startPoint, setStartPoint] = useState(0)

    const [itemsData, setItemsData] = useState({
        items: [],
        startPt: 0
    });
    
    // const [startPt, setStartPt] = useState(0);
    useEffect(() => {
        showPageNav(getItems); 
        getItems(0); 
        return closePageNav
    }, [])


    async function getItems(startPt) {
        console.log('GET DATA')
        // getTimeOut(timeOutID, )
        // timeOutID = getTimeOut(timeOutID, async ()=>{
            
        // })
        let response = await apiCall(globals.varNames.apiPath + 'item/getItems', {startPt: startPt})
        console.log('response: ', response);
        if(response.status == 1){
            // items = response.payload;
            setItemsData({...itemsData, items: response.payload});
        }
        
    }
    

    return (
       
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column px-2 py-1'>
            <div className='d-flex mb-1' style={{flexDirection: 'row', justifyContent: 'flex-end'}}>
                <Button variant="contained" onClick={()=>{openSingleModal('New Item', {tags: JSON.stringify([]), description: '', name: '', price: 0, id: 0, quantity: 0, bidAmt: 0})}}>New Item</Button>
            </div>
            
            <div style={{flex: 1, overflow: 'auto'}} className='px-1'>
                
                {itemsData.items.map((item, idx) => 
                    <ItemList key={item.id} isUser={true} item = {item} />
                )} 
            </div>
            {/* <PageNav isForward={true} getItems={getItems} startPt={startPt} setStartPt={setStartPt} /> */}
        </div>
    );
}