import React, {useState, useContext} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import MessageHeaders from '../Components/MessageHeaders';
import Messages from '../Components/Messages';

// import { View } from 'react-native';




export default function MessagesPage() {
    console.log('MESSAGES PAGE');
    const {userData} = useContext(GlobalContext);
    const [viewingMsgs, setViewingMsgs] = useState(false);
    const [chatData, setChatData] = useState(null);
    let page = <MessageHeaders onPressHeader={(msgHeader)=>{setViewingMsgs(true); setChatData(msgHeader)}} />;
    if(viewingMsgs){
        page = <Messages onPressBack={()=>setViewingMsgs(false)} msgHeader={chatData} />;
    }

    
    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column px-2 pt-1 pb-3'>
            {/* <MessageHeaders onPressHeader={(msgHeader)=>{setViewingMsgs(true); setChatData(msgHeader)}} /> */}
            {page}
            
        </div>
    );
}


