import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Link, Card, Paper} from '@material-ui/core';

import UserInfoDiv from '../HtmlComponents/UserInfoDiv';
import UserLink from '../HtmlComponents/UserLink';
import UserImg from '../Components/UserImg';
import Tab from '../HtmlComponents/Tab';
import MoreInfo from '../HtmlComponents/MoreInfo';
import UserMoreInfo from '../HtmlComponents/UserMoreInfo';

export default function MemberList({ member, fromUser }) {
    console.log('ITEM LIST')
    const { userData, globals, createUserInfo, getApiPath, apiCall, openYesNoModal, showToast } = useContext(GlobalContext);
    
    
    async function removeUser(){
        let userID = 0;
        // let userID = member.userid;
        if(member.userid != null){
            userID = member.userid;
        }else{
            userID = member.id;
        }
        let response = await apiCall(getApiPath() + 'company/removeUser', {userID: userID});
        console.log('response: ', response);
        if(response.status == 1){
            showToast('User removed successfully.', 3, false);
        }
    }
    

    // function onButtonsPressed(button){
    //     console.log('button: ', button);
    //     if(button == 'Accept'){
    //         acceptUser();
    //     }else if(button == 'Message'){
    //         openMsgModal(customer.username);    
    //     }else if(button = 'Cancel'){
    //         openYesNoModal('Are you sure?', cancelHire);
    //         // cancelHire();
    //     }
    // }
    let btns = [];
    if(fromUser){
        btns = ['Remove'];
    }

    let photoPassword = userData.photoPassword;
    let imgPath = globals.varNames.imgPath;
    if(userData.isSosyal){
        photoPassword = userData.sosyalData.photoPassword;
        imgPath = globals.varNames.imgPathBlags;
    }

    let url = 'http';
    
    if(member.imgurl != ''){
        url = imgPath + member.imgurl + '/' + userData.userID + '/' + photoPassword
        // url = profileData.profileImgUrl;
    }
    let userInfo = createUserInfo(member, false);

    return (
        <Paper elevation={2} className='border-bottom p-2 mb-2'>
            <UserInfoDiv imgPathBegin={imgPath} userInfo={userInfo} photoPassword={photoPassword} />
            {/* <div className='d-flex'>
                <UserImg imgPathBegin={imgPath} imgURL={member.imgurl} userID={userData.userID} photoPassword={photoPassword} />
                
                <div className='ml-2 d-flex flex-column'>
                    
                    <UserLink verified={member.verifieduser} username={member.username} />
                    <span>{member.name}</span>
                    <span>{member.description}</span>
                    <UserMoreInfo isSosyal={userData.isSosyal} includeCompany={false} user={member} />
                    <MoreInfo>
                        <span>{member.description}</span>
                        <Rating size={17} isVerified={false} rating={member.rating} numOfRaters={member.numofraters} />
                        <Rating size={17} isVerified={false} rating={member.ratingverified} numOfRaters={member.numofratersverified} />
                    </MoreInfo> 
                    
                    
                </div>
                
            </div> */}

            <Tab headers={btns} onChangeTab={(tab)=>openYesNoModal('Remove user?', removeUser)} />
            
        </Paper>
    );
}