
import React, { useState, useContext, useRef } from 'react'; 
// import { View, Image } from 'react-native';
import {GlobalContext} from '../../Context/GlobalProvider';
import {Button, TextField, Link} from '@material-ui/core';
// import { Button, COLOR } from 'react-native-material-ui';


// import { Text } from 'native-base';
// import { Rating } from 'react-native-elements';
import Rating from '../HtmlComponents/Rating';
import UserImg from '../Components/UserImg';
import UserLink from '../HtmlComponents/UserLink';
import CompanyDiv from '../HtmlComponents/CompanyDiv';
import UserInfoDiv from '../HtmlComponents/UserInfoDiv';
import CheckIcon from '@material-ui/icons/Check';
import WrongIcon from '@material-ui/icons/Close';
import { red, green } from '@material-ui/core/colors';

export default function ProfileDiv({profileData, isUser}) {
    console.log('profile data: ', profileData);
    const {userData, globals, apiCall, changePage, getApiPath, forceUpdate, openSingleModal, createUserInfo} = useContext(GlobalContext);
    let followed = false;
    let companyJoinLabel = 'Join Company';
    async function follow(){
        console.log('send money');
        // userToFollow: req.body.userToFollow,
        // type: req.body.type
        let type = 'follow';
        if(followed){
            type = 'unfollow';
        }
        
        let data = {
            userToFollow: profileData.userID,
            type: type
        }
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.blagsApiPath + 'user/follow', data);
        console.log('response: ', response);
        if(response.status == 1){
            
        }
    }

    async function join(){
        console.log('send money');
        // userToFollow: req.body.userToFollow,
        // type: req.body.type
        if(companyJoinLabel == 'Join Company'){
            joinCompany();
        }else if(companyJoinLabel == 'Cancel Request'){
            cancelRequest();
        }else{
            leaveCompany();
        }
        
    }

    async function joinCompany(){
        console.log('send money');
        // userToFollow: req.body.userToFollow,
        // type: req.body.type
        let data = {
            coID: profileData.userID,
            
        }

        
        // console.log('options: ', options);
        let response = await apiCall(getApiPath() + 'company/joinCompany', data);
        console.log('response: ', response);
        if(response.status == 1){
            userData.joinReqs.push(profileData.userID);  
            forceUpdate();    
        }
    }

    async function cancelRequest(){
        console.log('send money');
        // userToFollow: req.body.userToFollow,
        // type: req.body.type
        let data = {
            coID: profileData.userID,
            
        }

        
        // console.log('options: ', options);
        let response = await apiCall(getApiPath() + 'company/cancelRequest', data);
        console.log('response: ', response);
        if(response.status == 1){
            let ndxToSplice = 0;
            userData.joinReqs.forEach((joinReq, ndx) => {
                if(joinReq == profileData.userID){
                    ndxToSplice = ndx;
                }   
            }) 
            
            userData.joinReqs.splice(ndxToSplice, 1);
            forceUpdate();
        }
    }

    async function leaveCompany(){
        console.log('send money');
        
        let data = {
            coID: profileData.userID,
            
        }
        // console.log('options: ', options);
        let response = await apiCall(getApiPath() + 'company/leaveCompany', data);
        console.log('response: ', response);
        if(response.status == 1){
            userData.companyID = 0;   
            forceUpdate();
        }
    }

    
    let url = 'http';
    if(profileData.imgURL != ''){
        url = profileData.imgPath + profileData.imgURL + '/' + userData.userID + '/' + profileData.photoPassword;
        
    }
    
    
    console.log('img url: ', profileData.profileImgUrl);
    console.log('img url2: ', url);

    let bidName = 'User';
    if(userData.isSosyal){
        bidName = 'User Sosyal';
    }
    let verifiedLink = <Link className='' href="#" onClick={(e)=>{e.preventDefault(); openSingleModal('Verified Info', profileData.userID)}}>View</Link>;
    
    let verified = <CheckIcon className='' style={{color: green[500], fontSize: 20}} />;
    if(profileData.verifiedUser == 0){
        verified = <WrongIcon className='' style={{color: red[500], fontSize: 20}} />;
        verifiedLink = null;
    }
    let btns = <div className='d-flex mt-2' style={{justifyContent: 'flex-start'}}>
        {/* <Button style={{ container: {marginBottom: 20} }} raised primary text='Follow' onPress={follow} /> */}
        <Button className='mr-1' variant="contained" onClick={()=>changePage('Edit Profile')}>Edit</Button>
        <Button variant="contained" onClick={()=>openSingleModal('Bid', {bidAmt: profileData.bidAmt, itemName: bidName, id: profileData.userID, bidBal: profileData.bidBal})}>Bid</Button>
        {/* <Button raised primary text='Edit' onPress={()=>changePage('Edit Profile')} />
        <Button raised primary text='Bid' onPress={()=>openSingleModal('Bid', {bidAmt: profileData.bidAmt, itemName: bidName, id: profileData.userID})} /> */}
    </div>;


    if(!isUser){
        let label = 'Follow';
        profileData.followedUsers.forEach((followedUser)=>{
            if(followedUser == profileData.userID){
                label = 'Unfollow';
                followed = true;
            }
        })
        console.log('join requests: ', profileData.joinReqs);
        profileData.joinReqs.forEach((joinReq) => {
            if(joinReq == profileData.userID){
                companyJoinLabel = 'Cancel Request';
            }
        })

        let coID = userData.companyID;
        if(userData.isSosyal){
            coID = userData.sosyalData.companyID;
        }
        if(coID == profileData.userID){
            companyJoinLabel = 'Leave Company';
        }

        btns = <div className='d-flex mt-2'>
            <Button className='mr-1' variant="contained" onClick={follow}>{label}</Button>
            {/* <Button style={{ container: {marginBottom: 20} }} raised primary text={label} onPress={follow} /> */}
            <Button className='mr-1' variant="contained" onClick={join}>{companyJoinLabel}</Button>
            {/* <Button raised primary text={companyJoinLabel} onPress={join} /> */}
        </div>;
    }

    let companyDiv = null;
    let companyInfo = null;
    if(profileData.company.username != null){

        // username: userInfo.companyusername,
        // rate: userInfo.companyrating,
        // numOfRaters: userInfo.companynumofraters,
        // rateVerified: userInfo.companyratingverified,
        // numOfRatersVerified: userInfo.companynumofratersverified,
        // name: userInfo.companyname,
        // imgURL: userInfo.companyimgurl,
        let company = profileData.company;

        let userInfo = {
            companyusername: company.username,
            companyname: company.name,
            companyimgurl: company.imgURL,
            companynumofratersverified: company.numOfRatersVerified,
            companynumofraters: company.numOfRaters,
            companyrating: company.rating,
            companyratingverified: company.ratingVerified,
        }

        // let companyInfo = createUserInfo(userInfo, true);
        // companyDiv = <CompanyDiv isSosyal={userData.isSosyal} user={userInfo} />
        companyDiv = <div className='mt-2'>
            <h6>Company</h6>
            <UserInfoDiv userInfo={company} imgPathBegin={profileData.imgPath} photoPassword={profileData.photoPassword} />
        </div>
        // companyDiv = <div className='d-flex flex-column mt-3'>
        //     <h6>Company</h6>
        //     <div className='d-flex'>
        //         {/* <UserImg size={30} imgPathBegin={imgPathBegin} imgURL={user.companyimgurl} userID={userData.userID} photoPassword={photoPassword} /> */}
        //         <div className='d-flex flex-column ml-2'>
        //             <span>{profileData.company.name}</span>
        //             <UserLink verified={0} username={profileData.company.username} />
        //             {/* <Rating size={17} isVerified={false} rating={user.companyrating} numOfRaters={user.companynumofraters} />
        //             <Rating size={17} isVerified={true} rating={user.companyratingverified} numOfRaters={user.companynumofratersverified} /> */}
        //         </div>
        //     </div>
            
        // </div> 
    }

    return (
        // <View style={{flex: 1}}>
        //     <View style={{marginBottom: 20}}>
                
        //         <Image style={{width: 100, height: 100, backgroundColor: 'blue', alignSelf: 'center'}} source={{uri: url}}/>
                
        //         <Text>{profileData.username}</Text>
        //         <Text>{profileData.name}</Text>
        //         <Text>{profileData.description}</Text>
        //         <Text>{profileData.country}</Text>
                

        //         <Rating size={15} color={COLOR.yellow500} rating={profileData.rate} numOfRaters={profileData.numOfRaters} />
                
        //     </View>
        //     {btns}
            
            
            
        // </View>
        // profileData.imgPath + profileData.imgURL + '/' + profileData.userID + '/' + profileData.photoPassword;
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column p-2'>
            <div style={{justifyContent: 'center'}} className='d-flex'>
                <UserImg size={100} imgPathBegin={profileData.imgPath} imgURL={profileData.imgURL} userID={userData.userID} photoPassword={profileData.photoPassword} />
            </div>
            
            <div style={{flex: 1}} className='d-flex flex-column ml-2 px-2'>
                <UserLink username={profileData.username} />
                <span>{profileData.name}</span>
                <span>{profileData.description}</span>
                <span>{profileData.country}</span>
                <span>Verified: {verified} {verifiedLink}</span>
                {/* <Link className='' href="#" onClick={(e)=>{e.preventDefault(); openSingleModal('Verified Info', profileData.userID)}}>View</Link>     */}
                <Rating size={20} isVerified={false} rating={profileData.rate} numOfRaters={profileData.numOfRaters} />
                <Rating size={20} isVerified={true} rating={profileData.rateVerified} numOfRaters={profileData.numOfRatersVerified} />
                {/* <UserLink username={profileData.company.username} />
                <span>@{profileData.company.username}</span>
                <Rating size={20} isVerified={false} rating={profileData.company.rating / 100} numOfRaters={profileData.company.numOfRaters} />
                <Rating size={20} isVerified={true} rating={profileData.company.ratingVerified / 100} numOfRaters={profileData.company.numOfRatersVerified} /> */}
                {btns}
                {companyDiv}
            </div>
            
        </div>
    );
}


