import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import Deposit from '../Components/Deposit';
import Withdraw from '../Components/Withdraw';
import SendMoney from '../Components/SendMoney';
import Transactions from '../Components/Transactions';

import InsidePageWrapper from '../HtmlComponents/InsidePageWrapper';

export default function WalletPage() {
    const {userData, globals, apiCall, countries} = useContext(GlobalContext);
    
    let balance = userData.balance;
    if(userData.isSosyal){
        balance = userData.sosyalData.balance;
    }
    const [tab, setTab] = useState('Profile');

    
    
    let page = <SendMoney />;
    if(tab == 'Deposit'){
        // reviews, startPt, setStartPt, getReviews
        page = <Deposit />;
    }else if(tab == 'Withdraw'){
        page = <Withdraw/>
    }else if(tab == 'Transactions'){
        page = <Transactions/>
    }

    let tabs = ['Send', 'Transactions'];
    let sosyalBalLabel = null;
    let moreTabs = [];
    if(!userData.isSosyal){
        tabs.push('More');
        moreTabs = ['Deposit', 'Withdraw'];
        // tabs.splice(1, 0, 'Deposit');
        // tabs.splice(2, 0, 'Withdraw');
        if(tab == 'Deposit' || tab == 'Withdraw'){
            sosyalBalLabel = <h5>Sosyal Balance: {userData.sosyalData.balance / 100}</h5>;
        }
        
    }

    
    // if(tab == '')
    return (
        // <View style={{flex: 1}}>
        //     <Tab headers={['Send', 'Deposit', 'More']} moreTabs={['Withdraw', 'Txs']} onChangeTab={(tab)=>setTab(tab)}></Tab>
        //     <Text style={{fontSize: 20, margin: 5}}>Balance: {balance/100}</Text>
        //     {page}
        // </View>

        <InsidePageWrapper onChangeTab={(tab)=>setTab(tab)} tabs={tabs} moreTabs={moreTabs}>
            <div className='px-3 mt-3'>
                <h5>Balance: {balance / 100}</h5>
                {sosyalBalLabel}
            </div>
            
            {page}
        </InsidePageWrapper>
    );
}

