import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';

import {Button, Tabs, Tab, Paper} from '@material-ui/core';
// import { View } from 'react-native';

// import { Container, Header, Content, Text, List, ListItem, Icon, Item } from 'native-base';
// import { COLOR, Button } from 'react-native-material-ui';
// import Button from './Button';



export default function InsidePageWrapper({tabs, onChangeTab, moreTabs, children}) {
    console.log('AppModal');
    const {userData, globals, openMoreOptionsModal, forceUpdate, appModalData} = useContext(GlobalContext);
    const [tabVal, setTabVal] = useState(0);
    // const [isOpeningMoreTabs, setIsOpeningMoreTabs] = useState(false);
    function handleChange(e, newValue){
        console.log('value: ', newValue);
        let tab = tabs[newValue];
        if(tab != 'More'){
            onChangeTab(tabs[newValue]);
            setTabVal(newValue);
        }else{
            openMoreOptionsModal(moreTabs, openMoreTab)    
        }
        
    }

    function openMoreTab(moreTab){
        // e.preventDefault();
        onChangeTab(moreTab);
        // setIsOpeningMoreTabs(false);      
    }

    return (
        
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            <Paper square>
                <Tabs
                    value={tabVal}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleChange}
                    aria-label="disabled tabs example"
                >
                    
                    {tabs.map((tab, ndx) => {
                        return <Tab key={ndx} label={tab} />    
                        
                    })}
                </Tabs>
            </Paper>
            <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
                {children}
            </div>
            
            
        </div>
       
    );
}