import React, { useState, useContext, useRef } from 'react'; 
import {GlobalContext} from '../common/Context/GlobalProvider';
// import {CommonContext} from '../common/Context/GlobalProvider';
import ProfilePage from '../common/Views/ProfilePage';
import MessagesPage from '../common/Views/MessagesPage';
import ListPage from '../Views/ListPage';
import DataPage from '../common/Views/DataPage';
import MapPage from '../Views/MapPage';
import EditUserPage from '../common/Views/EditUserPage';
import CompanyPage from '../common/Views/CompanyPage';
import WalletPage from '../common/Views/WalletPage';
import ShappersPage from '../Views/ShappersPage';
import HomePage from '../common/Views/HomePage';
import SearchPage from '../common/Views/SearchPage';
import SerchPage from '../common/Views/SerchPage';

import VerifyAccountPage from '../common/Views/VerifyAccountPage';
import SearchTagsPage from '../common/Views/SearchTagsPage';
import SideNav from '../common/Components/SideNav';
import AdsDiv from '../common/Components/AdsDiv';
import PageNav from '../common/Components/PageNav';


import {Drawer, TextField, AppBar, Toolbar, Button, IconButton, Typography} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';









export default function InsidePages() {
    // const {userData, globals, appData, forceUpdate, toastData, singleModalData, otherPageData} = useContext(CommonContext);
    const {userData, pageNavData, globals, appData, forceUpdate} = useContext(GlobalContext);
    const [toggleMenu, setToggleMenu] = useState(false);

    // const drawerRef = useRef(null);

    // function openDrawer(){
    //     // console.log('drawer: ', drawerRef);
    //     drawerRef.current._root.open();
    // }

    function closeDrawer(){
        // console.log('drawer: ', drawerRef);
        setToggleMenu(false);
    }

    function toggleDrawer(){
        // console.log('drawer: ', drawerRef);
        setToggleMenu(!toggleMenu)
    }
  
    let page = <ProfilePage/>, title = 'Profile'
    
    if(userData.currentPage.back === 'Messages'){
        page = <MessagesPage /> 
        title = 'Messages'
    }else if(userData.currentPage.back === 'List'){
        page = <ListPage />
        title = 'List';
        if(!userData.isShappers){
            title = 'Items';
        }
    }else if(userData.currentPage.back === 'Data'){
        page = <DataPage />
        title = 'Data'
    }else if(userData.currentPage.back === 'Map'){
        page = <MapPage />
        title = 'Map'
    }else if(userData.currentPage.back === 'Wallet'){
        page = <WalletPage />
        title = 'Wallet'
    }else if(userData.currentPage.back === 'Edit Profile'){
        page = <EditUserPage />
        title = 'Edit Profile';
    }else if(userData.currentPage.back === 'Company'){
        page = <CompanyPage />
        title = 'Company';
    }else if(userData.currentPage.back === 'Home'){
        page = <HomePage />
        title = 'Home';
    }else if(userData.currentPage.back === 'Search'){
        page = <SearchPage />
        title = 'Search';
    }else if(userData.currentPage.back === 'Web Search'){
        page = <SerchPage />
        title = 'Serch';
    }else if(userData.currentPage.back === 'Search Tags'){
        page = <SearchTagsPage />
        title = 'Search Tags';
    }else if(userData.currentPage.back === 'Verify Account'){
        page = <VerifyAccountPage />
        title = 'Verify Account';
    }else if(userData.currentPage.back === 'Shoppers'){
        page = <ShappersPage />
        title = 'Shoppers';
    }

    let sideNav = null;
    if(userData.windowSize == 'lg'){
        sideNav = <SideNav closeDrawer={closeDrawer} />
    }
    
    let pageWidth = {width: 350};
    if(userData.windowSize == 'sm' || userData.windowSize == 'xm'){
        pageWidth = {flex: 1}
    }

    // let pageNav = null;
    // if(userData.showingPageNav){
    //     pageNav = <PageNav key={pageNavData.key} isForward={true} onChangeStartPt={(startPt)=>pageNavData.callbackFunc(startPt)} />
    // }

    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            <AppBar style={{zIndex: 1}} position="static">
                <Toolbar>
                    <IconButton onClick={toggleDrawer} edge="start" color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    
                </Toolbar>
            </AppBar>
            <Drawer anchor='left' open={toggleMenu} onClose={()=>toggleDrawer()}>
               
                <SideNav closeDrawer={closeDrawer} />
            </Drawer>
            <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
                {/* <AdsDiv isRight={false} />
                {sideNav} */}
                <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column pt-1'>
                    {page} 
                </div>
                {/* {pageNav} */}
                {/* <AdsDiv isRight={true} />      */}
                
                
                
            </div>
            
            
        </div>
        
        
    );
}
