
import React, {useState, useContext, forwardRef, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Button, TextField} from '@material-ui/core';
import Note from '../HtmlComponents/Note';

export default function SendMoney() {
    const {userData, globals, openHintsModal, consumptionNote, convertEmptyStr, getApiPath, apiCall, openYesNoModal, showToast} = useContext(GlobalContext);
    const [sendInputs, setSendInputs] = useState({
        amount: 0,
        username: ''
    });
    const [isTyping, setIsTyping] = useState(false);

    // function onChangeSearchInput(e){
    //     console.log('on search inputs pressed');
    //     setIsTyping(true);
    //     // setSearchInputs({s, searchValue: text})
    //     setSendInputs({...sendInputs, username: e.target.value})
    // }

    async function sendMoney(){
        console.log('send money');
        let usernameCheck = convertEmptyStr(sendInputs.username);
        if(usernameCheck == ''){
            
            let msg = 'Username cannot be empty.';
            
            
            showToast(msg, 3, true);
            // Toast.show(toastObj);
            return;
        }

        var data = {
            amount: sendInputs.amount,
            username: sendInputs.username
        }
        // console.log('options: ', options);
        let response = await apiCall(getApiPath() + 'pay/sendMoney', data);
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Money sent successfully.', 3, false);
        }
    }
    
    function onSearchInputPressed(){
        console.log('on search inputs pressed');
        // if(!isTyping){
        //     setIsTyping(true);         
        //     // getHints()
        // }

        openHintsModal(sendInputs.username, true, (hint)=>setSendInputs({...sendInputs, username: hint}));
        
    }

    // function onPressHint(hint){
    //     console.log('get hints');
        
    //     // setSearchInputVal(hint);
        
    //     setIsTyping(false);
    //     setSendInputs({...sendInputs, username: hint})
    //     // setSearchInputs({...searchInputs, searchValue: hint});
        
        
    // }

    // let searchHints = null;
    // if(isTyping){
    //     searchHints = <SearchHints onPressClosed={()=>setIsTyping(false)} hintVal={sendInputs.username} onPressHint={onPressHint} isUser={true} />;
    // }
    // console.log('user inputs: ', userInputs);
    return (
        <div className='d-flex flex-column py-2 px-5'>
            
            <TextField  onChange={(e)=>setSendInputs({...sendInputs, amount: e.target.value})} id="standard-basic" label="Amount" />
            <div style={{position: 'relative'}} className='d-flex flex-column'>
                <TextField value={sendInputs.username} onClick={onSearchInputPressed} autoComplete='off' id="standard-basic" label="Username" />
                {/* {searchHints} */}
            </div>
            
            
            <Button className='mt-2' variant="contained" onClick={()=>openYesNoModal('Send PHP' + sendInputs.amount + ' to @' + sendInputs.username + '?', sendMoney)}>Send</Button>
            <Note note={consumptionNote()} />
        </div>
        
    );
}