import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';



import PageNav from './PageNav';
import JoinRequestList from '../List/JoinRequestList';





export default function JoinRequests({coID}) {
    console.log('JOIN REQUESTS');
    const {userData, getApiPath, apiCall, showPageNav, closePageNav} = useContext(GlobalContext);
    // const [startPt, setStartPt] = useState(0);
    const [joinReqs, setJoinReqs] = useState([]);
    
    
    // console.log('msg headers:', messageData.msgHeaders);

    async function getRequests(startPt){
        let data = {
            coID: coID,
            startPoint: startPt
        }
        let response = await apiCall(getApiPath() + 'company/getJoinRequests', data);
        console.log('response: ', response);
        if(response.status == 1){
            setJoinReqs(response.payload);
            // setMsgHeaders(response.payload);
            // messageData.msgHeaders = response.payload;
            // console.log('message headers: ', messageData.msgHeaders);
            // forceUpdate();
        }     
    }

    // useEffect(() => {
    //     // Update the document title using the browser API
    //     getRequests();
    // }, []);
    useEffect(() => {
        // Update the document title using the browser API
        showPageNav(getRequests);
        getRequests(0);

        return closePageNav
    }, []);
    
    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column px-2'>
            
            
            <div style={{flex: 1, overflow: 'auto'}} className='mt-2 px-1'>
                {joinReqs.map((joinReq) => {
                    return <JoinRequestList joinRequest={joinReq} key={joinReq.id}/>
                    
                    
                })}
            </div>
            
            
           

            
            {/* <div style={{alignItems: 'flex-end'}} className='d-flex flex-column px-3'>
                <PageNav startPt={startPt} getItems={getRequests} setStartPt={setStartPt} isForward={true}/>
            </div> */}
        </div>
    );
}