import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';

import { Alert } from '@material-ui/lab';

export default function CustomToast({closeToast, message, duration, isError}) {
    console.log('AppModal');
    // const {userData, globals, getUserData, forceUpdate, toastData} = useContext(GlobalContext);
    setTimeout(()=>{
        closeToast();
    }, duration * 1000);

    function handleClose(){
        closeToast();
    }

    let severity = 'success';
    if(isError){
        severity = 'error';
    }
    // let text = <View style={{flexDirection: 'row'}}>
    //     <Text>{message}</Text>
    //     {/* <View style={{flexDirection: 'row'}}>
    //         <Button onPress={closeToast} text="Close"/>
    //     </View> */}
        
    // </View>;
    // let text = <Text>{message}<Button onPress={closeToast} text="Close"/></Text>;
    // <Button style={{container: {marginVertical: 10}}} raised onPress={register} primary text="Create" />
    return (
        
        // <Snackbar onRequestClose={closeToast} timeout={duration} visible={true} message={text} />
        // <ModalBase styleView={{}} style={{position: 'absolute', bottom: 50, right: 5, left: 5, zIndex: 3, elevation: 2}}>
        //     <View style={{backgroundColor: 'black', padding: 5}}>
        //         <Text style={{fontSize: 12, color: 'white', marginBottom: 10}}>{message}</Text>
        //         <View style={{flexDirection: 'row', justifyContent: 'flex-end'}}>
        //             <Button primary onPress={closeToast} text="Close"/>
        //         </View>
                
        //     </View>  
        // </ModalBase>
        <div style={{position: 'absolute', backgroundColor: 'white', right: 5, left: 5, bottom: 50, zIndex: 5}}>
            <Alert variant="filled" onClose={handleClose} severity={severity}>
                {message}
            </Alert>
        </div>
                 
        
        
       
    );
}