import React, {useState, useContext, forwardRef, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Button, TextField} from '@material-ui/core';
import Note from '../HtmlComponents/Note';

export default function Withdraw() {
    const {userData, globals, changePage, appData, consumptionNote, apiCall, getApiPath, showToast} = useContext(GlobalContext);
    const [withdrawInputs, setWithdrawInputs] = useState({
        amount: 0,
        address: '',
        
    });
    
    async function withdraw(){
        console.log('send money');
        let data = {
            amount: withdrawInputs.amount,
            // address: withdrawInputs.address
        }

        // let apiPath = globals.varNames.apiPath + 'pay/';
        // if(userData.isSosyal){
        //     apiPath = globals.varNames.blagsApiPath + 'walet/';
        // }
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.apiPath + 'pay/withdraw', data);
        console.log('response: ', response);
        if(response.status == 1){
            showToast(response.message, 3);    
        }
    }
    console.log('user data token: ', userData.token);
    
    // console.log('user inputs: ', userInputs);
    return (
        <div className='d-flex flex-column px-5'>
            
            
           
            <p>Transfer cash to your Sosyal account.</p>
            <TextField onChange={(e)=>setWithdrawInputs({...withdrawInputs, amount: e.target.value})} id="standard-basic" label="Amount" />
            
            <Button className='mt-2' variant="contained" onClick={withdraw}>Withdraw</Button>
            <Note note={consumptionNote()} />
        </div>
        
    );
}