import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';


import AppModal from '../HtmlComponents/AppModal';
import {Button, Link} from '@material-ui/core';


export default function SearchOptionsModal({closeModal}) {

    const mystyle = {
        divStyle: {
            
            color: "white",
            backgroundColor: "DodgerBlue",
            padding: "10px",
            fontFamily: "Arial"
        },
        
    };

    console.log('Search options modal');
    const {userData, globals, getUserData, appData, singleModalData} = useContext(GlobalContext);
    console.log('single modal:', singleModalData);
    const {searchOptions, setSearchOptions, isRepliesOption} = singleModalData.data;

    let isTagsTab = 0;
    let isReplies = 0;
    let allSite = false;
    
    if(!isRepliesOption){
        isTagsTab = searchOptions.isTagsTab;
        isReplies = searchOptions.isReplies;
        allSite = searchOptions.allSite;
    }

    
    const [options, setOptions] = useState({
        isAsc: searchOptions.isAsc,
        isReplies: isReplies,
        sortType: searchOptions.sortType,
        isTagsTab: isTagsTab,
        allSite: allSite  
    })
    
    
    

    function onClickDone(){
        console.log('on click done options', options);
        setSearchOptions(options);
        // getPosts();
        closeModal();
    }

    function changeSortType(e){
        e.preventDefault();
        let type = 'Last Replied';
        if(options.sortType == 'Last Replied'){
            type = 'Last Update';
        }

        setOptions({...options, sortType: type})
    }
    
    let orderLabel = 'Descending';
    if(options.isAsc){
        orderLabel = 'Ascending';
    }

    let isRepliesLabel = 'Post';
    if(options.isReplies){
        isRepliesLabel = 'Replies';   
    }

    let tagLabel = 'User';
    if(options.isTagsTab){
        tagLabel = 'Tags';   
    }

    let allSiteLabel = appData.name;
    if(userData.isSosyal){
        allSiteLabel = appData.sosyalName;
    }
    
    if(options.allSite){
        allSiteLabel = 'All';
    }

    let siteBtn = null;
    let otherBtns = null;
    if(!isRepliesOption){
        // btnIsReplies = <Button  raised primary text={isRepliesLabel} onPress={()=>{setOptions({...options, isReplies: !options.isReplies})}} />;
        // btnIsTagsTab = <Button style={{container: {marginRight: 5}}} raised primary text={tagLabel} onPress={()=>{setOptions({...options, isTagsTab: !options.isTagsTab})}} />;
        // siteBtn = <Button style={{container: {marginRight: 5}}} raised primary text={allSiteLabel} onPress={()=>{setOptions({...options, allSite: !options.allSite})}} />;
        // siteBtn = <Button variant="contained" onClick={()=>setOptions({...options, allSite: !options.allSite})}>{allSiteLabel}</Button>;
        siteBtn = <Link style={{alignSelf: 'flex-end', fontWeight: 500, fontSize: 18}} href="#" onClick={(e)=>{e.preventDefault(); setOptions({...options, allSite: !options.allSite})}}>
                    {allSiteLabel}
                </Link>
        // otherBtns = <View style={{flexDirection: 'row', marginBottom: 5}}>
        //     <Text style={{fontWeight: 'bold', marginRight: 5}}>Post Type    :</Text>
        //     <Button style={{container: {marginRight: 5}}} raised primary text={tagLabel} onPress={()=>{setOptions({...options, isTagsTab: !options.isTagsTab})}} />
        //     <Button  raised primary text={isRepliesLabel} onPress={()=>{setOptions({...options, isReplies: !options.isReplies})}} />
        // </View> 
        otherBtns = <div style={{justifyContent: 'space-between'}} className='d-flex flex-row mt-2'>

            <span style={styles.span}>Post Type: </span>
            {/* <Button variant="contained" onClick={()=>setOptions({...options, isTagsTab: !options.isTagsTab})}>{tagLabel}</Button> */}
            <div className='d-flex flex-row'>
                <Link style={styles.link} className='border-right px-3' href="#" onClick={(e)=>{e.preventDefault(); setOptions({...options, isTagsTab: !options.isTagsTab})}}>
                    {tagLabel}
                </Link>
                {/* <Button variant="contained" onClick={()=>setOptions({...options, isReplies: !options.isReplies})}>{isRepliesLabel}</Button> */}
                <Link style={styles.link} href="#" className='pl-3' onClick={(e)=>{e.preventDefault(); setOptions({...options, isReplies: !options.isReplies})}}>
                    {isRepliesLabel}
                </Link>
            </div>
            
        </div>;
    }

    return (
        
        <AppModal buttons={['Done']} onButtonsPressed={onClickDone} closeModal={closeModal}>
            
            {/* <View style={{flex: 1, paddingVertical: 30}}>
                <ScrollView>
                    
                    
                    {siteBtn}
                    
                    <View style={{flexDirection: 'row', marginBottom: 5}}>
                        <Text style={{fontWeight: 'bold', marginRight: 5}}>Sort Order   :</Text>
                        <Button raised primary text={orderLabel} onPress={()=>{setOptions({...options, isAsc: !options.isAsc})}} />
                    </View>
                   
                    {otherBtns}
                    <View style={{flexDirection: 'row'}}>
                        <Text style={{fontWeight: 'bold', marginRight: 5}}>Order By      :</Text>
                        <Button raised primary text={options.sortType} onPress={changeSortType} />
                    </View>
                   
                    
                    
                </ScrollView>    
                
            </View> */}
            <div className='d-flex flex-column'>
                {siteBtn}
                <div style={{justifyContent: 'space-between'}} className='d-flex mt-3'>
                    <span style={styles.span}>Sort Order: </span>
                    {/* <Button variant="contained" onClick={()=>setOptions({...options, isAsc: !options.isAsc})}>{orderLabel}</Button> */}
                    <Link style={styles.link} href="#" onClick={(e)=>{e.preventDefault(); setOptions({...options, isAsc: !options.isAsc})}}>
                        {orderLabel}
                    </Link>
                </div>  
                {/* <View style={{flexDirection: 'row', marginBottom: 5}}>
                    <Text style={{fontWeight: 'bold', marginRight: 5}}>Sort Order   :</Text>
                    <Button raised primary text={orderLabel} onPress={()=>{setOptions({...options, isAsc: !options.isAsc})}} />
                </View> */}
                
                {otherBtns}
                <div style={{justifyContent: 'space-between'}} className='d-flex flex-row mt-2'>
                    <span style={styles.span}>Order By: </span>
                    {/* <Button variant="contained" onClick={changeSortType}>{options.sortType}</Button> */}
                    <Link style={styles.link} href="#" onClick={changeSortType}>
                        {options.sortType}
                    </Link>
                </div>
                {/* <View style={{flexDirection: 'row'}}>
                    <Text style={{fontWeight: 'bold', marginRight: 5}}>Order By      :</Text>
                    <Button raised primary text={options.sortType} onPress={changeSortType} />
                </View>   */}
                
            </div>
        </AppModal>   
    );
}

const styles = {
    link: {
        fontWeight: 500
    },
    span: {
        fontWeight: 500  
    }
}