import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';


import ItemList from '../List/ItemList';

// import PageNav from '../common/Components/PageNav';

export default function OtherUserList({userID}) {
    console.log('LIST')
	const { userData, globals, apiCall, showPageNav, closePageNav, funcDelayer } = useContext(GlobalContext);
    // const [startPoint, setStartPoint] = useState(0)
    let timeOutID = 0;
    
    // let timeOutID = 0;
    const [itemsData, setItemsData] = useState({
        items: [],
        startPt: 0
    });

    const [toastData, setToastData] = useState({
        isOpen: false,
        message: '',
        duration: 0
    });

    
    
    const [startPt, setStartPt] = useState(0);
    useEffect(() => { 
        // timeOutID = funcDelayer(timeOutID, getItems, 1000);
        // getItems(); 
        showPageNav(getItems); 
        getItems(0); 
        return closePageNav
    }, [])


    async function getItems(startPt) {
        console.log('GET DATA')
        // getTimeOut(timeOutID, )
        // timeOutID = getTimeOut(timeOutID, async ()=>{
            
        // })
        // hirerID: req.body.hirerID,
        // startPoint: req.body.startPoint
        console.log('user id: ', userID)
        let route = 'item/getShapItems';
        let data = {
            startPoint: startPt,
            shapID: userID
        }

        if(userData.isShappers){
            route = 'item/getHirerItems';
            data = {
                startPoint: startPt,
                hirerID: userID
            }
        }
        let response = await apiCall(globals.varNames.apiPath + route, data);
        console.log('response: ', response);
        if(response.status == 1){
            // items = response.payload;
            setItemsData({...itemsData, items: response.payload});
        }
        
    }
    

    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            <div style={{flex: 1, overflow: 'auto'}}>
                {itemsData.items.map((item, idx) => 
                    <ItemList isUser={false} key={item.id} item = {item} />
                )}  
            </div>
            


            {/* <PageNav isForward={true} getItems={getItems} startPt={startPt} setStartPt={setStartPt} /> */}
        </div> 
    );
}