import React, {useState, useContext, useRef} from 'react'; 



import {GlobalContext} from '../../Context/GlobalProvider';

import AppModal from '../HtmlComponents/AppModal';
import Note from '../HtmlComponents/Note';
import {TextField} from '@material-ui/core';

// [bidAmt, id, itemName, bidBal]
export default function BidModal({closeModal}) {
    console.log('EDIT item modal');
    // console.log('item data: ', itemData);
    const {globals, apiCall, singleModalData, consumptionNote, getApiPath, showToast, convertNumToStr, convertStrToNum} = useContext(GlobalContext);
    console.log(globals.numOfItems)

    // let bidAmt = '';
    // if(singleModalData.data.bidAmt != 0){
    //     bidAmt = (singleModalData.data.bidAmt / 1000) + '';
    // }

    const [bidInputs, setBidInputs] = useState({
        amtPaid: 0,
        // bidAmt: convertNumToStr(singleModalData.data.bidAmt / 1000),
    });
    // const [error, setError] = useState('');
    // var startPoint = 0

    async function buyData(){
        console.log('SUBMIT NEW ITEM');
        
        let data = {
            amtPaid: bidInputs.amtPaid,
            // bidAmt: convertStrToNum(bidInputs.bidAmt),
        }
         
        let path = '';
        if(singleModalData.data.itemName == 'Serch Item'){
            path = globals.varNames.blagsApiPath + 'serch/bidItem';
            data.itemID = singleModalData.data.id;
        }else if(singleModalData.data.itemName == 'Post'){
            path = globals.varNames.blagsApiPath + 'blog/bidPost';
            data.blogID = singleModalData.data.id;
        }else if(singleModalData.data.itemName == 'User'){
            path = globals.varNames.apiPath + 'user/bidUser';
            // data.blogID = singleModalData.data.id;       
        }else if(singleModalData.data.itemName == 'User Sosyal'){
            path = globals.varNames.blagsApiPath + 'user/bidUser';
        }else if(singleModalData.data.itemName == 'Item'){
            path = globals.varNames.apiPath + 'item/bidItem';
            data.itemID = singleModalData.data.id;      
        }   

        // let response = await apiCall(getApiPath() + 'pay/buyData', data); 
        apiCall(path, data).then((response) => {
            console.log('response: ', response);
            if(response.status == 1){
                showToast(response.message, 3, false);
            }
        });
        closeModal();
    }

   
    
    return (
        <AppModal consumptionShow={true} onButtonsPressed={buyData} buttons={['Submit']} closeModal={closeModal}>
            <span>Bid Bal: {singleModalData.data.bidBal / 1000}</span>
            <TextField onChange={(e)=>setBidInputs({...bidInputs, amtPaid: e.target.value})} id="standard-basic" label="Amount to pay" />
            {/* <Note note={consumptionNote()} /> */}
            {/* <View>
                
                <Item>
                    <Input onChangeText={(text)=>setBidInputs({...bidInputs, amtPaid: text})} placeholder="Amount to pay" />
                </Item>
                
                 
            </View> */}
            
        </AppModal>
    );
}