import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';


import SearchTagList from '../List/SearchTagList'



// import PageNav from '../Components/PageNav';
// import Title from '../HtmlComponents/Title';

import {Button, TextField} from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';

import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';


export default function SearchTagsPage() {
	console.log('LIST')
	const { userData, globals, getTimeOut, closePageNav, showPageNav, apiCall, openSingleModal, getApiPath, funcDelayer } = useContext(GlobalContext);
    const [tag, setTag] = useState('');
    const [pageInitiated, setPageInitiated] = useState(false);
    const [startPt, setStartPt] = useState(0);
    const [tags, setTags] = useState([]);
    const [timeOutID, setTimeOutID] = useState(0);

    async function getTags(){
        console.log('search');
        // console.log('search options: ', searchInputs);
        
        let data = {
            tag: tag,
            startPoint: startPt,
            // email: userInputs.email    
        }

        let response = await apiCall(getApiPath() + 'user/getSearchTags', data);
        console.log('response: ', response);
        if(response.status == 1){
            setTags(response.payload);
            
        } 

        

        
        
    }

    useEffect(() => {
        // Update the document title using the browser API
        // getTags();
        
        showPageNav(setStartPt);
        getTags();
        setPageInitiated(true);
        return closePageNav
    }, []);

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            setTimeOutID(funcDelayer(timeOutID, getTags, 0.1));        
        }
    }, [tag]);

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            getTags();       
        }
    }, [startPt]);
    
    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            {/* <div style={{flexDirection: 'row'}}>
                <Button raised onPress={()=>openSingleModal('New Tag', '')} primary text="New" />
            </div> */}
            <div style={{justifyContent: 'flex-end'}} className='d-flex mb-2'>
                <Button variant="contained" onClick={()=>openSingleModal('New Tag', '')}>New</Button>
            </div>
            
            {/* <div className='d-flex flex-row'>
                <Item style={{flex: 1, marginRight: 2}}>
                    <Input onChangeText={(text) => {setTag(text)}} placeholder="Search..." />
                    <IconToggle onPress={getTags} name="search"/>
                    
                   
                    
                </Item>
            </div> */}
            {/* <div style={{flex: 1, overflow: 'auto'}} className='px-2 d-flex flex-column'>
                
            </div> */}

            <div className=''>
                <Paper style={{width: '100%'}} className='mb-2 d-flex py-2' component="form">
                    
                    <InputBase
                        // value={searchInputVal}
                        onChange={(e) => {setTag(e.target.value)}}
                        
                        style={{flex: 1}}
                        className='px-2'
                        placeholder="Search..."
                        inputProps={{ 'aria-label': 'search google maps' }}
                    />
                    
                    {/* <IconButton onClick={getTags} aria-label="search">
                        <SearchIcon />
                    </IconButton> */}
                
                    
                    
                </Paper>
            </div>
            {/* <h4 className='mb-1 mr-2' style={{alignSelf: 'flex-end'}}>Tags</h4> */}
            <div style={{flex: 1, overflow: 'auto'}} className='px-1'>
                {tags.map((tag) => {
                    return <SearchTagList tag={tag} key={tag.id}/>
                    
                    
                })}    
            </div>
            
            {/* <div style={{alignItems: 'flex-end'}} className='d-flex flex-column px-3'>
                <PageNav startPt={startPt} getItems={getTags} setStartPt={setStartPt} isForward={true}/>
            </div> */}
            
        </div>
    );
}