import React, {useState, useContext} from 'react'; 
// import {GlobalContext} from '../common/Context/GlobalProvider';
// import GoogleMapReact from 'google-map-react';
// import { View } from 'react-native';


import MapSearchComponent from '../Components/MapSearchComponent';
import InsidePageWrapper from '../common/HtmlComponents/InsidePageWrapper';
import SetLocation from '../Components/SetLocation'; 
import SetGroserMode from '../Components/SetGroserMode'; 

// import { Container, Header, Content, Button, Text, Input, Item } from 'native-base';
// import Tab from '../HtmlComponents/Tab';

export default function MapPage() {
    console.log('MAP PAGE');
    // const {userData} = useContext(GlobalContext);
    const [mapData, setMapData] = useState({
        tab: 'search'
    });
    
    function onChangeTab(page){
        setMapData({...mapData, tab: page});
        
    }

    let tabPage = <MapSearchComponent/>;

    

    if(mapData.tab === 'Settings'){
        tabPage = <div style={{flex: 1}} className='d-flex flex-column p-2'>
            <SetLocation />
            <SetGroserMode />
        </div>;
    }
    
    return (
        // <View style={{flex: 1}}>
        //     <Tab headers={['Search', 'Settings']} onChangeTab={onChangeTab} />
           
        //     {tabPage}
            
        // </View>
        <InsidePageWrapper onChangeTab={onChangeTab} tabs={['Search', 'Settings']} style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            {tabPage}    
        </InsidePageWrapper>
    );
}