
import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';


import AppModal from '../HtmlComponents/AppModal';

import {TextField} from '@material-ui/core';


export default function StartPointModal() {
    console.log('NewMsgModal');
    const {userData, globals, startPtModalData, forceUpdate} = useContext(GlobalContext);
    
    const [point, setPoint] = useState(startPtModalData.value);
    
    function changeStartPt(){
        let value = parseInt(point);

        if(!isNaN(value)){
            startPtModalData.callbackFunc(value);
        }
        
        closeModal();
    }
    
    
    function closeModal(){
        userData.openingStartPtModal = false;
        forceUpdate();
    }

    return (
        
        <AppModal buttons={['Ok']} onButtonsPressed={changeStartPt} closeModal={closeModal}>
            {/* <Item>
                <Input value={point + ''} onChangeText={(text)=>{setPoint(text)}} placeholder="Number" />
            </Item> */}
            <TextField onChange={(e)=>setPoint(e.target.value)} id="standard-basic" label="Number" />
            
            
        </AppModal>
    );
}