import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';



// import PageNav from './PageNav';
import PostsPicker from './PostsPicker';
import MenuIcon from '@material-ui/icons/Menu';
import {Button, TextField, IconButton} from '@material-ui/core';
import PostList from '../List/PostList';
import MoreVertIcon from '@material-ui/icons/MoreVert';


export default function PostsDiv({userViewedID, isProfilePage, isPostResult, postResult}) {
    console.log('MESSAGE HEADERS');
    // console.log('post result: ', postResult);
    // console.log('post result: ', postResult);
    const {userData, appData, globals, apiCall, openSingleModal, getSiteID, funcDelayer, showPageNav, closePageNav} = useContext(GlobalContext);
    const [startPt, setStartPt] = useState(0);
    const [startPtSequence, setStartPtSequence] = useState(0);
    const [counter, setCounter] = useState(0);
    // const [timeoutID, setTimeoutID] = useState(true);
    const [pageInitiated, setPageInitiated] = useState(false);
    let timeoutID = 0;
    // let postsInit = [];
    
    
    const [posts, setPosts] = useState([]);
    
    const [postOptions, setPostOptions] = useState({
        isAsc: false,
        isReplies: false,
        sortType: 'Last Update',
        isTagsTab: true,
        allSite: false
    })

    let siteID = getSiteID();
    
    

    function getSiteIDFinal(){
        let siteIDFinal = 0;
        if(!postOptions.allSite){
            siteIDFinal = siteID;
        } 
        
        return siteIDFinal;
    }

    function setOptions(options){
        // console.log('showingReplies: ', showingReplies);
        console.log('set options options: ', options);
        setPostOptions({
            
            isAsc: options.isAsc,
            isReplies: options.isReplies,
            sortType: options.sortType,
            isTagsTab: options.isTagsTab,
            allSite: options.allSite
        })
        
    }

    

    function getPosts(){
        // console.log('showingReplies: ', showingReplies);
        // funcDelayer()
        getBlogs();
        // if(postsData.showingReplies){
        //     getReplies(postsData.latestReplyID, postsData.latestSequence - 1);
        // }else{
            
        // }
    }

    

    async function getBlogs(){
        
        // if(isPostResult){
        //     return;
        // }

        if(isProfilePage){
            getProfileBlogs();
        }else{
            getHomeBlogs();
        }
        setCounter(counter + 1);
        // setStartPtSequence(0);
        // resetPostsData();
    }

    async function getProfileBlogs(){
        // let tab = 'TAGS';
        // if(isTagsTab){
        //     tab = 'USER';
        // }
        
        let data = {
            userViewedID: userViewedID,
            sortBy: postOptions.sortType,
            startPoint: startPt,
            tab: getTab(),
            siteID: getSiteIDFinal(),
            replyID: 0,
            isReplies: getIsReplies(),
            isAsc: getOrder(postOptions.isAsc)
            
        }
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.blagsApiPath + 'blog/getProfileBlogs', data);
        if(response.status == 1){
            // setMsgsData({...msgsData, msgs: response.payload});
            // clearPosts(0);
            // response.payload.forEach((post) => {
            //     initPost(post, 0);
            // })

            setPosts(response.payload)
            
        }
    }

    async function getHomeBlogs(){
        // if(pageInitiated)
        // let tab = 'TAGS';
        // if(isTagsTab){
        //     tab = 'USER';
        // }
        console.log('get home blogs');
        
        var data = {
            sortBy: postOptions.sortType,
            startPoint: startPt,
            tab: getTab(),
            siteID: getSiteIDFinal(),
            replyID: 0,
            isReplies: getIsReplies(),
            isAsc: getOrder(postOptions.isAsc)
        }

        console.log('get home blogs data: ', data);
        console.log('post options: ', postOptions);
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.blagsApiPath + 'blog/getHomeBlogs', data);
        console.log('response: ', response);
        if(response.status == 1){
            // setMsgsData({...msgsData, msgs: response.payload});
            // clearPosts(0);
            // response.payload.forEach((post) => {
            //     initPost(post, 0);
            // })
            
            setPosts(response.payload)
        }
    }

    function getTab(){
        let tab = 'USER';
        if(postOptions.isTagsTab){
            tab = 'TAGS';
        }
        
        
        return tab;
    }

    function getOrder(isAsc){
        let order = 0;
        if(isAsc){
            order = 1;
        }
        
        
        return order;
    }

    function getIsReplies(){
        let isReply = 0;
        if(postOptions.isReplies){
            isReply = 1;
        }
        
        
        return isReply;
    }

    useEffect(() => {
        // Update the document title using the browser API
        showPageNav(setStartPt);
        
        
        
        getBlogs();
        // if(isPostResult){
        
            
        //     setPosts([postResult]);
        //     // postsInit = [postResult];
        // }
        setPageInitiated(true);
        return closePageNav
    }, []);

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            getBlogs();
        }
        
        // setPageInitiated(true);
    }, [postOptions]);

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            getBlogs();
        }
        
        // setPageInitiated(true);
    }, [startPt]);

    // let pageNav = <PageNav startPt={startPt} getItems={getPosts} setStartPt={setStartPt} isForward={true}/>   ;
    let btns = <div style={{justifyContent: 'space-between'}} className='d-flex mb-1'>
        <Button variant="contained" onClick={()=>openSingleModal('New Post', {replyID: 0, post: '', tags: JSON.stringify([]), blogID: 0, bidAmt: 0})}>New</Button>
        
        
        <IconButton className='p-0' onClick={()=>openSingleModal('Post Options', {searchOptions: postOptions, setSearchOptions: setOptions, isRepliesOption: false})} aria-label="menu">
            <MoreVertIcon />
        </IconButton> 
        
    </div>;

    // if(isPostResult){
    //     // pageNav = null;
    //     btns = null;
    // }
    
    
    return (
        

        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column px-2 mt-1'>
            {btns}
            <div className='px-1' style={{flex: 1, overflow: 'auto'}}>
                {posts.map((post, ndx) => {
                    return (
                    
                        
                        <PostList isAsc={postOptions.isAsc} sortType={postOptions.sortType} sequence={1} getOrder={getOrder} post={post} key={post.id + '_' + 1 + '_' + counter}/>
                    )
                    
                })}
                
            </div>
            {/* {pageNav} */}
               
        </div>
    );
}