import React, {useState, useContext, forwardRef, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Button, TextField} from '@material-ui/core';
import Note from '../HtmlComponents/Note';

export default function SendMoney() {
    const {userData, globals, changePage, appData, getApiPath, apiCall, showToast} = useContext(GlobalContext);
    const [depositInputs, setDepositInputs] = useState({
        amount: 0,
        address: '',
        password: ''
    });
    
    async function deposit(){
        console.log('send money');
        let data = {
            amount: depositInputs.amount,
            // address: depositInputs.address,
            // password: depositInputs.password
        }

        
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.apiPath + 'pay/deposit', data);
        console.log('response: ', response);
        if(response.status == 1){
            showToast(response.message, 3);
        }
    }
    
    
    // console.log('user inputs: ', userInputs);
    return (
        <div className='d-flex flex-column px-5'>
            {/* <Item>
                <Input onChangeText={(text)=>setDepositInputs({...depositInputs, amount: text})} placeholder="Amount" />
            </Item> */}
            {/* <Item>
                <Input onChangeText={(text)=>setDepositInputs({...depositInputs, address: text})} placeholder="Address" />
            </Item>
            <Item>
                <Input onChangeText={(text)=>setDepositInputs({...depositInputs, password: text})} placeholder="Password" />
            </Item> */}
            <p>Transfer cash from your Sosyal account.</p>
            <TextField onChange={(e)=>setDepositInputs({...depositInputs, amount: e.target.value})} id="standard-basic" label="Amount" />
            
            <Button className='mt-2' variant="contained" onClick={deposit}>Deposit</Button>
            <Note note={'Deposit Fee is PHP ' + globals.minPayments.depositFee + '.'} />
        </div>
        
    );
}