import React, {useState, useContext} from 'react'; 

import { GlobalContext } from '../../Context/GlobalProvider';
import ErrorLog from '../HtmlComponents/ErrorLog';
import {Button, TextField, Link} from '@material-ui/core';






export default function ForgotPass({setForgotPassInputs}) {
    const {userData, globals, checkPassInput, checkRepeatPassInput, showToast, apiCall} = useContext(GlobalContext);
    const [passInputs, setPassInputs] = useState({
        newPass: '',
        repeatPass: '',
        email: '',
        username: ''
    });

    const [errors, setErrors] = useState({
        
        password: '',
        repeatPass: ''
    });

    const [error, setError] = useState('');

    async function changePass(){
        console.log('change pass');

        if(errors.password != '' || errors.repeatPass != '' || passInputs.username == '' || passInputs.newPass == '' || passInputs.email == '' || passInputs.repeatPass == ''){
            
            let msg = '';
            if(errors.password != '' || errors.repeatPass != ''){
                msg = 'Please check errors.';
                
            }else{
                if(passInputs.username == ''){
                    msg = 'Username cannot be blank.';
                     
                }else if(passInputs.newPass == ''){
                    msg = 'Password cannot be blank.';
                }else if(passInputs.repeatPass == ''){
                    msg = 'Repeat password cannot be blank.';    
                }else{
                    msg = 'Email cannot be blank.';   
                }
                  
            }

            showToast(msg, true, 3);
            // Toast.show(toastObj);
            return;
        }
        
        let data = {
            password: passInputs.newPass,
            email: passInputs.email,
            username: passInputs.username
            
            
        }
        let response = await apiCall(globals.varNames.blagsApiPath + 'user/forgotPassword', data);
        console.log('response: ', response);
        if(response.status == 1){
            setForgotPassInputs({
                userID: response.payload.userID,
                reqCode: response.payload.reqCode,
                confirmingForgotPass: true
            })
              
        }
    }
    
    function onChangePass(e){
        let text = e.target.value;
        setPassInputs({...passInputs, newPass: text});
        // checkInputs(text, passInputs.repeatPass);
        // setError(checkPassInput(text));
        setErrors({...errors, password: checkPassInput(text)});
    }

    function onChangeRepeatPass(e){
        let text = e.target.value;
        setPassInputs({...passInputs, repeatPass: text});
        // checkInputs(passInputs.newPass, text);
        setErrors({...errors, repeatPass: checkRepeatPassInput(passInputs.newPass, text)});
    }

    return (
        <div className='d-flex flex-column pt-5 mb-2'>
            <h4 style={{alignSelf: 'flex-end'}}>Forgot Password</h4>
            {/* <Item>
                <Input onChangeText={text => setPassInputs({...passInputs, username: text})} placeholder='Username'/>
            </Item> */}
            <TextField onChange={(e)=>setPassInputs({...passInputs, username: e.target.value})} id="standard-basic" label="Username" />
            {/* <Item>
                <Input onChangeText={text => setPassInputs({...passInputs, email: text})} placeholder='Email'/>
            </Item> */}
            <TextField onChange={(e)=>setPassInputs({...passInputs, email: e.target.value})} id="standard-basic" label="Email" />
            {/* <Item>
                <Input onChangeText={onChangePass} placeholder='New Password'/>
            </Item> */}
            <TextField type={userData.production? 'password' : 'text'} onChange={onChangePass} id="standard-basic" label="New Password" />
            <ErrorLog error={errors.password} />
            {/* <Item>
                <Input onChangeText={onChangeRepeatPass} placeholder='Repeat Password'/>
            </Item> */}
            <TextField type={userData.production? 'password' : 'text'} onChange={onChangeRepeatPass} id="standard-basic" label="Repeat Password" />
            <ErrorLog error={errors.repeatPass} />
            {/* <Button raised onPress={changePass} primary text="Change" /> */}
            <Button className='mt-2' variant="contained" onClick={changePass}>Change</Button>
        </div>

    );
}

