import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
// import { View, Image, TouchableOpacity } from 'react-native';

import {Card, Button, Avatar} from '@material-ui/core';




export default function UserImg({imgPathBegin, imgURL, userID, photoPassword, size=50}) {
	console.log('LIST')
    const { openSingleModal } = useContext(GlobalContext);

	let url = 'htto';
    if(imgURL != ''){
        url = imgPathBegin + imgURL + '/' + userID + '/' + photoPassword
        // url = profileData.profileImgUrl;
    }

    // let myStyle = {backgroundColor: 'gray', width: 50, height: 50}
    let imgURI = url;
    console.log('img uri: ', imgURI);
    // if(image != null){
    //     imgURI = URL.createObjectURL(image);
    // }
    return (
        
        <Avatar style={{width: size, height: size}} onClick={()=>openSingleModal('Image', url)} className='' alt="Remy Sharp" src={imgURI} />
    );
}