import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Link, Card, Paper} from '@material-ui/core';
// import { View, Image } from 'react-native';

// import { Button, COLOR } from 'react-native-material-ui';
// import { Avatar, Accessory } from 'react-native-elements';
// import { Text, Input, Item, Card } from 'native-base';
import UserLink from '../HtmlComponents/UserLink';
import Tab from '../HtmlComponents/Tab';

export default function MsgList({ msg, msgHeader }) {
    console.log('ITEM LIST')
    const { userData, globals, dateToStr, apiCall, showToast, openMsgModal, openYesNoModal, showOtherUser, convertTextToObj } = useContext(GlobalContext);
    
    let senderName = msgHeader.sendername;
    let imgURL = msgHeader.senderimg;
    if(msg.creator == msgHeader.receiver){
        senderName = msgHeader.receivername;
        imgURL = msgHeader.receiverimg;
    }

    let msgIsFromUser = false;
    if(userData.username == senderName){
        msgIsFromUser = true;
    }

    function onButtonsPressed(button){
        console.log('button: ', button);
        if(button == 'Edit'){
            // openSingleModal('Edit Msg', {msgID: msg.id, msg: msg.message}) 
            // username, msg, msgID, headerID  
            openMsgModal('', msg.message, msg.id, msg.msgheader);
        }else if(button == 'Delete'){
            openYesNoModal('Delete this message?', deleteMsg);
            
            // deleteMsg();   
        }
    }

    async function deleteMsg(){

        let data = {
           
            msgID: msg.id   
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'message/deleteMessage', data);
         
        console.log('response: ', response);
        if(response.status == 1){
            showToast(response.message, 5);
        }
    }

    let buttons = [];
    let bgColor = 'white';
    let alignSelf = 'flex-start';
    if(msgIsFromUser){
        bgColor = 'blue';
        buttons = ['Edit', 'Delete'];
        alignSelf = 'flex-end';
    }

    // let url = 'http';
    // if(imgURL != ''){
    //     url = globals.varNames.imgPathBlags + imgURL + '/' + userData.userID + '/' + userData.sosyalData.photoPassword
    //     // url = profileData.profileImgUrl;
    // }

    let wordsObjArr = convertTextToObj(msg.message);

    return (
        // <ListCard style={{backgroundColor: bgColor}} buttons={buttons} onButtonsPressed={onButtonsPressed}>
           
        //     <View style={{flexDirection: 'row'}}>
                
        //         <View style={{flex: 1}}>
        //             <Text style={{fontSize: 20, color: 'blue', marginBottom: 5}} onPress={()=>showOtherUser(senderName)}>@{senderName}</Text>
        //             <View style={{flexDirection: 'row'}}>
        //                 {wordsObjArr.map((word, ndx) => {
        //                     if(word.obj == 'text'){
        //                         return <Text style={{fontSize: 14}} key={ndx}>{word.text}</Text>;    
        //                     }else{
        //                         if(word.obj == 'userLink'){
        //                             return <Text key={ndx} style={{color: 'blue', fontSize: 14}} onPress={()=>{showOtherUser(word.text)}}>{'@' + word.text}</Text>      
        //                         }else{
        //                             return <Text key={ndx} style={{color: 'blue', fontSize: 14}} onPress={()=>{Linking.openURL(word.text)}}>{word.text}</Text>      
        //                         }
                                
        //                     }
                            
                        
                            
        //                 })}
        //             </View>
        //             <View style={{alignItems: 'flex-end'}}>
        //                 <Text style={{fontSize: 12}}>{dateToStr(msg.datecreated)}</Text>
        //             </View>
        //         </View>
                
        //     </View>
        //     <p>{msg.message}</p>
        // </ListCard>

        <Paper elevation={2} style={{width: '70%', alignSelf: alignSelf}} className='py-2 mb-2 px-2'>
            <div className='d-flex flex-row'>
                <UserLink username={senderName} />
            </div>
            <p>
                {wordsObjArr.map((word, ndx) => {
                    if(word.obj == 'text'){
                        return word.text;   
                    }else{
                        if(word.obj == 'userLink'){
                            return <UserLink username={word.text} />      
                            // return <Text key={ndx} style={{color: 'blue'}} onPress={()=>{showOtherUser(word.text)}}>{'@' + word.text}</Text>      
                        }else{
                            return <Link target="_blank" href={word.text}>{word.text}</Link>     
                            // return <Text key={ndx} style={{color: 'blue'}} onPress={()=>{Linking.openURL(word.text)}}>{word.text}</Text>      
                        }
                        
                    }
                    
                    
                    
                })}
            </p>
            <div className='d-flex' style={{justifyContent: 'flex-end'}}>
                <span className='' style={{fontSize: 12}}>{dateToStr(msg.datecreated)}</span>
            </div>
            <Tab headers={buttons} onChangeTab={onButtonsPressed} moreTabs={[]} />
        </Paper>
    );
}