
import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
// import { View } from 'react-native';


// import PageNav from './PageNav';
import {Button, TextField, Link} from '@material-ui/core';

// import { Button } from 'react-native-material-ui';
import MsgHeaderList from '../List/MsgHeaderList';

export default function MessageHeaders({onPressHeader}) {
    console.log('MESSAGE HEADERS');
    const {userData, globals, apiCall, messageData, closePageNav, showPageNav, getSiteID, openMsgModal, appData} = useContext(GlobalContext);
    const [startPt, setStartPt] = useState(0);
    const [msgHeaders, setMsgHeaders] = useState([]);
    const [allSite, setAllSite] = useState(false);
    const [pageInitiated, setPageInitiated] = useState(false);
    
    // console.log('msg headers:', messageData.msgHeaders);

    async function getMsgHeaders(){
        let siteID = 0;
        if(!allSite){
            siteID = getSiteID();
        }
        console.log('all site; ', allSite);
        let data = {
            startPoint: startPt,
            siteID: siteID
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'message/getMsgHeaders', data);
        console.log('response: ', response);
        if(response.status == 1){
            setMsgHeaders(response.payload);
            // messageData.msgHeaders = response.payload;
            // console.log('message headers: ', messageData.msgHeaders);
            // forceUpdate();
        }     
    }

    // function getHeaders(point){
    //     setStartPt(point); 
    // }

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            getMsgHeaders();
        }
        
    }, [allSite]);

    useEffect(() => {
        // Update the document title using the browser API
        // getMsgHeaders();
        
        showPageNav(setStartPt);
        getMsgHeaders();
        setPageInitiated(true);
        return closePageNav
    }, []);

    useEffect(() => {
        // Update the document title using the browser API
        // getMsgHeaders();
        if(pageInitiated){
            getMsgHeaders();
        } 
        
    }, [startPt]);
    
    let allSiteLabel = appData.name;
    if(userData.isSosyal){
        allSiteLabel = appData.sosyalName;
    }
    
    if(allSite){
        allSiteLabel = 'All';
    }
    // let modal = null;
    // if(newMsgModal){
    //     modal = <NewMsgModal closeModal={()=>setNewMsgModal(false)}/>
    // }
    // console.log('user inputs: ', userInputs);
    return (
        // <View style={{flex: 1}}>
        //     <View style = {{flexDirection: 'row', justifyContent: 'flex-end', marginBottom: 5}}>
        //         <Button raised primary text='New' onPress={()=>openMsgModal('', '', 0, 0)} />
        //         <Button raised primary text={allSiteLabel} onPress={()=>setAllSite(!allSite)} />
        //     </View>
            
        //     <View style={{flex: 1}}>
        //         {msgHeaders.map((msgHeader) => {
        //             return <MsgHeaderList msgHeader={msgHeader} key={msgHeader.id} onPressHeader={() => {onPressHeader(msgHeader)}}/>
                    
                    
        //         })}
        //     </View>
            
            
           

        //     <PageNav startPt={startPt} getItems={getMsgHeaders} setStartPt={setStartPt} isForward={true}/>
            
        // </View>
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            <div className='mb-2 d-flex' style={{justifyContent: 'flex-end'}}>
                <Link className='mr-3 pt-2' href="#" onClick={(e)=>{e.preventDefault(); setAllSite(!allSite)}}>{allSiteLabel}</Link>
                <Button variant="contained" onClick={()=>openMsgModal('', '', 0, 0)}>New</Button>
                {/* <Button variant="contained" onClick={()=>setAllSite(!allSite)}>{allSiteLabel}</Button> */}
                
            </div>
            
            <div className='px-1' style={{flex: 1, overflow: 'auto'}}>
                {msgHeaders.map((msgHeader) => {
                    return <MsgHeaderList msgHeader={msgHeader} key={msgHeader.id} onPressHeader={() => {onPressHeader(msgHeader)}}/>
                    
                    
                })}
            </div>
            {/* <div style={{alignItems: 'flex-end'}} className='d-flex flex-column px-3'>
                <PageNav startPt={startPt} getItems={getMsgHeaders} setStartPt={setStartPt} isForward={true}/>
            </div> */}
            
        </div>
    );
}

