
import React, {useState, useContext, useRef} from 'react'; 

// import {GlobalContext} from '../Context/GlobalProvider';
import {GlobalContext} from '../../Context/GlobalProvider';
import {Button, TextField, Link} from '@material-ui/core';
// import { View } from 'react-native';
// import { Text, Input, Item } from 'native-base';
// import { Button } from 'react-native-material-ui';

export default function Login() {
    console.log('LOGIN');
    const {userData, globals, getUserData, forceUpdate, appData, changeOutsidePage, apiCall} = useContext(GlobalContext);
    // const fbRef = useRef(null);
    // console.log('context: ', context);
    // const [amount, setAmount] = useState(5);
    const [userInputs, setUserInputs] = useState({
        username: '',
        password: ''
    });


    async function login(){
        console.log('login');
        
        let data = {
            username: userInputs.username,
            password: userInputs.password,
            siteID: appData.siteID   
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'user/login', data);
        
        console.log('response: ', response);
        if(response.status == 1){
            // userData.token = response.data.payload;
            // AsyncStorage.setItem('userData', JSON.stringify({token: userData.token}));
            if(response.code == 0){
                getUserData(response.payload);  
            }else{
                // userData.verification.email = '';
                userData.currentPage.front = 'Verification';
                userData.verification.email = response.payload;
                forceUpdate();
            }
              
        }
    }
    
    return (
        <div style={{flex: 1, marginTop: 50}} className='d-flex flex-column'>
            <div style={{flex: 1}} className='d-flex flex-column'>
                <div style={{alignItems: 'center'}} className='d-flex flex-column'>
                    <h3 style={{fontWeight: 700, fontSize: 20}} className=''>{appData.sosyalName}</h3>
                    <h1 style={{fontWeight: 700}}>{appData.name}</h1>
                </div>
                
                <div className='d-flex flex-column mb-2'>
                    <TextField onChange={(e)=>setUserInputs({...userInputs, username: e.target.value})} id="standard-basic" label="Username" />
                    <TextField type={userData.production? 'password' : 'text'} onChange={(e)=>setUserInputs({...userInputs, password: e.target.value})} id="standard-basic" label="Password" />
                    <Button className='mt-2' variant="contained" onClick={login}>Login</Button>
                </div>
                <div className='d-flex flex-row' style={{justifyContent: 'space-between'}}>
                    {/* <Text style={{fontSize: 30, alignSelf: 'center'}}>Sosyal Shaps</Text> */}
                    <Link className='mb-1 border-bottom' noIndent onClick={(e) => {e.preventDefault(); changeOutsidePage('Register')}}>
                        Create Account
                    </Link>
                    <Link className='mb-1 border-bottom' noIndent onClick={(e) => {e.preventDefault(); changeOutsidePage('ForgotPass')}}>
                        Forgot Password?
                    </Link>
                </div>
            </div>
            
            <div style={{alignItems: 'center'}} className='d-flex flex-column'>
                {/* <h3 style={{fontWeight: 700, fontSize: 20}} className=''>{appData.sosyalName}</h3> */}
                <span style={{fontSize: 14, fontWeight: 300}}>{appData.name} {appData.sosyalName}</span>
            </div>
            {/* <Item>
                <Input onChangeText={(text)=>setUserInputs({...userInputs, username: text})} placeholder="Username" />
            </Item>
            <Item>
                <Input onChangeText={(text)=>setUserInputs({...userInputs, password: text})} placeholder="Password" />
            </Item>
            
            <Button style={{container: {marginVertical: 14}}} raised onPress={login} primary text="Login" /> */}
            {/* <div style={{flexDirection: 'row', justifyContent: 'space-between'}}>
                
                <Button onPress={()=>changeOutsidePage('Register')} primary text="Create Account" />
                <Button onPress={()=>changeOutsidePage('ForgotPass')} primary text="Forgot Password" />
            </div> */}
           
        </div>

        
        
    );
}
