import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';


import {Card, Button, Link} from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import { yellow, green } from '@material-ui/core/colors';

export default function UserLink({username, verified, fontSize = 15}) {
	console.log('LIST')
    const { showOtherUser } = useContext(GlobalContext); 
    let checkIcon = null;
	if(verified == 1){
        checkIcon = <CheckIcon className='' style={{color: green[500], fontSize: 20}} />;
    }

    return (
        <Link style={{fontWeight: 500, fontSize}} href="#" onClick={(e)=>{e.preventDefault(); showOtherUser(username)}}>
            @{username}
            {checkIcon}
        </Link>
        // <Text onPress={()=>showOtherUser(username)} style={{...style, color: 'blue', fontWeight: 'bold'}}>@{username}</Text>
    );
}