import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';


import VerifyAccount from '../Components/VerifyAccount';
import EditVerifyAccount from '../Components/EditVerifyAccount';

export default function VerifyAccountPage() {
    const {userData, globals, apiCall, openSingleModal, getApiPath} = useContext(GlobalContext);
    const [isEditing, setIsEditing] = useState(false);
    console.log('verify data set: ', userData.verifiedDataSet);

    let name = '';
    let address = '';
    let country = 147;
    let birthdate = 0;
    let isMale = 1;

    if(userData.verifiedDataSet){
        name = userData.verifiedData.name;
        address = userData.verifiedData.address;
        country = userData.verifiedData.country;
        birthdate = userData.verifiedData.birthdate;
        isMale = userData.verifiedData.isMale;
    }
    
    let page = <VerifyAccount name={name} address={address} country={country} birthdate={birthdate} isMale={isMale} setIsEditing={setIsEditing} isEditing={isEditing} />;
    if(userData.verifiedDataSet && !isEditing){
        page = <EditVerifyAccount setIsEditing={setIsEditing} />
    }

    console.log('verified data set: ', userData.verifiedDataSet);

    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column px-4 p-2'>
            
            {page}
        </div>
    );
}