
import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';

import {Button, TextField} from '@material-ui/core';

// import PageNav from './PageNav';

import ReviewList from '../List/ReviewList';

export default function Reviews({userData, isUser}) {
    console.log('REVIEWS');
    const {globals, apiCall, openSingleModal, showPageNav, closePageNav, getSiteID} = useContext(GlobalContext);
    // const {apiCall} = useContext(GlobalContext);
    console.log('profile data: ', userData);
    // const [startPt, setStartPt] = useState(0);
    const [reviewsData, setReviewsData] = useState({
        reviews: [],
    });

    async function getReviews(startPt){
        // userViewedID: req.body.userViewedID,
        // startPoint
        console.log('site id: ', getSiteID());
        let response = await apiCall(globals.varNames.blagsApiPath + 'user/getReviews', {startPoint: startPt, userViewedID: userData.userID, siteID: getSiteID()});
        console.log('response: ', response);
        if(response.status == 1){
            
            setReviewsData({...reviewsData, reviews: response.payload});
        } 
    }
    
    useEffect(() => {
        // Update the document title using the browser API
        // getReviews();
        showPageNav(getReviews);
        getReviews(0);

        return closePageNav
    }, []);
    
    
    
    
    let reviewBtn = null;
    if(!isUser){
        // reviewBtn = <View>
        //     <Button onPress={()=>openSingleModal('Review', userData.userID)} transparent>
        //         <Text>Create Review</Text>
        //     </Button>   
        // </View>;
        // <View style={{flexDirection: 'row'}}>
        //         <Button style={{container: {marginRight: 25}}} raised primary text='Back' onPress={onPressBack} />
        //         <PageNav startPt={startPt} setStartPt={setStartPt} isForward={false} getItems={getMsgs} />
        //     </View>
        // reviewBtn = <View style={{flexDirection: 'row'}}>
        //     <Button primary text='Review user' onPress={()=>openSingleModal('Review', userData.userID)} raised />
        // </View>
        reviewBtn = <Button variant="contained" onClick={()=>openSingleModal('Review', userData.userID)}>Review User</Button>;
        
    }
    
    
    // console.log('user inputs: ', userInputs);
    return (
        // <View style={{flex: 1}}>
            
        //     {reviewBtn}
            
        //     <View style={{flex: 1}}>
        //         <ScrollView>
        //             {reviewsData.reviews.map((review) => {
        //                 return <ReviewList key={review.id} review={review}/>
                        
        //             })}
        //         </ScrollView>   
        //     </View>
            
            
        //     <PageNav isForward={true} startPt={startPt} setStartPt={setStartPt} getItems={getReviews} />
        // </View>
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column py-1 px-2'>
            <div style={{justifyContent: 'flex-end'}} className='d-flex'>
                {reviewBtn}
            </div>
            
            <div style={{flex: 1, overflow: 'auto'}} className='px-1 py-1'>
                {reviewsData.reviews.map((review) => {
                    return <ReviewList key={review.id} review={review}/>
                    
                })}
            </div>
            {/* <div style={{alignItems: 'flex-end'}} className='d-flex flex-column px-3'>
                <PageNav isForward={true} startPt={startPt} setStartPt={setStartPt} getItems={getReviews} />
            </div> */}
            
        </div>
        
        
    );
}