import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';

import Tab from '../common/HtmlComponents/Tab';
import UserInfoDiv from '../common/HtmlComponents/UserInfoDiv';
import DetailsDiv from '../common/Components/DetailsDiv';
import {Link, Card, Paper} from '@material-ui/core';

export default function ShapperList({ shapper }) {
    console.log('ITEM LIST')
    const { showOtherUser, userData, openMsgModal, apiCall, showToast, globals, openYesNoModal, createUserInfo, openSingleModal } = useContext(GlobalContext);
    
    

    async function cancelHire(){
        let data = {
            type: 'shapper',
            userID: shapper.userid   
        }
        let response = await apiCall(globals.varNames.apiPath + 'map/cancelHire', data); 
        console.log('response: ', response);
        if(response.status == 1){
            showToast(response.message, 3, false);
        }        
    }

    let imgPathBegin = globals.varNames.imgPath;
    let photoPassword = userData.photoPassword;

    let details = <DetailsDiv info={shapper} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />;

    function onButtonsPressed(button){
        console.log('button: ', button);
        if(button == 'Message'){
            // username, msg, msgID, headerID
            openMsgModal(shapper.username, '', 0, 0);
        }else if(button == 'Cancel'){
            openYesNoModal('Cancel the services of this user.', cancelHire);
            // cancelHire();
        }else if(button == 'More Info'){
            openSingleModal('Details', {div: details});
            // cancelHire();
        }
    }

    let url = 'http';
    if(shapper.imgurl != ''){
        url = globals.varNames.imgPath + shapper.imgurl + '/' + userData.userID + '/' + userData.photoPassword
        // url = profileData.profileImgUrl;
    }

    let userInfo = createUserInfo(shapper, false);
    
    return (
        <Paper elevation={2} className='d-flex flex-column p-2'>
            <div>
                <UserInfoDiv userInfo={userInfo} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />
                
            </div>
            
            {/* <div className='d-flex'>
                <UserImg imgPathBegin={imgPathBegin} imgURL={shapper.imgurl} userID={userData.userID} photoPassword={photoPassword} />
                
                <div className='d-flex flex-column ml-2'>
                    <span>@{shapper.username}</span>
                    
                </div>
            </div> */}
            <Tab headers={['Message', 'Cancel', 'More Info']} onChangeTab={onButtonsPressed} moreTabs={[]} />
        </Paper>
    );
}