import React, {useState, useContext} from 'react'; 


import { GlobalContext } from '../../Context/GlobalProvider';


import {Button, TextField, InputLabel, FormControl, Select, MenuItem} from '@material-ui/core';
// import ErrorLog from '../HtmlComponents/ErrorLog';
import Note from '../HtmlComponents/Note';



export default function EditProfile() {
    const {userData, globals, showToast, apiCall, convertTagsStrToArrayStr, getApiPath, tagsNote, minBidNote, convertTagsToStr, countries, convertNumToStr, convertStrToNum} = useContext(GlobalContext);

    let name = userData.name;
    let description = userData.description;
    let tags = userData.tags;
    let country = userData.country;
    let bidAmt = convertNumToStr(userData.bidAmt / 1000);

    if(userData.isSosyal){
        name = userData.sosyalData.name;
        description = userData.sosyalData.description;
        tags = userData.sosyalData.tags;
        country = userData.sosyalData.country;
        bidAmt = convertNumToStr(userData.sosyalData.bidAmt / 1000);
    }

    let tagsStr = convertTagsToStr(tags);

    // let tagsArr = JSON.parse(tags);
    // let tagsStr = '';
    // tagsArr.forEach((tag, ndx) => {
    //     tagsStr = tagsStr + tag + ' ';
    // })

    const [profileInputs, setProfileInputs] = useState({
        name: name,
        description: description,
        tags: tagsStr,
        country: country,
        bidAmt: bidAmt
        
    });

    // const [error, setError] = useState('');
    
    async function editProfile(){
        console.log('change pass');
        // var response = null;
        if(profileInputs.name == '' || profileInputs.description == ''){
            
            let msg = '';
            
            if(profileInputs.name == ''){
                msg = 'Name cannot be empty.';
            }else if(profileInputs.description == ''){
                msg = 'Description cannot be empty.';   
            }

            showToast(msg, 3, true);
            // Toast.show(toastObj);
            return;
        }
        // let tagsArray = profileInputs.tags.split(" ");
        let arrayStr = convertTagsStrToArrayStr(profileInputs.tags);
        // console.log('wordArray: ', wordArray);
        // tagsArray.forEach((tag) => {
        //     parseInt(tag);
        // })
        let data = {
            name: profileInputs.name,
            description: profileInputs.description,
            tags: arrayStr,
            country: profileInputs.country,
            bidAmt: convertStrToNum(profileInputs.bidAmt)  
        }

        let response = await apiCall(getApiPath() + 'user/editProfile', data);
       
        
        console.log('response: ', response);
        if(response.status == 1){
            showToast(response.message, 3);      
             
        }
    }

    // function onChangePass(text){
    //     setPassInputs({...passInputs, newPass: text});
    //     // checkInputs(text, passInputs.repeatPass);
    //     setError(checkPassInput(text));
    // }

    // function onChangeRepeatPass(text){
    //     setPassInputs({...passInputs, repeatPass: text});
    //     // checkInputs(passInputs.newPass, text);
    //     setError(checkRepeatPassInput(passInputs.newPass, text));
    // }

    return (
        <div className='d-flex flex-column mb-3 py-2'>
            <h5 style={{alignSelf: 'flex-end'}}>Edit Profile</h5>
            {/* <Title title='Edit Profile'/> */}
            {/* <Item>
                <Input value={profileInputs.name} onChangeText={(text)=>setProfileInputs({...profileInputs, name: text})} placeholder="Name" />
            </Item> */}
            <TextField value={profileInputs.name} onChange={(e)=>setProfileInputs({...profileInputs, name: e.target.value})} id="standard-basic" label="Name" />
            {/* <Item>
                <Input value={profileInputs.description} onChangeText={(text)=>setProfileInputs({...profileInputs, description: text})} placeholder="Description" />
            </Item> */}
            <TextField value={profileInputs.description} onChange={(e)=>setProfileInputs({...profileInputs, description: e.target.value})} id="standard-basic" label="Description" />
            {/* <Item>
                <Input value={profileInputs.tags} onChangeText={(text)=>setProfileInputs({...profileInputs, tags: text})} placeholder="Tags" />
            </Item> */}
            <TextField value={profileInputs.tags} onChange={(e)=>setProfileInputs({...profileInputs, tags: e.target.value})} id="standard-basic" label="Tags" />
            <Note note={tagsNote()} />
            {/* <Item>
                <Input value={profileInputs.bidAmt} onChangeText={(text)=>setProfileInputs({...profileInputs, bidAmt: text})} placeholder="Bid Amount" />
            </Item> */}
            <TextField value={profileInputs.bidAmt} onChange={(e)=>setProfileInputs({...profileInputs, bidAmt: e.target.value})} id="standard-basic" label="Bid Amount" />
            <Note note={minBidNote()} />
            <FormControl>
                <InputLabel id="demo-simple-select-label">Country</InputLabel>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={profileInputs.country}
                    onChange={(e)=>{setProfileInputs({...profileInputs, country: e.target.value})}}
                >
                    {countries.map((country, id) => {
                        return (
                            // <Picker.Item key={id} label={country} value={id} />
                            <MenuItem key={id} value={id}>{country}</MenuItem>
                        )
                        
                    })}
                {/* <MenuItem value={10}>Ten</MenuItem>
                <MenuItem value={20}>Twenty</MenuItem>
                <MenuItem value={30}>Thirty</MenuItem> */}
                </Select>
            </FormControl>
            {/* <div style={{flexDirection: 'row'}}>
                <Picker
                    note
                    mode="dropdown"
                    // style={{ width: 10 }}
                    selectedValue={profileInputs.country}
                    onValueChange={(value)=>{setProfileInputs({...profileInputs, country: value})}}
                >

                    {countries.map((country, id) => {
                        return (
                            <Picker.Item key={id} label={country} value={id} />
                        )
                        
                    })}
                    
                </Picker>    
            </div> */}
            
            {/* <ErrorLog error={error} /> */}
            {/* <div style={{flexDirection: 'row', marginTop: 5}}>
                <Button raised primary text='Edit' onPress={editProfile} />
            </div> */}
            <Button className='mt-3' variant="contained" onClick={editProfile}>Edit</Button>
            
           
        </div>

    );
}