import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';


import DataList from '../List/DataList';
// import PageNav from '../Components/PageNav';
// import Title from '../HtmlComponents/Title';
import {Button, TextField} from '@material-ui/core';

export default function DataPage() {
    const {userData, globals, showPageNav, closePageNav, apiCall, openSingleModal, getApiPath} = useContext(GlobalContext);
    
    const [datasArr, setDatasArr] = useState([]);
    const [tab, setTab] = useState('Profile');
    // const [startPt, setStartPt] = useState(0);
    
    let dataBal = userData.dataBal;
    let totalAddtlData = userData.totalAddtlData;

    if(userData.isSosyal){
        dataBal = userData.sosyalData.dataBal;
        totalAddtlData = userData.sosyalData.totalAddtlData;
    }

    console.log('databal: ', dataBal);
    console.log('total addtl data: ', totalAddtlData);
    async function getDatas(startPt){
        
        let response = await apiCall(getApiPath() + 'pay/getDatas', {startPoint: startPt});
        console.log('response: ', response);
        if(response.status == 1){
            setDatasArr(response.payload);
           
        }     
    }

    useEffect(() => {
        // Update the document title using the browser API
        showPageNav(getDatas);
        getDatas(0);

        return closePageNav
        
    }, []);

    return (
        // <View style={{flex: 1}}>
            
        //     <Text style={{fontSize: 20}}>Balance: {dataBal}</Text>
        //     <Text style={{fontSize: 20}}>Free: {dataBal - totalAddtlData}</Text>
        //     <Text style={{fontSize: 20}}>Additional Data: {totalAddtlData}</Text>
        //     <Button style={{container: {marginHorizontal: 20, marginVertical: 10}}} primary text='Buy Data' onPress={()=>openSingleModal('Buy Data', '')} raised />
        //     <Title title='Datas'/>
        //     <View style={{flex: 1}}>
        //         {datasArr.map((dataArr) => {
        //             return <DataList data={dataArr} key={dataArr.id}/>
                   
                    
        //         })}
        //     </View>
            
            
           

        //     <PageNav startPt={startPt} getItems={getDatas} setStartPt={setStartPt} isForward={true}/>
        // </View>

        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
            <div className='mb-3 d-flex flex-column px-5 mt-3 border-bottom py-2'>
                <h5>Balance: {dataBal}</h5>       
                <h6>Free: {dataBal - totalAddtlData}</h6>
                <h6>Additional Data: {totalAddtlData}</h6>
                <Button className='mt-3' style={{width: '60%', alignSelf: 'center'}} variant="contained" onClick={()=>openSingleModal('Buy Data', '')}>Buy Data</Button>
                {/* <div style={{justifyContent: 'flex-end'}} className='d-flex'>
                    
                </div> */}
                
            </div>
            
            
            {/* <Title title='Datas'/> */}
            <div style={{flex: 1, overflow: 'auto'}} className='px-2'>
                {datasArr.map((dataArr) => {
                    return <DataList data={dataArr} key={dataArr.id}/>
                
                    
                })}
            </div>



            {/* <div className='border-top'>
                <PageNav startPt={startPt} getItems={getDatas} setStartPt={setStartPt} isForward={true}/>
            </div> */}
            
        </div>
    );
}