import React, {useState, useContext, forwardRef, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import {Button, TextField, InputLabel, FormControl, Select, MenuItem} from '@material-ui/core';

export default function VerifyAccount({name, address, birthdate, isMale, country, setIsEditing, isEditing}) {
    const {userData, globals, changePage, countries, getApiPath, apiCall} = useContext(GlobalContext);

    let currentDate = new Date().getTime();
    if(birthdate != 0){
        currentDate = birthdate * 1000;
    }

    

    const [verifyInputs, setVerifyInputs] = useState({
        name: name,
        address: address,
        birthdate: currentDate,
        gender: convertIsMaleToGender(isMale),
        country: country
    });
    
    function getGender(){
        let male = 1;
        if(verifyInputs.gender == 'Female'){
            male = 0;
        }  
        
        return male;
    }

    function convertIsMaleToGender(){
        let gender = 'Male';
        if(isMale == 0){
            gender = 'Female';
        }
         
        
        return gender;
    }

    async function verifyAccount(){
        console.log('send money');

        var data = {
            name: verifyInputs.name,
            address: verifyInputs.address,
            isMale: getGender(),
            birthdate: (verifyInputs.birthdate / 1000),
            country: verifyInputs.country
        }
        let path = 'user/verifyAccount';
        if(userData.verifiedDataSet){
            path = 'user/editVerifiedData';
        }
        
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.blagsApiPath + path, data);
        console.log('response: ', response);
        if(response.status == 1){
            
        }
    }

    
    async function setDate(response){
        console.log('send money');
        console.log('response: ', response.target.value);
        let date = new Date(response.target.value).getTime();
        // console.log('date: ', date);
        // let dateStr = new Date(date);
        // console.log('dateStr: ', dateStr);
        setVerifyInputs({...verifyInputs, birthdate: date});
    }
    
    let dateToConvert = new Date(verifyInputs.birthdate);
    console.log('date to convert', dateToConvert);
    let month = dateToConvert.getMonth() + 1;
    let day = dateToConvert.getDate();
    let monthFinal = month;
    let dayFinal = day;

    if(month < 10){
        monthFinal = '0' + month;
    } 

    if(day < 10){
        dayFinal = '0' + day;
    }

    // let dateStr = currentDate.getFullYear() + '-0' + (currentDate.getMonth() + 1) + '-' + currentDate.getDate();
    let dateStr = dateToConvert.getFullYear() + '-' + monthFinal + '-' + dayFinal;
    
    console.log('datestr: ', dateStr);
    let pickerOptions = ['Male', 'Female'];
    // let defaultDate = new Date();
    console.log('current date: ', currentDate);
    // console.log('user inputs: ', userInputs);
    return (
        <div className='d-flex flex-column py-5 px-2'>
            {/* <Item>
                <Input value={verifyInputs.name} onChangeText={(text)=>setVerifyInputs({...verifyInputs, name: text})} placeholder="Name" />
            </Item> */}
            <TextField value={verifyInputs.name} onChange={(e)=>setVerifyInputs({...verifyInputs, name: e.target.value})} id="standard-basic" label="Name" />
            {/* <Item>
                <Input value={verifyInputs.address} onChangeText={(text)=>setVerifyInputs({...verifyInputs, address: text})} placeholder="Address" />
            </Item> */}
            <TextField value={verifyInputs.address} onChange={(e)=>setVerifyInputs({...verifyInputs, address: e.target.value})} id="standard-basic" label="Address" />

            {/* <DatePicker
                defaultDate={verifyInputs.birthdate * 1000}
                minimumDate={new Date(1900, 1, 1)}
                maximumDate={currentDate}
                locale={"en"}
                timeZoneOffsetInMinutes={undefined}
                modalTransparent={false}
                animationType={"fade"}
                androidMode={"default"}
                placeHolderText={verifyInputs.birthdate + ''}
                textStyle={{ color: "green" }}
                placeHolderTextStyle={{ color: "#d3d3d3" }}
                onDateChange={setDate}
                disabled={false}
            /> */}
            <form className='d-flex flex-column' noValidate>
                <TextField
                    id="date"
                    label="Birthday"
                    type="date"
                    onChange={setDate}
                    // defaultValue="2017-05-24"
                    // defaultValue={dateStr}
                    value={dateStr}
                    // className={classes.textField}
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
            </form>

            <FormControl>
                <InputLabel id="demo-simple-select-label">Gender</InputLabel>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={verifyInputs.gender}
                    onChange={(e)=>{setVerifyInputs({...verifyInputs, gender: e.target.value})}}
                >
                    {/* {countries.map((country, id) => {
                        return (
                            // <Picker.Item key={id} label={country} value={id} />
                            <MenuItem key={id} value={id}>{country}</MenuItem>
                        )
                        
                    })} */}

                    {pickerOptions.map((option, id) => {
                        return (
                            // <Picker.Item key={id} label={option} value={option} />
                            <MenuItem key={id} value={option}>{option}</MenuItem>
                        )
                        
                    })}
                {/* <MenuItem value={10}>Ten</MenuItem>
                <MenuItem value={20}>Twenty</MenuItem>
                <MenuItem value={30}>Thirty</MenuItem> */}
                </Select>
            </FormControl>

            <FormControl>
                <InputLabel id="demo-simple-select-label">Country</InputLabel>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={verifyInputs.country}
                    onChange={(e)=>{setVerifyInputs({...verifyInputs, country: e.target.value})}}
                >
                    {countries.map((country, id) => {
                        return (
                            // <Picker.Item key={id} label={country} value={id} />
                            <MenuItem key={id} value={id}>{country}</MenuItem>
                        )
                        
                    })}

                    
                {/* <MenuItem value={10}>Ten</MenuItem>
                <MenuItem value={20}>Twenty</MenuItem>
                <MenuItem value={30}>Thirty</MenuItem> */}
                </Select>
            </FormControl>

            {/* <div style={{flexDirection: 'row'}}>
                <Picker
                    note
                    mode="dropdown"
                    // style={{ width: 120 }}
                    selectedValue={verifyInputs.gender}
                    onValueChange={(value)=>{setVerifyInputs({...verifyInputs, gender: value})}}
                >

                    {pickerOptions.map((option, id) => {
                        return (
                            <Picker.Item key={id} label={option} value={option} />
                        )
                        
                    })}
                    
                </Picker>    
            </div>

            <div style={{flexDirection: 'row'}}>
                <Picker
                    note
                    mode="dropdown"
                    // style={{ width: 120 }}
                    selectedValue={verifyInputs.country}
                    onValueChange={(value)=>{setVerifyInputs({...verifyInputs, country: value})}}
                >

                    {countries.map((country, id) => {
                        return (
                            <Picker.Item key={id} label={country} value={id} />
                        )
                        
                    })}
                    
                </Picker>    
            </div> */}
            
            {/* <Button raised primary text="Submit" onPress={verifyAccount} /> */}
            <Button className='mt-2' variant="contained" onClick={verifyAccount}>Submit</Button>
            
        </div>
        
    );
}