import React, {useState, useContext, useRef} from 'react'; 


// import { View } from 'react-native';
import {GlobalContext} from '../Context/GlobalProvider';
// import { Input, Item } from 'native-base';
import AppModal from '../common/HtmlComponents/AppModal';
import Note from '../common/HtmlComponents/Note';
import {TextField} from '@material-ui/core';

// [tags, description, name, price, id, quantity, bidAmt]
export default function NewItemModal({closeModal}) {
    console.log('NewItemModal');
    const {userData, globals, apiCall, showToast, singleModalData, minBidNote, tagsNote, convertTagsToStr, convertTagsStrToArrayStr, convertNumToStr, convertStrToNum, convertEmptyStr} = useContext(GlobalContext);
    console.log(globals.numOfItems)
    console.log('single modal data: ', singleModalData);
    // let tagsInit = JSON.stringify([]);
    // if(!userData.isShappers){
    //     tagsInit = singleModalData.data.tags;   
    // }
    let tagsStr = '';
    if(!userData.isShappers){
        tagsStr = convertTagsToStr(singleModalData.data.tags);
    }
    
    // let price = '';
    // let quantity = '';
    
    // if(singleModalData.data.price > 0){
    //     price = (singleModalData.data.price / 100) + '';
    // }

    // if(singleModalData.data.quantity > 0){
    //     quantity = singleModalData.data.quantity + '';
    // }

    const [item, setItem] = useState({
        name: singleModalData.data.name,
        price: convertNumToStr(singleModalData.data.price),
        description: singleModalData.data.description,
        tags: tagsStr,
        quantity: convertNumToStr(singleModalData.data.quantity),
        bidAmt: convertNumToStr(singleModalData.data.bidAmt / 1000)
    });

    const [error, setError] = useState('');
    
    
    // var startPoint = 0

    async function addNewItem(){
        console.log('SUBMIT NEW ITEM');
        let itemName = convertEmptyStr(item.name);
        let itemDescription = convertEmptyStr(item.description);
        
        if(itemName == '' || itemDescription == ''){
            let msg = '';
            if(itemName == ''){
                msg = 'Name cannot be empty.';
            }else{
                msg = 'Description cannot be empty.';    
            }
            
           
            showToast(msg, 3, true);
            // Toast.show(toastObj);
            return;
        }

        let arrayStr = '';
        if(!userData.isShappers){
            arrayStr = convertTagsStrToArrayStr(item.tags);
        }
        

        let data = {
            name: item.name,
            price: convertStrToNum(item.price),
            description: item.description,
            tags: arrayStr,
            quantity: convertStrToNum(item.quantity)    
        }
        // convertStrToNum(item.bidAmt)
        let path = 'item/newItem';

        if(singleModalData.data.id != 0){
            // data = {
            //     name: item.name,
            //     price: item.price,
            //     description: item.description,
            //     tags: arrayStr,
            //     itemID: singleModalData.data.id,
            //     quantity: item.quantity,
            // }

            data.itemID = singleModalData.data.id;
            data.bidAmt = convertStrToNum(item.bidAmt)
            path = 'item/editItem';
        }

        let response = await apiCall(globals.varNames.apiPath + path, data); 
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Item submitted successfully.', 3, false);
            // if(path == 'item/editItem'){
            //     showToast('Item edited successfully.', 5);
            // }else{
            //     showToast('Item created successfully.', 5);   
            // }
            closeModal();
        }
        // apiCall(globals.varNames.apiPath + path, data).then((response)=>{
            
        // })
        
        
    } 
    let bidAmtInput = null;
    let tagsInput = null;
    let note = null;
    let minNote = null;
    if(!userData.isShappers){
        tagsInput = <TextField value={item.tags} onChange={(e)=>setItem({...item, tags: e.target.value})} id="standard-basic" label="Tags" />
        note = <Note note={tagsNote()} />;
        // tagsInput = <Item>
        //     <Input value={item.tags} onChangeText={(text)=>setItem({...item, tags: text})} placeholder="Tags" />
        // </Item>;
        if(singleModalData.data.id != 0){
            bidAmtInput = <TextField value={item.bidAmt} onChange={(e)=>setItem({...item, bidAmt: e.target.value})} id="standard-basic" label="Bid Amount" />
            minNote = <Note note={minBidNote()} />;
            // bidAmtInput = <Item>
            //     <Input value={item.bidAmt} onChangeText={(text)=>setItem({...item, bidAmt: text})} placeholder="Bid Amount" />
            // </Item>;
        }
    }

    
    
    
    return (
        <AppModal consumptionShow={true} buttons={['Submit']} onButtonsPressed={addNewItem} closeModal={closeModal}>
            
            <TextField value={item.name} onChange={(e)=>setItem({...item, name: e.target.value})} id="standard-basic" label="Name" />
            <TextField value={item.price} onChange={(e)=>setItem({...item, price: e.target.value})} id="standard-basic" label="Price" />
            <TextField value={item.description} onChange={(e)=>setItem({...item, description: e.target.value})} id="standard-basic" label="Description" />
            <TextField value={item.quantity} onChange={(e)=>setItem({...item, quantity: e.target.value})} id="standard-basic" label="Quantity" />
            {tagsInput}
            {note}
            {bidAmtInput}
            {minNote}
            
            
        </AppModal>
    );
}




 