import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';


import AppModal from '../HtmlComponents/AppModal';


import {TextField, Button, Checkbox, FormControlLabel, Link} from '@material-ui/core';

export default function SearchOptionsModal({closeModal}) {
    console.log('Search options modal');
    const {userData, appData, getUserData, forceUpdate, singleModalData, convertNumToStr} = useContext(GlobalContext);
    console.log('single modal:', singleModalData);
    const {searchOptions, setSearchOptions} = singleModalData.data;
    const [options, setOptions] = useState({
        searchType: searchOptions.searchType,
        userSearch: searchOptions.userSearch,
        companyEnabled: searchOptions.companySearchEnabled,
        numOfRaters: convertNumToStr(searchOptions.numOfRaters),
        isVerifiedRatings: searchOptions.isVerifiedRatings,
        company: {
            searchValue: searchOptions.companySearch.searchValue,
            searchType: searchOptions.companySearch.searchType,
            numOfRaters: convertNumToStr(searchOptions.companySearch.numOfRaters),
            isVerifiedRatings: searchOptions.companySearch.isVerifiedRatings,
        },
        // allSite: searchOptions.allSite,
        siteUsername: searchOptions.siteUsername 
    })
    
    // let pickerOptions = ['description', 'username', 'tags', 'name'];
    
    // const [error, setError] = useState('');
    let label = 'User Search';
    // let pickerItem = <Picker.Item label="Username" value="username" />;
    // let pickerOptionsFinal = [...pickerOptions];
    // let allSiteBtn = null;
    if(!options.userSearch){
        label = searchOptions.otherLabel + ' Search';
        

        // if(searchOptions.otherLabel == 'Posts'){
                
        // }
        
    }

    function onClickDone(){
        setSearchOptions(options);
        closeModal();
    }

    function onCheckCheckBox(e){
        console.log('e.target: ', e.target.checked);
        setOptions({...options, companyEnabled: e.target.checked})
    }
    
    let ratingsTypeLabel = 'All';
    let ratingsTypeLabelCo = 'All';
    if(options.isVerifiedRatings){
        ratingsTypeLabel = 'Verified Only';    
    }

    if(options.company.isVerifiedRatings){
        ratingsTypeLabelCo = 'Verified Only';
    }

    let companyEnabled = true;
    let pickerCo = <div className='d-flex flex-column'>
        <h5>Company</h5>
        
        
        {/* <Item>
            <Input value={options.company.searchValue} onChangeText={(value)=>setOptions({...options, company: {...options.company, searchValue: value}})} placeholder="Search Companies..." />
        </Item>
        <Item>
            <Input value={options.company.numOfRaters} onChangeText={(value)=>setOptions({...options, company: {...options.company, numOfRaters: value}})} placeholder="Num of Raters" />
        </Item> */}
        <TextField value={options.company.searchValue} onChange={(e)=>setOptions({...options, company: {...options.company, searchValue: e.target.value}})} id="standard-basic" placeholder="Search Companies" />
        <TextField value={options.company.numOfRaters} onChange={(e)=>setOptions({...options, company: {...options.company, numOfRaters: e.target.value}})} id="standard-basic" label="Num of Raters" />
        <div className='d-flex my-2'>
            <span className='mr-3'>Ratings Type: </span>
            {/* <Button primary text={ratingsTypeLabelCo} onPress={()=>{setOptions({...options, company: {...options.company, isVerifiedRatings: !options.company.isVerifiedRatings}})}} /> */}
            {/* <Button variant="contained" onClick={()=>{setOptions({...options, company: {...options.company, isVerifiedRatings: !options.company.isVerifiedRatings}})}}>{ratingsTypeLabelCo}</Button> */}
            <Link href="#" onClick={(e)=>{e.preventDefault(); setOptions({...options, company: {...options.company, isVerifiedRatings: !options.company.isVerifiedRatings}})}}>{ratingsTypeLabelCo}</Link>
        </div>
        
        
    </div>;

    if(!options.companyEnabled){
        companyEnabled = false;
        pickerCo = null;
    }

    let userSearchBtn = null;
    if(!searchOptions.userSearchOnly){
        
        // userSearchBtn = <Button raised primary text={label} onPress={()=>{setOptions({...options, userSearch: !options.userSearch})}} />
        // userSearchBtn = <Button variant="contained" onClick={()=>{setOptions({...options, userSearch: !options.userSearch})}}>{label}</Button>;
        userSearchBtn = <Link href="#" onClick={(e)=>{e.preventDefault(); setOptions({...options, userSearch: !options.userSearch})}}>{label}</Link>
    }

    let siteUsernameInput = null;
    if(!searchOptions.isSearch){
        siteUsernameInput = <TextField value={options.siteUsername} onChange={(e)=>setOptions({...options, siteUsername: e.target.value})} id="standard-basic" label="Site Username" />
        // siteUsernameInput = <Item>
        //     <Input value={options.siteUsername} onChangeText={(value)=>setOptions({...options, siteUsername: value})} placeholder="Site Username" />
        // </Item>;
    }

    return (
        
        // <AppModal buttons={['Done']} onButtonsPressed={onClickDone} closeModal={closeModal}>
            
        //     <View style={{flex: 1}}>
        //         <ScrollView>
                    
        //             <View style={{flexDirection: 'row', justifyContent: 'flex-end'}}>
        //                 {userSearchBtn}
        //             </View>
                    
        //             {siteUsernameInput}
                    
                    
                    
        //             <Item>
        //                 <Input value={options.numOfRaters} onChangeText={(value)=>setOptions({...options, numOfRaters: value})} placeholder="Num of Raters" />
        //             </Item> 
                     
                   
        //             <View style={{flexDirection: 'row', marginVertical: 5}}>
        //                 <Text style={{paddingTop: 5}}>Ratings Type: </Text>
        //                 <Button primary text={ratingsTypeLabel} onPress={()=>{setOptions({...options, isVerifiedRatings: !options.isVerifiedRatings})}} />
        //             </View>
                    
        //             <Checkbox onCheck={()=>setOptions({...options, companyEnabled: !options.companyEnabled})} label="Include Company" value="agree" checked={companyEnabled} />
                    
        //             {pickerCo}
                    
        //         </ScrollView>    
                
        //     </View>
        // </AppModal>
        <AppModal buttons={['Done']} onButtonsPressed={onClickDone} closeModal={closeModal}>
            
           
            <div className='d-flex' style={{justifyContent: 'flex-end'}}>
                {userSearchBtn}
                
            </div>
            {siteUsernameInput}
            <TextField value={options.numOfRaters} onChange={(e)=>setOptions({...options, numOfRaters: e.target.value})} id="standard-basic" label="Num of Raters" />
            <div className='d-flex my-2' style={{}}>
                <span className='mr-3'>Ratings Type:</span>
                {/* <Button variant="contained" onClick={()=>{setOptions({...options, isVerifiedRatings: !options.isVerifiedRatings})}}>{ratingsTypeLabel}</Button> */}
                <Link href="#" onClick={(e)=>{e.preventDefault(); setOptions({...options, isVerifiedRatings: !options.isVerifiedRatings})}}>{ratingsTypeLabel}</Link>
            </div>
            
            <FormControlLabel 
                control={<Checkbox
                    checked={companyEnabled}
                    onChange={onCheckCheckBox}
                    inputProps={{ 'aria-label': 'primary checkbox' }}
                />} 
                label="Include company" />
            {pickerCo}
            
        </AppModal>   
    );
}