import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import ProfileDiv from '../Components/ProfileDiv';
import Reviews from '../Components/Reviews';
import PostsDiv from '../Components/PostsDiv';
import OtherUserItems from '../../Components/OtherUserItems';


import InsidePageWrapper from '../HtmlComponents/InsidePageWrapper';

import OtherPage from '../HtmlComponents/OtherPage';
import Members from '../Components/Members';

export default function OtherProfilePage({closeOtherPage}) {
    console.log('other profile page');
    const {userData, globals, apiCall, otherUserData, otherPageData, countries, getApiPath} = useContext(GlobalContext);
    const [profileData, setProfileData] = useState({
        reviews: [],
        gotUserData: false,
        userData: null
    })
    console.log('followed users: ', userData.followedUsers);
    const [toastData, setToastData] = useState({
        isOpen: false,
        message: '',
        duration: 0
    });
    
    const [tab, setTab] = useState('Profile');
    
    

    async function getUserProfile(){
        let response = await apiCall(getApiPath() + 'user/getProfile', {username: otherPageData.data});
        console.log('response: ', response);
        if(response.status == 1){
            // getReviews();
            let userID = 0;
            let joinReqs = [];
            let companyID = 0;
            let userInfo = response.payload;
            if(userInfo.userid == null){
                userID = userInfo.id;
                joinReqs = userData.sosyalData.joinReqs;
                
            }else{

                userID = userInfo.userid;
                joinReqs = userData.joinReqs;
            }
            
            let userDataInit = {
                userID: userID,
                username: userInfo.username,
                profileImgUrl: globals.varNames.imgPath + userInfo.imgurl,
                name: userInfo.name,
                companyID: userData.companyID,
                country: countries[userInfo.country],
                rate: userInfo.rating,
                followedUsers: userData.followedUsers,
                joinReqs: joinReqs,
                rateVerified: userInfo.ratingverified,
                numOfRatersVerified: userInfo.numofratersverified,
                rate: userInfo.rating,
                numOfRaters: userInfo.numofraters,
                verifiedUser: userInfo.verifieduser,
                company: {
                    username: userInfo.companyusername,
                    rating: userInfo.companyrating,
                    numOfRaters: userInfo.companynumofraters,
                    ratingVerified: userInfo.companyratingverified,
                    numOfRatersVerified: userInfo.companynumofratersverified,
                    name: userInfo.companyname,
                    imgURL: userInfo.companyimgurl,
                }
            }
            
            setProfileData({...profileData, userData: userDataInit, gotUserData: true});
        } 
    }

    // function onPressBack(){
    //     userData.showingOtherUser = false;
    //     forceUpdate();
    // }
    
    function onChangeTab(page){
        setTab(page);
        
    }
    useEffect(() => {
        // Update the document title using the browser API
        getUserProfile();
    }, []);
    
    let page = <ProfileDiv profileData={profileData.userData} />;
    if(tab == 'Reviews'){
        // reviews, startPt, setStartPt, getReviews
        page = <Reviews isUser={false} userData={profileData.userData} />;
    }else if(tab == 'Members'){
        page = <Members fromUser={false} coID={profileData.userData.userID}/>
    }else if(tab == 'Items'){
        page = <OtherUserItems userID={profileData.userData.userID}/>;
    }else if(tab == 'Posts'){
        page = page = <PostsDiv isPostResult={false} postResult={null} isProfilePage={true} userViewedID={profileData.userData.userID} />;
    }

    let finalPage = null;
    if(profileData.gotUserData){
        finalPage = page;
        
    }
    
    let moreTabs = ['Posts', 'Items', 'Members'];
    if(userData.isSosyal){
        moreTabs.splice(1, 1);
    }
    return (
        <OtherPage onPressBack={closeOtherPage} >
            <InsidePageWrapper onChangeTab={onChangeTab} tabs={['Profile', 'Reviews', 'More']} moreTabs={moreTabs}>
                <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column py-2 px-1'>
                    {finalPage}
                </div>
                
            </InsidePageWrapper>
            {/* <div style={{flex: 1}}>
                
                <Tab headers={['Profile', 'Reviews', 'More']} moreTabs={['Items', 'Posts', 'Members']} onChangeTab={onChangeTab}></Tab>
                
            </div> */}
            
        </OtherPage>    
    );
}