import Axios from 'axios';
import React, { useState, useContext, useRef, useEffect } from 'react'; 

import {GlobalContext} from '../../Context/GlobalProvider';




import UploadPhoto from '../HtmlComponents/UploadPhoto';
// import Title from '../HtmlComponents/Title';




export default function UploadProfilePhoto() {
    const {userData, globals, getUserData, appData, randomStr, getApiPath, showToast} = useContext(GlobalContext);

    let photoURI = '';
    
    
    if(userData.isSosyal){
        if(userData.sosyalData.imgURL != ''){
            photoURI = globals.varNames.imgPathBlags + userData.sosyalData.imgURL + '/' + userData.userID + '/' + userData.sosyalData.photoPassword;
        }
        
    }else{
        if(userData.imgURL != ''){
            photoURI = globals.varNames.imgPath + userData.imgURL + '/' + userData.userID + '/' + userData.photoPassword;
        }
    }

    const [image, setImage] = useState(null);

    // const [image, setImage] = useState({
    //     uri: photoURI
    // })

    

    

    // const uploadPhoto = async () => {
    //     const formData = new FormData();
        
    //     const uri = image.uri;
    //     const uriParts = uri.split('.');
      
    //     const fileType = uriParts[uriParts.length - 1];
    //     console.log('uri parts: ', uriParts);
    //     const uriParts2 = uri.split('/');
    //     console.log('uri parts2', uriParts2);
    //     formData.append('file', {
    //         uri,
    //         name: uriParts2[uriParts2.length - 1],
    //         type: `image/${fileType}`,
    //     });
    //     formData.append('userID', userData.userID);
    //     // data.append('file', photo);
    //     var options = {
    //         method: 'post',
    //         url: getApiPath() + 'user/uploadProfile',
    //         data: formData,
    //         headers: {
    //             'x-access-token': userData.token,
    //             'version': appData.version,
    //             'sosyal-version': appData.versionSosyal,
    //             'photo-token': randomStr(5)
    //         }
    //     }
    //     var response = await Axios(options);
    //     console.log('response: ', response.data);
    //     if(response.status == 1){

    //     }
    //     // console.log(result);
    
        
    // };

    async function uploadPhoto(){
        let formData = new FormData();
        formData.append('file', image);
        formData.append('userID', userData.userID);
        let options = {
            method: 'post',
            url: getApiPath() + 'user/uploadProfile',
            data: formData,
            headers: {
                'x-access-token': userData.token,
                'version': appData.version,
                'sosyal-version': appData.versionSosyal,
                'photo-token': randomStr(5)
            }
        }
        let response = await Axios(options);
        console.log('response: ', response.data);
        let isError = false;
        if(response.data.status == 1){
            
            // closeModal();   
        }else{
            isError = true;
            // showToast(response.data.message, 5, false);   
        }
        showToast(response.data.message, 3, isError);
    }
    
    return (
        <div className='d-flex flex-column py-2 border-bottom'>
            {/* <Title title='Upload Photo'/> */}
            <h5 style={{alignSelf: 'flex-end'}}>Upload Photo</h5>
            <UploadPhoto imgURL={photoURI} setImage={setImage} image={image} uploadPhoto={uploadPhoto} />
            
        </div>
    );
}