import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';


import SerchItemList from '../List/SerchItemList'


import {Button, TextField} from '@material-ui/core';
// import PageNav from '../Components/PageNav';
// import Title from '../HtmlComponents/Title';





export default function SerchItemsDiv() {
	console.log('LIST')
	const { userData, globals, getTimeOut, apiCall, openSingleModal, showPageNav, closePageNav, getApiPath } = useContext(GlobalContext);
    const [tag, setTag] = useState('');
    // const [startPt, setStartPt] = useState(0);
    const [items, setItems] = useState([]);

    async function getItems(startPt){
        console.log('search');
        // console.log('search options: ', searchInputs);
        // timeOutID = getDelayer(timeOutID, getSearchedUsers);
        // console.log('timeout id: ', timeOutID)
        
        let data = {
            
            startPoint: startPt,
            // email: userInputs.email    
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'serch/getItemsByUser', data);
        console.log('response: ', response);
        if(response.status == 1){
            setItems(response.payload);
           
        } 
        

        
        
    }

    useEffect(() => {
        // Update the document title using the browser API
        // getItems();
        showPageNav(getItems);
        getItems(0);

        return closePageNav
    }, []);
    
    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column px-2'>
            {/* <div style={{flexDirection: 'row'}}>
                <Button raised onPress={()=>openSingleModal('New Serch Item', {name: '', description: '', link: '', id: 0, tags: JSON.stringify([]), siteUsername: '', bidAmt: 0})} primary text="New" />
            </div> */}
            <div className='d-flex mb-2 py-2' style={{justifyContent: 'flex-end'}}>
                <Button variant="contained" onClick={()=>openSingleModal('New Serch Item', {name: '', description: '', link: '', id: 0, tags: JSON.stringify([]), siteUsername: '', bidAmt: 0})}>New</Button>
            </div>
            
            <div style={{flex: 1, overflow: 'auto'}} className='px-1'>
                {items.map((item) => {
                    return <SerchItemList isUser={true} item={item} key={item.id}/>
                    
                    
                })}    
            </div>
            {/* <div style={{alignItems: 'flex-end'}} className='d-flex flex-column'>
                <PageNav startPt={startPt} getItems={getItems} setStartPt={setStartPt} isForward={true}/>
            </div> */}
            
            
            
        </div>
    );
}