import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import {Link, Card, Paper} from '@material-ui/core';
import Axios from 'axios';
import Tab from '../common/HtmlComponents/Tab';
import MoreInfo from '../common/HtmlComponents/MoreInfo';
import UserMoreInfo from '../common/HtmlComponents/UserMoreInfo';
import UserLink from '../common/HtmlComponents/UserLink';
import UserImg from '../common/Components/UserImg';
// import MoreInfo from '../common/HtmlComponents/MoreInfo';
import DetailsDiv from '../common/Components/DetailsDiv';

export default function ItemList({ item, isUser, isSearch = false, fromModal = false }) {
    console.log('ITEM LIST')
    const { userData, appData, randomStr, globals, showToast, apiCall, openSingleModal, openYesNoModal, convertTagsToStr } = useContext(GlobalContext);
    
    console.log('item: ', item);
    console.log('item img: ', globals.varNames.itemsImgPath + item.imgurl);

    let url = 'http';
    if(item.imgurl != ''){
        url = globals.varNames.itemsImgPath + item.imgurl + '/' + userData.userID + '/' + userData.photoPassword;
    }

    async function deleteItem() {
        console.log('DELETE ITEM')
        let response = await apiCall(globals.varNames.apiPath + 'item/deleteItem', {itemID: item.id});
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Item deleted successfully.', 3, false);
        }
        
    }

    function onButtonsPressed(button){
        if(button == 'Edit'){
            // openSingleModal('New Item', item); 
            // [tags, description, name, price, id, quantity, bidAmt] 
            openSingleModal('New Item', {tags: item.tags, description: item.description, name: item.name, price: item.price / 100, id: item.id, quantity: item.quantity, bidAmt: item.bidamt}) 
        }else if(button == 'Upload'){
            openSingleModal('Upload Item Photo', item);   
        }else if(button == 'Delete'){
            openYesNoModal('Delete this item?', deleteItem);   
        }else if(button == 'Bid'){
            openSingleModal('Bid', {bidAmt: item.bidamt, itemName: 'Item', id: item.id, bidBal: item.bidbal});  
        }
    }

    // async function uploadPhoto(image){
    //     let formData = new FormData();
    //     formData.append('file', image);
    //     // formData.append('userID', userData.userID);
    //     formData.append('itemID', item.id);
    //     let options = {
    //         method: 'post',
    //         url: globals.varNames.apiPath + 'item/uploadItemPhoto',
    //         data: formData,
    //         headers: {
    //             'x-access-token': userData.token,
    //             'version': appData.version,
    //             'sosyal-version': appData.versionSosyal,
    //             'photo-token': randomStr(5),
    //             'photo-id': item.id
    //         }
    //     }

    //     let response = await Axios(options);
    //     console.log('response: ', response.data);
    //     if(response.data.status == 1){
    //         showToast(response.data.message, 3, false);
    //         // closeModal();   
    //     }
    // }

    let buttons = [];
    let moreBtns = [];
    let moreInfoDiv = null;
    if(isUser){
        buttons = ['Edit', 'Delete', 'Upload'];
        if(!userData.isShappers){
            buttons = ['Edit', 'Delete', 'More'];
            moreBtns = ['Upload', 'Bid'];
        }

        if(!userData.isShappers){
            moreInfoDiv = <div className='d-flex flex-column'>
                <span style={styles.span}>Bid Amount: {item.bidamt / 1000}</span>
                <span style={styles.span}>Bid Balance: {item.bidbal / 1000}</span>
                <span style={styles.span}>Search Tags: {convertTagsToStr(item.tags)}</span>
            </div>
        }
        
        
    }

    let imgPathBegin = globals.varNames.itemsImgPath;
    let photoPassword = userData.photoPassword;

    let moreInfoDivSearch = null;
    let itemName = item.name;
    let itemDescription = item.description;
    let itemImgURL = item.imgurl;
    if(isSearch){
        itemName = item.itemname;
        itemDescription = item.itemdescription;
        itemImgURL = item.itemimgurl;
        if(!fromModal){

            let details = <div>
                <h6 className='m-0'>{itemName}</h6>
                <span style={styles.span}>{itemDescription}</span>  
                <DetailsDiv info={item} imgPathBegin={imgPathBegin} photoPassword={photoPassword} /> 
            </div>;

            moreInfoDivSearch = <div className='d-flex flex-column'>
                {/* <Rating size={17} isVerified={false} rating={item.rating} numOfRaters={item.numofraters} />
                <Rating size={17} isVerified={true} rating={item.ratingverified} numOfRaters={item.numofratersverified} />    
                <span>@{item.companyusername}</span>
                <Rating size={17} isVerified={false} rating={item.companyrating} numOfRaters={item.companynumofraters} />
                <Rating size={17} isVerified={true} rating={item.companyratingverified} numOfRaters={item.companynumofratersverified} />     */}
                {/* <span>{item.username}</span> */}
                <UserLink verified={item.verifieduser} username={item.username} />
                <MoreInfo div={details} />
                {/* <span>{item.name}</span> */}
                {/* <UserMoreInfo user={item} isSosyal={false} includeCompany={true} /> */}
            </div> 
        }
        
    }

    
    console.log('item img url: ', url);
    return (
        // <ListCard moreBtns={moreBtns} style={{padding: 5}} buttons={buttons} onButtonsPressed={onButtonsPressed}>
           
        //     <View style={{flexDirection: 'row'}}>
                
        //         <Image onPress={()=>openSingleModal('Image', url)} style={{width: 50, height: 50, marginRight: 5, backgroundColor: 'blue', alignSelf: 'center'}} source={{uri: url}}/>
        //         <View>
        //             <Text>{item.name}</Text> 
        //             <Text>{item.description}</Text>
        //             <Text>PHP {item.price / 100}</Text>
        //             <Text>{item.quantity}</Text>  
        //         </View>
                    
        //     </View>
            
        // </ListCard>
        <Paper elevation={2} className='d-flex flex-column mb-2 px-2 py-2'>
            <div>
                <div className='d-flex'>
                    <UserImg imgPathBegin={imgPathBegin} imgURL={itemImgURL} userID={userData.userID} photoPassword={photoPassword} />
                    <div style={{flex: 1}} className='d-flex flex-column ml-2 border-left pl-2'>
                        <h6 className='m-0'>{itemName}</h6>
                        <span style={styles.span}>{itemDescription}</span>
                        <div style={{width: '100%', justifyContent: 'space-between'}} className='d-flex' >
                            <span style={styles.span} className='mr-5'>PHP {item.price / 100}</span>
                            <span style={styles.span}>Qty: {item.quantity}</span>
                        </div>
                        
                        {/* <MoreInfo>
                            <div className='d-flex flex-column'>
                                <span>Bid Amount: {item.bidamt / 1000}</span>
                                <span>Bid Balance: {item.bidbal / 1000}</span>
                                <span>Search Tags: {item.searchtags}</span>
                            </div>
                            
                        </MoreInfo> */}
                        {moreInfoDiv}
                        {moreInfoDivSearch}
                    </div>
                </div>
            </div>
            
            
            <Tab headers={buttons} onChangeTab={onButtonsPressed} moreTabs={moreBtns} />
            
            
        </Paper>
    );
}

const styles = {
    span: {
        fontSize: 14
    }
}