import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';

import Tab from '../common/HtmlComponents/Tab';
import UserInfoDiv from '../common/HtmlComponents/UserInfoDiv';
import DetailsDiv from '../common/Components/DetailsDiv';
// import ListCard from '../HtmlComponents/ListCard';
import {Link, Card, Paper} from '@material-ui/core';

export default function CustomerList({ customer }) {
    console.log('ITEM LIST')
    const { showOtherUserList, globals, userData, createUserInfo, openSingleModal, openMsgModal, cancelShapper, openYesNoModal } = useContext(GlobalContext);
    
    
    function cancelHire(){
        cancelShapper(customer.userid, 'customer');
    }

    let imgPathBegin = globals.varNames.imgPath;
    let photoPassword = userData.photoPassword;

    let details = <DetailsDiv info={customer} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />;
    function onButtonsPressed(button){
        console.log('button: ', button);
        if(button == 'Items'){
            showOtherUserList(customer.userid);
        }else if(button == 'Message'){
            // openMsgModal(customer.username);    
            openMsgModal(customer.username, '', 0, 0);
        }else if(button == 'Cancel'){
            openYesNoModal('Cancel user?', cancelHire);
            // cancelHire();
        }else if(button == 'More Info'){
            openSingleModal('Details', {div: details});
            // cancelHire();
        }
    }

    let url = 'http';
    if(customer.imgurl != ''){
        url = globals.varNames.imgPath + customer.imgurl + '/' + userData.userID + '/' + userData.photoPassword
        // url = profileData.profileImgUrl;
    }

    // let verifiedIcon = null;
    // if(customer.verifieduser == 1){
    //     verifiedIcon = <Icon name='check' color='green' />;
    // }

    

    let userInfo = createUserInfo(customer, false);
    return (
        <Paper elevation={2} className='mb-2 p-2 d-flex flex-column'>
            <div>
                <UserInfoDiv userInfo={userInfo} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />
                {/* <div className='d-flex'>
                    <UserImg imgPathBegin={imgPathBegin} imgURL={customer.imgurl} userID={userData.userID} photoPassword={photoPassword} />
                   
                    <div className='d-flex flex-column'>
                        
                        <span>@{customer.username}</span>
                        
                        
                    </div>
                </div> */}
            </div>
            
            <Tab headers={['Message', 'Cancel', 'More']} onChangeTab={onButtonsPressed} moreTabs={['Items', 'More Info']} />
            {/* <Text>{JSON.stringify(customer)}</Text> */}
        </Paper>
    );
}