import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';


// import UserRatings from './UserRatings';
import UserImg from '../Components/UserImg';
import UserLink from './UserLink';
import Rating from './Rating';







export default function UserInfoDiv({userInfo, imgPathBegin, photoPassword}) {
	console.log('LIST')
	const {userData, globals, getUserData, singleModalData, apiCall, showToast, getSiteID} = useContext(GlobalContext);
    console.log('user info: ', userInfo);
    return (
        <div className='d-flex'>
            <div className='border-right px-2'>
                <UserImg imgPathBegin={imgPathBegin} imgURL={userInfo.imgURL} userID={userData.userID} photoPassword={photoPassword} />
            </div>
            
            <div className='d-flex flex-column px-2'>
                {/* <Text style={{color: 'blue', fontSize: 20}}>@{userInfo.username}</Text> */}
                <UserLink verified={userInfo.verifiedUser} username={userInfo.username} />
                <span>{userInfo.name}</span>
                <span>{userInfo.description}</span>
                <Rating size={17} isVerified={false} rating={userInfo.rating} numOfRaters={userInfo.numOfRaters} />
                <Rating size={17} isVerified={true} rating={userInfo.ratingVerified} numOfRaters={userInfo.numOfRatersVerified} />
            </div>
        </div>
    );
}