import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import UserLink from '../HtmlComponents/UserLink';
import {Link, Paper} from '@material-ui/core';

export default function TransactionList({ transaction }) {
    console.log('ITEM LIST')
    const { userData, globals, forceUpdate, dateToStr, openSingleModal, openYesNoModal } = useContext(GlobalContext);
    

    
    let text = transaction.typeName;
    let userComponent = null;
    if(text == 'Receive'){
        let userText = transaction.sendername;
        text = 'Receive Money From ';

        if(transaction.sender == userData.userID){
            text = 'Send Money To ';
            userText = transaction.receivername;
        }
        userComponent = <UserLink username={userText} />;
    }else if(text == 'Withdraw Sosyal'){
        if(userData.isSosyal){
            text = 'Transfer from ';
            userComponent = <UserLink username={transaction.sendername} />
        }else{
            text = 'Withdraw(Sosyal)';
        }          
    }else if(text == 'Deposit Sosyal'){
        if(userData.isSosyal){
            text = 'Transfer to ';
            userComponent = <UserLink username={transaction.sendername} />
        }else{
            text = 'Deposit(Sosyal)';
        }     
    }

    let labelComponent = <h6 style={{fontWeight: 700}}>
        {text}
        {userComponent}
    </h6>

    return (
        <Paper className='border-bottom py-2 mb-2 px-3 d-flex flex-column'>
            {labelComponent}
            <span style={{fontSize: 14}}>Php {transaction.amount}</span>
            <span style={{fontSize: 12, alignSelf: 'flex-end'}}>{dateToStr(transaction.datecreated)}</span>
            {/* <Text>{JSON.stringify(transaction)}</Text> */}
            {/* <div className='d-flex flex-column'>
                
               
                
            </div> */}
            
            
        </Paper>
        
    );
}