import React, {useState, useContext, useRef} from 'react'; 

import {AppBar, Button, TextField, InputLabel, FormControl, Select, MenuItem} from '@material-ui/core';

// import {GlobalContext} from '../Context/GlobalProvider';


// [post, tags, blogID, replyID]
export default function InsertComponent({setPost, post}) {
    console.log('NewItemModal');
    // const {userData, globals, getUserData, singleModalData, apiCall, showToast, getSiteID} = useContext(GlobalContext);
    
    
    const [insertType, setInsertType] = useState('none');
    const [insertValue, setInsertValue] = useState('');

    
    
    // var startPoint = 0
    

    // function onChangeInsertPicker(value){
    //     console.log('SUBMIT NEW ITEM');
    //     if(value == 'link'){

    //     }
        
    // } 

    function onClickInsert(){
        console.log('SUBMIT NEW ITEM');
        
        if(insertType == 'link'){
            let newPost = post + '[link]' + insertValue + '[/link]';
            setPost(newPost);
        }
        
    } 

    let insertInput = null;
    let insertBtn = null;
    if(insertType != 'none'){
        if(insertType == 'link'){
            // insertInput = <Item>
            //     <Input onChangeText={(text)=>setInsertValue(text)} placeholder={insertType.charAt(0).toUpperCase() + insertType.substring(1)} />
            // </Item>; 
            insertInput = <TextField onChange={(e)=>setInsertValue(e.target.value)} label={insertType.charAt(0).toUpperCase() + insertType.substring(1)} id="standard-basic" />    
        }

        // insertBtn = <Button primary text='Insert' onPress={onClickInsert} raised />
        // <Button className='mt-2' variant="contained" onClick={login}>Login</Button>
        insertBtn = <Button onClick={onClickInsert} variant="contained">Insert</Button>
    }
    
    
    let pickerOptionsFinal = ['none', 'link'];
    
    return (
       
        <div className='d-flex flex-column'>
            
            {/* <Picker
                note
                mode="dropdown"
                // style={{ width: 120 }}
                selectedValue={insertType}
                onValueChange={(value)=>setInsertType(value)}
            >
                
                {pickerOptionsFinal.map((option, id) => {
                    return (
                        <Picker.Item key={id} label={option.charAt(0).toUpperCase() + option.substring(1)} value={option} />
                    )
                    
                })}
                
            </Picker> */}

            <FormControl>
                <InputLabel id="demo-simple-select-label">Insert Type</InputLabel>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={insertType}
                    onChange={(e)=>{setInsertType(e.target.value)}}
                >
                   

                    {pickerOptionsFinal.map((option, id) => {
                        return (
                            // <Picker.Item key={id} label={option} value={option} />
                            <MenuItem key={id} value={option}>{option.charAt(0).toUpperCase() + option.substring(1)}</MenuItem>
                        )
                        
                    })}
                
                </Select>
            </FormControl>
            {insertInput}
            {insertBtn}
        </div>
            
            
        
    );
}