import React, { useState, useEffect, useContext } from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';
import UserImg from '../Components/UserImg';
import UserLink from '../HtmlComponents/UserLink';
import Tab from '../HtmlComponents/Tab';
import {Link, IconButton, Card, Paper} from '@material-ui/core';
import PageNav from '../Components/PageNav';
import PostList2 from './PostList';
import MenuIcon from '@material-ui/icons/Menu';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import UserMoreInfo from '../HtmlComponents/UserMoreInfo';
import ExpandableDiv from '../HtmlComponents/ExpandableDiv';
import SiteSpan from '../HtmlComponents/SiteSpan';
import DetailsDiv from '../Components/DetailsDiv';
// import {Button, TextField, IconButton} from '@material-ui/core';

export default function PostList({ post, sequence, isAsc, sortType }) {
    console.log('ITEM LIST')
    console.log('post: ', post);
    const { userData, globals, showOtherUser, openSingleModal, getOrder, openYesNoModal, convertTextToObj, dateToStr, apiCall, showToast } = useContext(GlobalContext);
    // const [replyData, setReplyData] = useState({
    //     showingReplies: false
    // });
    console.log('isAsc: ', isAsc);
    const [pageInitiated, setPageInitiated] = useState(false);
    const [showingReplies, setShowingReplies] = useState(false);
    const [replies, setReplies] = useState([]);
    const [startPt, setStartPt] = useState(0);
    const [counter, setCounter] = useState(0);
    const [showingOtherPost, setShowingOtherPost] = useState(false);
    const [replyOptions, setReplyOptions] = useState({
        isAsc: isAsc,
        
        sortType: sortType
        
    })
    console.log('reply options: ', replyOptions);
    let fromUser = false;
    if(userData.userID == post.creator){
        fromUser = true;
    }

    function setOptionsReply(options){
        // console.log('showingReplies: ', showingReplies);
        console.log('set options options: ', options);
        setReplyOptions({
            
            isAsc: options.isAsc,
            
            sortType: options.sortType
            
        })
        
        
    }

    let wordsObjArr = convertTextToObj(post.post);
    let postPar = <p style={{fontSize: 14}} className='py-2 m-0'>
        {wordsObjArr.map((word, ndx) => {
            if(word.obj == 'text'){
                return word.text;   
            }else{
                if(word.obj == 'userLink'){
                    return <UserLink username={word.text} />      
                    
                }else{
                    return <Link target="_blank" href={word.text}>{word.text}</Link>     
                    
                }
                
            }
            
            
            
        })}       
    </p>

    let imgPathBegin = globals.varNames.imgPathBlags;
    let photoPassword = userData.sosyalData.photoPassword;
    let details = <div>
        {postPar}
        <DetailsDiv info={post} imgPathBegin={imgPathBegin} photoPassword={photoPassword} />
    </div>
    

    function onButtonsPressed(button){
        // e.preventDefault();
        console.log('on buttons pressed ')
        console.log('button: ', button);
        if(button == 'Reply'){
            openSingleModal('New Post', {replyID: post.id, post: '', tags: JSON.stringify([]), blogID: 0, bidAmt: 0, siteID: post.siteid});
        }else if(button == 'Show'){
            // setReplyData({...replyData, showingReplies: true});
            console.log('post:  ', post);
            // getReplies(post.id, post.sequence); 
            getReplies(); 
        }else if(button == 'Hide'){
            // setReplyData({...replyData, showingReplies: false});
            setShowingReplies(false);
            setShowingOtherPost(false);
            // getPostsBySequence(post.sequence); 
        }else if(button == 'Edit'){
            // setReplyData({...replyData, showingReplies: false});
            // getReplies(post.id); 
            openSingleModal('New Post', {replyID: 0, post: post.post, tags: post.tags, blogID: post.id, bidAmt: post.bidamt});
        }else if(button == 'Delete'){
            openYesNoModal('Delete post?', deletePost);
            // cancelHire();
        }else if(button == 'More Info'){
            // info, imgPathBegin, photoPassword
            openSingleModal('Details', {div: details});
            // cancelHire();
        }else if(button == 'Bid'){
            openSingleModal('Bid', {bidAmt: post.bidamt, itemName: 'Post', id: post.id, bidBal: post.bidbal});
            // cancelHire();
        }
        
    }

    // const preventDefault = (event) => event.preventDefault();


    async function deletePost(){
        let data = {
           
            blogID: post.id   
        }

        let response = await apiCall(globals.varNames.blagsApiPath + 'blog/deleteBlog', data);
         
        console.log('response: ', response);
        if(response.status == 1){
            showToast('Post deleted successfully.', 5);
        }    
    }

    async function getReplies(){
        console.log('get replies');
        // startPoint: req.body.startPoint,
        // sortBy: req.body.sortBy,
        // replyID: req.body.replyID
        
        var data = {
            startPoint: startPt,
            sortBy: replyOptions.sortType,
            replyID: post.id,
            isAsc: getOrder(replyOptions.isAsc)
        }
        // console.log('options: ', options);
        let response = await apiCall(globals.varNames.blagsApiPath + 'blog/showReplies', data);
        console.log('response: ', response);
        if(response.status == 1){
            // setMsgsData({...msgsData, msgs: response.payload});
            // response.payload.forEach((post) => {
            //     initPost(post, post.sequence);
            // })
            setReplies(response.payload);
            setShowingReplies(true);
            setCounter(counter + 1);
            // post.replies = response.payload;
            
            
        }
    }

    

    

    let bgNum = sequence % 10;
    
    
    // console.log('wordsObjArr: ', wordsObjArr);
    // console.log('post: ', post);

    let url = 'http';
    if(post.imgurl != ''){
        url = globals.varNames.imgPathBlags + post.imgurl + '/' + userData.userID + '/' + userData.sosyalData.photoPassword
        // url = profileData.profileImgUrl;
    }
    
    let card = null; 
    
    

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            getReplies();
        }
        
        console.log('reply options: ', replyOptions);
        
        // setPageInitiated(true);
    }, [replyOptions]);

    useEffect(() => {
        // Update the document title using the browser API
        if(pageInitiated){
            getReplies();
        }
        
        console.log('reply options: ', replyOptions);
        
        // setPageInitiated(true);
    }, [startPt]);

    useEffect(() => {
        
        setPageInitiated(true);
    }, []);

    let btns = ['Reply', 'Show', 'More Info'];
    if(showingReplies){
        btns = ['Reply', 'Hide', 'More Info'];  
    }

    let moreBtns = [];
    if(fromUser){
        moreBtns = ['Edit', 'Delete', 'Bid', 'More Info'];
        btns.splice(2, 1);
        btns.push('More');
    }

    let pFontSize = 14;

    let postDiv = <div>
        <div  className='d-flex border-bottom py-2'>
            <UserImg imgPathBegin={globals.varNames.imgPathBlags} imgURL={post.imgurl} userID={userData.userID} photoPassword={userData.sosyalData.photoPassword} />
            <div style={{flex: 1}} className='d-flex flex-column ml-2 pl-2 border-left'>
                <div style={{justifyContent: 'space-between'}} className='d-flex'>
                    
                    <UserLink verified={post.verifieduser} username={post.username} />
                    
                    <span style={styles.idSpans}>{post.id}</span>
                </div>
                <div style={{justifyContent: 'space-between'}} className='d-flex'>
                    
                    <span className='d-flex m-0 mr-2'>{post.name}</span>
                    
                    <span style={styles.idSpans}>{dateToStr(post.lastupdate)}</span>
                </div>
                
                
                
            </div>
        </div>
        <div className='d-flex flex-column border-bottom'>
            <div className='d-flex flex-column'>
                <p style={{fontSize: pFontSize}} className='py-2 m-0'>
                    {wordsObjArr.map((word, ndx) => {
                        if(word.obj == 'text'){
                            return word.text;   
                        }else{
                            if(word.obj == 'userLink'){
                                return <UserLink verified={0} fontSize={pFontSize} username={word.text} />      
                                
                            }else{
                                return <Link target="_blank" href={word.text}>{word.text}</Link>     
                                
                            }
                            
                        }
                        
                        
                        
                    })}       
                </p> 
                <div className='d-flex flex-column'>
                    
                    
                    {/* <UserMoreInfo user={post} isSosyal={true} /> */}
                    <SiteSpan username={post.siteusername} />
                    
                    
                </div>   
            </div>
                   
        </div>
        
        
        <div style={{borderLeftWidth: 5, borderLeftStyle: 'solid', justifyContent: 'space-between'}} className={'d-flex px-2 mt-2 b-' + bgNum}>
            <span style={styles.idSpans}>{sequence}</span>
            
            <Tab headers={btns} onChangeTab={onButtonsPressed} moreTabs={moreBtns} />
            
        </div>   
    </div>

    let borderTop = '';
    let repliesDiv = null;
    

    let otherPostDiv = null;
    
    if(showingOtherPost){
        otherPostDiv = postDiv;  
        borderTop = ' border-top mt-2'
    }

    if(showingReplies){
        // btns = ['Reply', 'Hide'];
        
        repliesDiv = <div>
            <div>
                {replies.map((reply, ndx) => {
                    return (
                    
                        
                        <PostList2 isAsc={replyOptions.isAsc} sortType={replyOptions.sortType} sequence={sequence + 1} getOrder={getOrder} getReplies={getReplies} post={reply} key={reply.id + '_' + (sequence + 1) + '_' + counter}/>
                    )
                    
                })}
                
            </div>
            
            <Paper elevation={2} className={'d-flex flex-column px-2'}>
                {otherPostDiv}
                <div className={'d-flex' + borderTop}>
                    <div style={{borderLeftWidth: 5, borderLeftStyle: 'solid'}} className={'d-flex px-2 my-1 b-' + bgNum}>
                        <span style={styles.idSpans}>{sequence}</span>
                        <IconButton className='p-0' onClick={()=>openSingleModal('Post Options', {searchOptions: replyOptions, setSearchOptions: setOptionsReply, isRepliesOption: true})} aria-label="menu">
                            <MoreVertIcon />
                        </IconButton>
                    </div>
                    <div style={{justifyContent: 'flex-end', flex: 1}} className='d-flex py-1'>
                        <PageNav onChangeStartPt={setStartPt} isForward={true}/>
                        <Link style={styles.idSpans} className='ml-5 ' href="#" onClick={(e)=>{e.preventDefault(); setShowingOtherPost(!showingOtherPost)}}>{post.id}</Link>
                        {/* <div className='d-flex py-1'>
                            
                            
                            
                        </div> */}
                    </div>
                </div>
                
                
                
            </Paper>
               
        </div> 
    }

    

    
    // if(userData.userID != post.creator){
    //     btns.splice(2, 1);
    // }
    
    
    return (
        <div className='mb-2'>
            <Paper elevation={2} className='mb-3 px-2 pb-2'>
                {postDiv}
                {/* <div  className='d-flex border-bottom py-2'>
                    <UserImg imgPathBegin={globals.varNames.imgPathBlags} imgURL={post.imgurl} userID={userData.userID} photoPassword={userData.sosyalData.photoPassword} />
                    <div style={{flex: 1}} className='d-flex flex-column ml-2 pl-2 border-left'>
                        <div style={{justifyContent: 'space-between'}} className='d-flex'>
                            <UserLink username={post.username} />
                            <span style={{fontSize: 12}}>{post.id}</span>
                        </div>
                        
                        <span style={{fontSize: 12}}>{dateToStr(post.lastupdate)}</span>
                        
                    </div>
                </div>
                <p style={{fontSize: 14}} className='border-bottom py-2 m-0'>
                    {wordsObjArr.map((word, ndx) => {
                        if(word.obj == 'text'){
                            return word.text;   
                        }else{
                            if(word.obj == 'userLink'){
                                return <UserLink username={word.text} />      
                                
                            }else{
                                return <Link target="_blank" href={word.text}>{word.text}</Link>     
                                
                            }
                            
                        }
                        
                        
                        
                    })}       
                </p>
                
                <div style={{borderLeftWidth: 5, borderLeftStyle: 'solid'}} className={'d-flex px-2 mt-2 b-' + bgNum}>
                    <span>{sequence}</span>
                    
                    <Tab headers={btns} onChangeTab={onButtonsPressed} moreTabs={moreBtns} />
                    
                </div> */}
                
                {/* <h6>{JSON.stringify(post)}</h6> */}
            </Paper>

            {repliesDiv}
            
        </div>   
    );
}

const styles = {
    idSpans: {
        fontSize: 15
    }
}