import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';



// import PageNav from './PageNav';



import TransactionList from '../List/TransactionList';

export default function Transactions() {
    console.log('MESSAGE HEADERS');
    const {userData, globals, closePageNav, showPageNav, apiCall, messageData, forceUpdate, getApiPath, openMsgModal} = useContext(GlobalContext);
    // const [startPt, setStartPt] = useState(0);
    const [transactions, setTransactions] = useState([]);
    
    
    // console.log('msg headers:', messageData.msgHeaders);

    async function getTransactions(startPt){
        let response = await apiCall(getApiPath() + 'pay/getTxs', {startPoint: startPt});
        console.log('response: ', response);
        if(response.status == 1){
            setTransactions(response.payload);
            // messageData.msgHeaders = response.payload;
            // console.log('message headers: ', messageData.msgHeaders);
            // forceUpdate();
        }     
    }

    useEffect(() => {
        // Update the document title using the browser API
        // getTransactions();
        showPageNav(getTransactions); 
        getTransactions(0); 
        return closePageNav
    }, []);
    
    // let modal = null;
    // if(newMsgModal){
    //     modal = <NewMsgModal closeModal={()=>setNewMsgModal(false)}/>
    // }
    // console.log('user inputs: ', userInputs);
    return (
        <div style={{flex: 1, overflow: 'auto'}} className='d-flex flex-column'>
           
            <h5 style={{alignSelf: 'flex-end'}} className='mb-3 mr-2'>Transactions</h5>
            <div style={{flex: 1, overflow: 'auto'}} className='px-2'>
                {transactions.map((transaction) => {
                    return <TransactionList transaction={transaction} key={transaction.id}/>
                    
                    
                })}
            </div>
            
            
           
            {/* <div className='border-top'>
                <PageNav startPt={startPt} getItems={getTransactions} setStartPt={setStartPt} isForward={true}/>
            </div> */}
            
            
        </div>
    );
}