import React, {useState, useContext, useRef} from 'react'; 
import {GlobalContext} from '../../Context/GlobalProvider';

import {Card, Button} from '@material-ui/core';


export default function YesNoModal() {
    console.log('Yes No Modal');
    const { userData, forceUpdate, yesNoModalData } = useContext(GlobalContext);

    function onPressedNo(){
        userData.openingYesNoModal = false;
        forceUpdate();
    }

    function onPressedYes(){
        yesNoModalData.callbackFunc();
        onPressedNo();
    }

    return (
        // <ModalBase styleView={{}} style={{position: 'absolute', top: 250, backgroundColor: 'white', elevation: 2, right: 5, left: 5}}>
        //     <View style={{padding: 5, marginTop: 10}}>
                
        //         <View style={{marginBottom: 5, padding: 10}}>
        //             <Text style={{fontSize: 15}}>
        //                 {yesNoModalData.question}
        //             </Text>
                    
                    
        //         </View>
                
               
        //         <View style={{flexDirection: 'row', justifyContent: 'flex-end'}}>
        //             <Button style={{container:{marginRight: 5}}} primary text='Yes' onPress={onPressedYes} />
        //             <Button primary text='No' onPress={onPressedNo} />
                  

        //         </View>
        //     </View>
            
        // </ModalBase>
        <div className='shadow-lg d-flex flex-column px-3 pt-3 rounded' style={{position: 'absolute', backgroundColor: 'white', top: 100, right: 5, left: 5, zIndex: 2}}>
            
            <p className='' style={{fontWeight: 500}}>{yesNoModalData.question}</p>
            <div className='d-flex m-0' style={{justifyContent: 'flex-end'}}>
                <Button className='' onClick={onPressedNo} color="primary">No</Button>
                <Button className='' onClick={onPressedYes} color="primary">Yes</Button>   
            </div>
        </div>
       
    );
}