    //         ...Ionicons.font,
            
    //     });
    //     setFontLoaded(true);
    // }

    function getWindowSize(){
        let width = window.innerWidth;
        let size = 'lg';
        if(width >= 769 && width < 980){
            size = 'md';
        }else if(width >= 480 && width < 769){
            size = 'sm';
        }else if(width < 480){
            size = 'xm';   
        }

        return size;
    }
    
    function closeToast(){
        userData.showingToast = false;
        forceUpdate();
    }

    // function closeOtherPage(){
    //     userData.showingOtherPage = false;
    //     forceUpdate();
    // }

    // function closeSingleModal(){
    //     userData.openingSingleModal = false;
    //     forceUpdate();
    // }

    function updateDimensions(){
        console.log('UPDATE DIMENSIONS');
        let SIZE = getWindowSize();
        
        if(userData.windowSize != SIZE){
            userData.windowSize = SIZE
            forceUpdate();
        }
    }

    async function pageLoad(){
        initiateVariables();
        // loadFont();
        // await getGlobals();
        getUserToken();
    }

    // useEffect(() => {
    //     // Update the document title using the browser API
        
    //     pageLoad();
    //     // await getUserToken(userData, getUserData);
    // }, []);
    
