
import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import Switch from '@material-ui/core/Switch';
import Note from '../common/HtmlComponents/Note';


export default function SetGroserMode() {
    console.log('MAP SEARCH COMPONENT');
    const {userData, globals, appData, getMsgs, messageData, forceUpdate, apiCall, showToast} = useContext(GlobalContext);
    const [mode, setMode] = useState(userData.groserMode);

    const [settingsInputs, setSettingsInputs] = useState({
        lat: userData.position.lat,
        lng: userData.position.lng
    });
    
    async function setGroserMode(){
        
        let modeFinal = 1;
        if(!mode){
            modeFinal = 0; 
            // userData.groserMode = false;   
        }

        // forceUpdate();
        let data = {
            mode: modeFinal
        }

        let response = await apiCall(globals.varNames.apiPath + 'user/setGroserMode', data);
        console.log('response: ', response);
        if(response.status == 1){
            userData.groserMode = mode;  
            showToast('Mode changed successfully.', 3, false);
                 
        }
        
    } 

    useEffect(() => {
        // Update the document title using the browser API
        if(mode != userData.groserMode){
            setGroserMode();
        }
        
    }, [mode]);

    let label = 'Shop';
    if(userData.isShappers){
        label = 'Shopper';
    }
    // let label = 'ON';
    // if(!userData.groserMode){
    //     label = 'OFF';
    // }
    // console.log('user inputs: ', userInputs);
    return (
        <div>
            <div>
                <span style={{paddingTop: 9}}>{label} Mode:        </span>
                
                {/* <Button style={{container: {alignSelf: 'flex-start'}}} raised primary text={label} onPress={setGroserMode} /> */}
                <Switch
                    checked={mode}
                    onChange={(e)=>setMode(e.target.checked)}
                    name="checkedA"
                    inputProps={{ 'aria-label': 'secondary checkbox' }}
                />
                
            
                
                
            </div>
            <Note note={'Turning this on will make your location public.'} />
            {/* <p style={{paddingTop: 9}}>Note: Turning this on will make your location public.</p> */}
        </div>
        
    );
}