import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';


import OtherPage from '../HtmlComponents/OtherPage';

import PostsDiv from '../Components/PostsDiv';

export default function OtherPostPage({closeOtherPage}) {
    console.log('LIST')
	
    const { userData, globals, forceUpdate, apiCall, funcDelayer, otherPostData, otherPageData } = useContext(GlobalContext);
    console.log('other post data: ', otherPostData);
    const [toastData, setToastData] = useState({
        isOpen: false,
        message: '',
        duration: 0
    });

    function onPressBack(){
        userData.showingOtherPost = false;
        forceUpdate();
    }
    
    return (
        <OtherPage onPressBack={closeOtherPage}>
            
            <PostsDiv userViewedID={0} isProfilePage={false} isPostResult={true} postResult={otherPageData.data} />
        </OtherPage>    
    );
}