    function checkErrors(code){
        let errorExist = false;
        // let codeFinal
        console.log('state. globals: ', state.globals);
        console.log('code: ', code);
        state.globals.errorCodes.forEach((errorCode) => {
            if(code == errorCode){
                errorExist = true;
                if(errorCode == 500){
                    openSingleModal('Info', {div: 'Data'});  
                    // showToast('wsjf alsjfafsf ljsfajfjsf ;ajsfjjf ;asfjajs  f lakjjdflajfjajf ;adfjsjff asfa; asdfsldkf a;sdfasf jalsfjfa afjaf;af kajsjfal', 5000);
                }else if(errorCode == 501){
                    openSingleModal('Info', {div: 'Balance'});
                    // showToast('wsjf alsjfafsf ljsfajfjsf ;ajsfjjf ;asfjajs  f lakjjdflajfjajf ;adfjsjff asfa; asdfsldkf a;sdfasf jalsfjfa afjaf;af kajsjfal', 5000);
                }else if(errorCode == 56){
                    showToast('Request limit exceeded. Try again after a few seconds.', 5, true);
                    
                    
                }else if(errorCode == 5){
                    showToast('Invalid token.', 5, true);
                    logout();
                    
                }
            }
        })  
        
        return errorExist;
    }

    

    async function apiCall(url, data){
        let response = await axios({
            method: 'post',
            url: url,
            data: data,
            headers: {
                'x-access-token': state.userData.token,
                'version': state.appData.version,
                'sosyal-version': state.appData.versionSosyal,
            }    
        })
    
        response.data.globalError = false;
    
        if(response.data.status == 0){
            if(!checkErrors(response.data.code)){
                showToast(response.data.message, 3, true);
            }
        }
    
        
        return response.data;
    
        
    };

