    function checkUsernameInput2(username){
        let minChar = 3;
        let label = 'Username';
        let msg = '';
        // if(!isUsername){
        //     minChar = 5;
        //     label = 'Address';
        // }
        let maxChar = 16;
        if(state.userData.production){
            minChar = 0;
        }

        let specialCharUsername = (username.match(/[^a-z0-9_]/ig) || []).length;
        if(specialCharUsername == 0 && username.length >= minChar && username.length <= maxChar){
            msg = '';
            
        }else{
            if(specialCharUsername > 0){
                msg = label +' can only contain numbers, letters and _.';
                // setError(label +' can only contain numbers, letters and _.');
            }else if(username.length < minChar){
                msg = label +' must be at least ' + minChar + ' characters.';
                // setError(label +' must be at least ' + minChar + ' characters.');
            }else{
                msg = label +' cannot exceed ' + maxChar + ' characters.';
                // setError(label +' cannot exceed ' + maxChar + ' characters.');    
            }
        }

        return msg;
    }

