    function dateToStr(payload) { 
        let timestampDate = new Date(payload * 1000);
        let hours = timestampDate.getHours();
        let minutes = timestampDate.getMinutes();
        let seconds = timestampDate.getSeconds();
        let year = timestampDate.getFullYear();
        let month = timestampDate.getMonth() + 1;
        let date = timestampDate.getDate();  
        
        return month + "/" + date + "/" + year + " " + hours + ":" + minutes + ":" + seconds;     
    }

    function datePickerStr(time) { 
        let dateToConvert = new Date(time);
        let month = dateToConvert.getMonth() + 1;
        let day = dateToConvert.getDate();
        let monthFinal = month;
        let dayFinal = day;

        if(month < 10){
            monthFinal = '0' + month;
        } 

        if(day < 10){
            dayFinal = '0' + day;
        }

        
        let dateStr = dateToConvert.getFullYear() + '-' + monthFinal + '-' + dayFinal;  
        return dateStr;   
    }


