    async function getSearchTags(tag, startPt, isUserTags){

        let tags = [];

        let data = {
            tag: tag,
            startPoint: startPt,
               
        }

        let path = 'user/getSearchTags';
        if(isUserTags){
            path = 'user/getUserHints';
        }

        let response = await apiCall(getApiPath() + path, data);
        console.log('response: ', response);
        if(response.status == 1){
            // setTags(response.payload);
            tags = response.payload
        } 
        
        return tags;
    }

